<template>
  <div
    @click="updateCheckboxSelected()"
    :class="{
      clickable:
        !item.is_returned &&
        this.item.is_eligible &&
        this.item.quantity &&
        this.item.quantity > 0,
    }"
    :style="item.is_returned ? 'opacity:0.4' : 'opacity:1'"
  >
    <v-divider class="my-2" v-if="this.index > 0" />
    <v-card
      class="d-flex"
      :class="{
        'mb-4': $vuetify.breakpoint.smAndDown,
        'mb-6': $vuetify.breakpoint.mdAndUp,
      }"
      flat
      tile
    >
      <v-col class="ml-n2">
        <image-thumbnail
          :item="item"
          :order-brand="orderBrand"
          :key="index"
          :index="index"
          product-thumbnail
          show-select
          show-note-message
          :title="formatText(item.title)"
          :sub-title1="item.subtitle"
          :sub-title2="formattedPrice"
          :image-url="item.image_url"
        />
      </v-col>
      <div
        :class="{
          'mr-4 mt-n2': $vuetify.breakpoint.smAndDown,
          'mr-n1 mt-n1': $vuetify.breakpoint.mdAndUp,
        }"
      >
        <div
          class="d-flex align-center justify-end"
          v-if="item.is_eligible && item.quantity && item.quantity > 0"
        >
          <v-icon style="color: #000000;" x-small>
            close
          </v-icon>
          <o-text
            class="ml-1 mr-2 mb-0 "
            sm
            semi-bold
          >
            {{ item.quantity }}
          </o-text>
          <v-checkbox
            v-model="selected"
            @click="updateCheckboxSelected($event)"
            :disabled="item.is_returned"
            color="var(--button-primary)"
          />
        </div>
        <div
          class="d-flex align-center justify-end mt-5 mr-2"
          v-if="!item.is_eligible && !item.is_returned"
        >
          <o-text
            sm
            semi-bold
            text-red
            class="pr-1"
          >
            {{ NOT_ELIGIBLE_FOR_RETURN }}
          </o-text>
          <v-icon style="color: #d83933;" small>
            block
          </v-icon>
        </div>
      </div>
    </v-card>
  </div>
</template>

<script>
import ImageThumbnail from '@/components/organism/ImageThumbnail.vue'
import { UPDATE_SELECTED_ITEM } from '@/store/constants.js'
import OText from '@/components/atoms/OText.vue'
import { ORDER_DISPLAY } from 'branding/strings.json'
import { formatText } from '@/utils'

export default {
  name: 'ProductListItemView',
  components: {
    ImageThumbnail,
    OText
  },
  props: {
    item: {
      type: Object,
      required: true
    },
    orderBrand: {
      type: String,
      required: false,
      default: ''
    },
    index: {
      type: Number,
      required: true
    }
  },
  data () {
    return {
      NOT_ELIGIBLE_FOR_RETURN: ORDER_DISPLAY.NOT_ELIGIBLE_FOR_RETURN
    }
  },
  methods: {
    updateCheckboxSelected: function (e) {
      if (
        !this.item.is_eligible ||
        !this.item.quantity ||
        this.item.quantity === 0 ||
        this.item.is_returned
      ) {
        return
      }
      // this.selected = !this.selected
      this.$store.commit('order/' + UPDATE_SELECTED_ITEM, this.item.identifier)

      if (e) {
        e.stopPropagation()
      }
    },
    formatText (text) {
      return formatText(text)
    }
  },
  computed: {
    selected: function () {
      return this.item.selected
    },
    formattedPrice: function () {
      return `$${this.item.price}`
    }
  }
}
</script>

<style scoped>
.add-border {
  border-bottom: 0.5px solid #d8d8d8;
}
.clickable {
  cursor: pointer;
}
</style>
