<template>
  <div>
    <v-row>
      <span class="d-flex justify-start">
        <v-btn
          icon
          text
          style="background-color: #ffffff; color: var(--button-primary);"
          @click.stop="onClickMethod"
          class="circle align-start"
          :class="{'mt-3 mr-n2': $vuetify.breakpoint.smAndDown, 'mt-n1 mr-n2': $vuetify.breakpoint.mdAndUp}"
        >
          <v-icon
            style="color: var(--button-primary);"
            small
          >
            arrow_back
          </v-icon>
        </v-btn>
        <v-spacer v-if="$vuetify.breakpoint.smAndDown" />
        <div
          class="d-flex align-center ml-8 text-body-2"
          :class="{'mt-3 mr-4': $vuetify.breakpoint.smAndDown}"
          v-if="count>1"
        >
          <o-text
            base-bold
            class="mr-1"
          >
            Item {{ formattedIndex }}
          </o-text>
          <o-text>
            /
          </o-text>
          <o-text class="ml-1">
            {{ formattedCount }}
          </o-text>
        </div>
      </span>
      <header-title
        v-if="this.showThumbnail"
        class="d-flex justify-start px-3"
        :class="{'mt-5 mb-0': $vuetify.breakpoint.smAndDown, 'mt-9 mb-0': $vuetify.breakpoint.mdAndUp}"
      >
        {{ title }}
      </header-title>
      <o-text
        base
        class="d-flex justify-start mt-1 px-3"
        v-if="subTitle != ''"
      >
        {{ subTitle }}
      </o-text>
      <div
        v-if="this.showThumbnail"
        class="d-flex justify-start mt-0"
        :class="{'mt-6 mb-3': $vuetify.breakpoint.smAndDown, 'mt-6 mb-8': $vuetify.breakpoint.mdAndUp}"
      >
        <image-thumbnail
          :item="item"
          :title="item.title"
          :sub-title1="item.subtitle"
          :sub-title2="formattedPrice"
          :image-url="item.image_url"
          :order-brand="orderBrand"
          product-thumbnail
          is-small-thumbnail
        />
      </div>
    </v-row>
  </div>
</template>

<script>
import ImageThumbnail from './ImageThumbnail.vue'
import { pad } from '@/utils'
import HeaderTitle from '@/components/molecules/HeaderTitle.vue'
import OText from '@/components/atoms/OText.vue'

export default {
  name: 'ReturnReasonHeaderView',
  components: {
    ImageThumbnail,
    HeaderTitle,
    OText
  },
  props: {
    title: {
      type: String,
      default: ''
    },
    subTitle: {
      type: String,
      default: ''
    },
    index: {
      type: Number,
      default: 1
    },
    count: {
      type: Number,
      default: 1
    },
    showFeedback: {
      type: Boolean,
      default: false
    },
    showThumbnail: {
      type: Boolean,
      default: true
    },
    item: { type: Object, default: null },
    orderBrand: { type: String, default: '' }
  },
  methods: {
    onClickMethod (event) {
      this.$emit('click', event)
    },
    onFeedbackClickedMethod (event) {
      this.$emit('feedbackClicked', event)
    }
  },
  computed: {
    formattedIndex: function () {
      return `${pad(this.index + 1, 2)}`
    },
    formattedCount: function () {
      return `${pad(this.count, 2)}`
    },
    formattedPrice: function () {
      return `$${this.item.price}`
    }
  }
}
</script>

<style scoped>
 .circle {
  display: inline-block;
  border-radius: 60px;
  border: 0.2px solid #c8c8c8;
  padding: 0.4em 0.6em;

}
</style>
