<template>
  <div>
    <v-row>
      <div class="d-flex justify-start mt-4" v-if="!this.isReturnUser">
        <header-title>
          {{ SELECT_ITEMS_TO_RETURN }}
        </header-title>
      </div>
      <div>
        <header-medium-title
          class="mt-6"
          :heading="this.isReturnUser"
          :level="this.isReturnUser ? Number(1) : null"
        >
          Order #{{ orderNumber }}
        </header-medium-title>
        <div class="d-flex align-center mt-1">
          <o-text
            sm
            class="mr-1"
            v-if="this.order.order.created_at"
          >
            {{ this.order.order.items.length > 0 && this.order.rma_list.length > 0 ? 'Placed on ' : 'Purchased on ' }}{{ getReturnDate(this.order.order.created_at) }}
          </o-text>
          <o-text sm>
            {{ this.totalItemsCountTitle }}
          </o-text>
          <o-text
            sm
            class="ml-1"
            v-if="this.rmaItemsCountTitle"
          >
            {{ this.rmaItemsCountTitle }}
          </o-text>
        </div>
      </div>
    </v-row>
  </div>
</template>

<script>
import { ORDER_DISPLAY } from 'branding/strings.json'
import { mapGetters } from 'vuex'
import OText from '@/components/atoms/OText.vue'
import HeaderMediumTitle from '@/components/molecules/HeaderMediumTitle.vue'
import { getFormattedDate } from '@/utils'
import HeaderTitle from '@/components/molecules/HeaderTitle.vue'

export default {
  name: 'ProductListHeaderView',
  data () {
    return {
      orderId: '',
      zipCode: '',
      isDisabled: true,
      SELECT_ITEMS_TO_RETURN: ORDER_DISPLAY.SELECT_ITEMS_TO_RETURN
    }
  },
  components: {
    OText,
    HeaderMediumTitle,
    HeaderTitle
  },
  props: {
    selectedCount: {
      type: Number,
      required: true
    },
    buttonTitle: {
      type: String,
      required: false,
      default: ''
    },
    orderNumber: {
      type: String,
      required: true
    },
    totalItemsCountTitle: {
      type: String,
      required: true
    },
    rmaItemsCountTitle: {
      type: String,
      required: true
    },
    isReturnUser: {
      type: Boolean,
      required: false
    }
  },
  methods: {
    getReturnDate (createdAt) {
      return getFormattedDate(createdAt)
    }
  },
  computed: { ...mapGetters({ order: 'order/order' }) }
}
</script>

