<template>
  <div>
    <back-button
      override-go-back
      v-show="!disableModalGoBack && !(source === 'mapView')"
      :override-on-click="() => openModal({ type: DIALOG_CARRIER_LOCATIONS })"
    />
    <div style="max-width: 664px" class="mx-auto">
      <div>
        <header-title>{{ SEARCH_ELSEWHERE }}</header-title>
        <o-text text-secondary bold>
          Enter address
        </o-text>
        <div class="mt-4">
          <o-places-auto-complete
            :id="id"
            placeholder="Start typing"
            @placechanged="getAddressData"
            enable-geolocation
            @change="change"
          />
        </div>
        <action-button
          block
          x-large
          primary-color
          :disabled="disabled"
          :loading="isLoading"

          @click="onClick"
        >
          Select
        </action-button>
      </div>
    </div>
  </div>
</template>

<script>
import OText from '@/components/atoms/OText.vue'
import HeaderTitle from '@/components/molecules/HeaderTitle.vue'
import { RETURN_METHOD_DIALOGS, PAGE_TITLES } from 'branding/strings.json'
import ActionButton from '@/components/atoms/ActionButton.vue'
import BackButton from '@/components/atoms/BackButton.vue'
import { mapMutations, mapGetters } from 'vuex'
import { DIALOG_CARRIER_LOCATIONS } from '@/store/constants'
import OPlacesAutoComplete from '@/components/atoms/OPlacesAutoComplete.vue'
import { formatReturnMethods } from '@/controllers/ReturnMethodsController.js'

export default {
  components: {
    OText,
    HeaderTitle,
    ActionButton,
    BackButton,
    OPlacesAutoComplete
  },
  computed: {
    ...mapGetters({
      policyReturnMethods: 'returns/policyReturnMethods',
      policies: 'returns/policies',
      disableModalGoBack: 'returns/getDisableModalGoBack'
    })
  },
  watch: {
    policies: function () {
      this.openReturnLocations()
    }
  },
  methods: {
    ...mapMutations({
      openModal: 'returns/openModal',
      setAddress: 'returns/setAddress'
    }),
    getAddressData (data) {
      this.address = data
    },
    change () {
      this.disabled = !document.getElementById(this.id).value
    },
    onClick () {
      const newAddress = {
        city: this.address.locality,
        state: this.address.administrative_area_level_1,
        zip_code: this.address.postal_code,
        country_code: this.address.country,
        longitude: this.address.longitude,
        latitude: this.address.latitude,
        street1: this.address.route
      }
      this.setAddress(newAddress)
      this.isLoading = true
      this.$store
        .dispatch('returns/fetchAddressInfo', {
          shippingAddress: newAddress,
          returnMethods: formatReturnMethods(this.policyReturnMethods)
        })
      if (this.source === 'mapView') {
        setTimeout(() => { this.isLoading = false }, 3000)
      } else {
        setTimeout(() => { if (!this.isReturnLocationsLoaded) { this.openReturnLocations() } }, 5000)
      }
    },
    openReturnLocations () {
      this.isReturnLocationsLoaded = true
      this.isLoading = false
      this.openModal({ type: DIALOG_CARRIER_LOCATIONS })
    }
  },
  props: {
    source: {
      type: String,
      required: true
    }
  },
  mounted () {
    document.title = PAGE_TITLES.SEARCH_ELSEWHERE
  },
  data () {
    return {
      SEARCH_ELSEWHERE: RETURN_METHOD_DIALOGS.SEARCH_ELSEWHERE,
      DIALOG_CARRIER_LOCATIONS,
      address: null,
      id: `search-return-location_${this.source}`,
      disabled: true,
      isReturnLocationsLoaded: false,
      isLoading: false
    }
  }
}
</script>

