<template>
  <o-dialog
    :visible="show"
    :is-full-screen="false"
    width="450"
    @close="show = false"
  >
    <v-form :class="{ 'px-2': $vuetify.breakpoint.mdAndUp }">
      <header-title
        class="pb-4"
      >
        {{ RETURN_A_GIFT }}
      </header-title>
      <h6
        class="headingSubTitle"
        :class="{
          'mb-3': $vuetify.breakpoint.smAndDown,
          'mb-3': $vuetify.breakpoint.mdAndUp,
        }"
      >
        <strong>Help us find the order, </strong>please enter the orderID shown
        on the receipt from your order.
      </h6>
      <o-text-field
        :title="ENTER_YOUR_ORDER_ID"
        id="orderId"
        placeholder="Order ID"
        :class="{ 'mb-2': $vuetify.breakpoint.mdAndUp }"
        @onChange="onOrderChange($event)"
      />
      <o-text-field
        :title="ENTER_YOUR_ZIP_CODE"
        id="zipCode"
        placeholder="Zip Code"
        :class="{ 'mb-2': $vuetify.breakpoint.mdAndUp }"
        @onChange="onZipCodeChange($event)"
      />
      <h6 class="headingSubTitle mb-3">
        <strong>Track your return, </strong>we will use only this address for
        messages about your return
      </h6>
      <o-text-field
        :title="ENTER_EMAIL_ADDRESS"
        id="email"
        placeholder="user@example.com"
        @onChange="onEmailChange($event)"
      />
      <o-text-field
        :title="RE_ENTER_EMAIL_ADDRESS"
        id="reEnterEmail"
        placeholder="user@example.com"
        @onChange="onRenterEmailChange($event)"
      />
      <o-text
        sm
        bold
        class="red--text"
      >
        {{ this.errorMessage }}
      </o-text>
      <action-button
        @click="lookupOrder"
        :disabled="isDisabled"
        class="mt-2"
        primary-color
        :loading="isLoading"
        block
        x-large
      >
        {{ LOOKUP_ORDER }}
      </action-button>
    </v-form>
  </o-dialog>
</template>

<script>
import OTextField from '@/components/molecules/OTextField.vue'
import ActionButton from '@/components/atoms/ActionButton.vue'
import ODialog from '@/components/atoms/ODialog.vue'
import { validateOrderId, validateEmail, validateZipcode } from '@/utils'
import { RETURN_A_GIFT, ZIP_CODE_LOOKUP } from 'branding/strings.json'
import { mapGetters } from 'vuex'
import { FETCH_ORDER } from '@/store/constants.js'
import OText from '@/components/atoms/OText.vue'
import HeaderTitle from '@/components/molecules/HeaderTitle.vue'
import { setIsGift, setGiftEmail } from '@/store/sessionStorage.js'

export default {
  props: {
    visible: Boolean
  },
  components: {
    OTextField,
    ActionButton,
    ODialog,
    OText,
    HeaderTitle
  },
  data () {
    return {
      orderId: '',
      zipCode: '',
      email: '',
      reEnterEmail: '',
      isDisabled: true,
      errorMessage: '',
      RETURN_A_GIFT: RETURN_A_GIFT.RETURN_A_GIFT,
      ENTER_YOUR_ORDER_ID: RETURN_A_GIFT.ENTER_YOUR_ORDER_ID,
      ENTER_YOUR_ZIP_CODE: RETURN_A_GIFT.ENTER_YOUR_ZIP_CODE,
      ENTER_EMAIL_ADDRESS: RETURN_A_GIFT.ENTER_EMAIL_ADDRESS,
      RE_ENTER_EMAIL_ADDRESS: RETURN_A_GIFT.RE_ENTER_EMAIL_ADDRESS,
      LOOKUP_ORDER: ZIP_CODE_LOOKUP.LOOKUP_ORDER
    }
  },
  computed: {
    ...mapGetters({
      order: 'order/order',
      isLoading: 'order/isLoading'
    }),
    show: {
      get () {
        return this.visible
      },
      set (value) {
        if (!value) {
          this.$emit('close')
        }
      }
    }
  },
  methods: {
    validate () {
      this.isDisabled = !(
        validateOrderId(this.orderId) && validateEmail(this.email) && validateZipcode(this.zipCode) && validateEmail(this.reEnterEmail) && this.email === this.reEnterEmail
      )
    },
    onEmailChange (event) {
      const value = event.target.value
      this.email = value
      this.validate()
    },
    onRenterEmailChange (event) {
      const value = event.target.value
      this.reEnterEmail = value
      this.validate()
    },
    onZipCodeChange (event) {
      const value = event.target.value
      this.zipCode = value
      this.validate()
    },
    onOrderChange (event) {
      const value = event.target.value
      this.orderId = value
      this.validate()
    },
    async lookupOrder () {
      this.$store.commit('ui/updateOrderId', this.orderId)
      this.$store.commit('ui/updateZipCode', this.zipCode)
      setGiftEmail(this.email)
      const params = {
        order_id: this.orderId,
        zip_code: this.zipCode,
        email: this.email
      }
      this.$store
        .dispatch('order/' + FETCH_ORDER, params)
        .then((response) => {
          if (response) {
            const orderHash =
              response && response != null
                ? response.order_hash
                : this.order.orderId
            setIsGift(true)
            this.$router.push('/orders/' + orderHash)
          } else {
            this.$router.push('/orderandzipcode')
          }
        })
        .catch((error) => {
          console.log({ error })
          this.errorMessage = 'Order not found'
        })
    }
  }
}
</script>

<style scoped></style>
