<template>
  <div class="d-flex justify-center align-center" style="min-height: 100vh; ">
    <v-container>
      <header-title style="text-align: center" class="mt-12">
        {{ LOGIN_PAGE.PAGE_HEADING }}
      </header-title>
      <v-form @keyup.enter="onSubmit">
        <v-row class="justify-center">
          <v-col
            cols="12"
            sm="6"
            md="3"
          >
            <o-text-field
              label="username"
              id="username"
              v-model="username"
              @onChange="onChange($event, 'username')"
              @onKeyEnter="onSubmit"
              placeholder="username"
            />
          </v-col>
        </v-row>
        <v-row class="justify-center">
          <v-col
            cols="12"
            sm="6"
            md="3"
          >
            <o-text-field
              label="password"
              id="password"
              v-model="secureText"
              type="password"
              @onChange="onChange($event, 'secureText')"
              @onKeyEnter="onSubmit"
              placeholder="password"
            />
          </v-col>
        </v-row>
        <v-row class="justify-center">
          <v-col
            cols="12"
            sm="6"
            md="3"
          >
            <div v-if="errors.length">
              <o-text
                v-for="error in errors"
                :key="error"
                text-red
              >
                {{ error }}
              </o-text>
            </div>
          </v-col>
        </v-row>
        <v-row class="justify-center">
          <v-col
            cols="12"
            sm="6"
            md="3"
          >
            <div class="d-flex justify-center">
              <action-button
                @click.prevent="onSubmit"
                primary-color
                x-large
                block
              >
                {{ LOGIN_PAGE.ACTION_BUTTON }}
              </action-button>
            </div>
          </v-col>
        </v-row>
      </v-form>
    </v-container>
  </div>
</template>

<script>
import HeaderTitle from '@/components/molecules/HeaderTitle.vue'
import OTextField from '@/components/molecules/OTextField.vue'
import OText from '@/components/atoms/OText.vue'
import ActionButton from '@/components/atoms/ActionButton.vue'
import { appSecureText } from '@/common/config'
import { LOGIN_PAGE } from 'branding/strings.json'
export default {
  name: 'Login',
  components: { HeaderTitle, OTextField, ActionButton, OText },
  data () {
    return {
      username: '',
      secureText: '',
      errors: [],
      LOGIN_PAGE
    }
  },
  methods: {
    onChange (e, field) {
      const value = e.target.value
      this[field] = value
    },
    onSubmit: function (e) {
      this.errors = []

      if (!this.username) {
        this.errors.push(LOGIN_PAGE.USERNAME_REQUIRED_VALIDATION)
      }
      if (!this.secureText) {
        this.errors.push(LOGIN_PAGE.PASSWORD_REQUIRED_VALIDATION)
      }

      if (!this.username || !this.secureText) {
        return
      }

      if (
        this.username.trim().toLowerCase() === 'optoro' &&
        this.secureText === appSecureText
      ) {
        localStorage.setItem('isAuthenticated', 'true')
        this.$router.push('/')
        return true
      } else {
        this.errors.push(LOGIN_PAGE.INCORRECT_PASSWORD_VALIDATION)
      }

      e.preventDefault()
    }
  },
  created () {
    // Check if user has already logged in
    const isAuthenticated = localStorage.getItem('isAuthenticated')
    if (isAuthenticated === 'true') {
      this.$router.push('/')
    }
  }
}
</script>

