<template>
  <v-container>
    <v-row class="d-flex justify-center">
      <v-col
        md="4"
        sm="8"
        xs="12"
        :class="{
          'px-7': $vuetify.breakpoint.smAndDown,
          'py-5': $vuetify.breakpoint.smAndDown,
        }"
      >
        <v-form>
          <div class="d-flex align-center">
            <v-icon style="color: #d83933;" small>
              mdi-alert-circle
            </v-icon>
            <o-text
              sm
              semi-bold
              text-red
              class="pl-2"
            >
              {{ NO_ORDER_FOUND }}
            </o-text>
          </div>

          <header-title
            :class="{
              'pb-3': $vuetify.breakpoint.smAndDown,
              'pb-5': $vuetify.breakpoint.mdAndUp,
            }"
          >
            {{ PLEASE_TRY_AGAIN }}
          </header-title>
          <o-text-field
            :title="ENTER_ORDER_ID"
            id="orderId"
            v-model="orderId"
            @onChange="onOrderChange($event)"
            @onKeyEnter="lookupOrder"
            @onPaste="validate()"
            :class="{ 'pb-3': $vuetify.breakpoint.mdAndUp }"
            :placeholder="ENTER_ORDER_ID_PLACEHOLDER"
          />
          <o-text-field
            :title="EMAIL_TEXT_FIELD_TITLE"
            id="email"
            v-if="isGiftReturn"
            v-model="email"
            @onChange="onEmailChange($event)"
            @onKeyEnter="lookupOrder"
            @onPaste="validate()"
            :class="{ 'pb-3': $vuetify.breakpoint.mdAndUp }"
            :placeholder="EMAIL"
          />
          <o-text-field
            :title="ZIP_CODE_TEXT_FIELD_TITLE"
            id="zipCode"
            v-model="zipCode"
            @onChange="onZipChange($event)"
            @onKeyEnter="lookupOrder"
            :placeholder="ZIP_CODE_TEXT_FIELD_TITLE"
            @onPaste="validate()"
          />
          <action-button
            @click="lookupOrder"
            :class="{
              'mt-2': $vuetify.breakpoint.smAndDown,
              'mt-0': $vuetify.breakpoint.mdAndUp,
            }"
            :loading="isLoading"
            :disabled="isDisabled"
            primary-color
            block
            x-large
          >
            {{ LOOKUP_ORDER }}
          </action-button>
        </v-form>
        <v-col class="mt-8 mb-2">
          <o-text
            sm
            text-blue
            class="d-flex justify-center mb-1"
          >
            Have a question? Give us a call.
          </o-text>
          <o-text
            sm
            text-blue
            class="d-flex justify-center"
          >
            {{ this.customerSupportContact }}
          </o-text>
        </v-col>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapGetters } from 'vuex'
import { FETCH_ORDER, GET_CONFIG } from '@/store/constants.js'
import OTextField from '@/components/molecules/OTextField.vue'
import ActionButton from '@/components/atoms/ActionButton.vue'
import { validateOrderId, validateZipcode, validateEmail } from '@/utils'
import {
  ORDER_LOOKUP,
  ORDER_LOOKUP_TRY_AGAIN,
  ZIP_CODE_LOOKUP,
  EMAIL_LOOKUP
} from 'branding/strings.json'
import OText from '@/components/atoms/OText.vue'
import HeaderTitle from '@/components/molecules/HeaderTitle.vue'
import { setGiftEmail, getIsGift, getGiftEmail } from '@/store/sessionStorage.js'

export default {
  components: {
    OTextField,
    ActionButton,
    OText,
    HeaderTitle
  },
  data () {
    return {
      orderId: '',
      zipCode: '',
      email: '',
      isDisabled: true,
      customerSupportContact: '',
      NO_ORDER_FOUND: ORDER_LOOKUP_TRY_AGAIN.NO_ORDER_FOUND,
      PLEASE_TRY_AGAIN: ORDER_LOOKUP_TRY_AGAIN.PLEASE_TRY_AGAIN,
      ENTER_ORDER_ID: ORDER_LOOKUP.ENTER_ORDER_ID,
      ENTER_ORDER_ID_PLACEHOLDER: ORDER_LOOKUP.ENTER_ORDER_ID_PLACEHOLDER,
      ZIP_CODE_TEXT_FIELD_TITLE: ZIP_CODE_LOOKUP.ZIP_CODE_TEXT_FIELD_TITLE,
      ENTER_ZIP_CODE_PLACEHOLDER: ZIP_CODE_LOOKUP.ENTER_ZIP_CODE_PLACEHOLDER,
      LOOKUP_ORDER: ZIP_CODE_LOOKUP.LOOKUP_ORDER,
      EMAIL_TEXT_FIELD_TITLE: EMAIL_LOOKUP.EMAIL_TEXT_FIELD_TITLE,
      EMAIL: EMAIL_LOOKUP.EMAIL,
      isGiftReturn: getIsGift()
    }
  },
  computed: {
    ...mapGetters({
      order: 'order/order',
      isLoading: 'order/isLoading',
      state_orderId: 'ui/orderId',
      state_zipCode: 'ui/zipCode',
      config: 'returns/config'
    })
  },
  mounted () {
    // TODO: depending on when you refresh sometimes it will only save one of these values
    this.orderId = this.state_orderId
    this.zipCode = this.state_zipCode
    this.email = getGiftEmail()
    this.validate()

    if (!this.config) {
      this.$store
        .dispatch('order/' + GET_CONFIG, null)
        .then((response) => {
          if (response) {
            this.customerSupportContact =
              response.retailer_info?.customer_support_contact
          }
        })
        .catch((err) => {
          console.error({ err })
        })
    } else {
      this.customerSupportContact = this.config?.retailer_info?.customer_support_contact
    }
  },
  methods: {
    validate () {
      this.isDisabled = !(
        this.isGiftReturn
          ? validateOrderId(this.orderId) &&
        validateZipcode(this.zipCode) &&
        validateEmail(this.email) : validateOrderId(this.orderId) &&
        validateZipcode(this.zipCode)
      )
    },
    onZipChange (event) {
      const value = event.target.value
      this.zipCode = value
      this.validate()
    },
    onEmailChange (event) {
      const value = event.target.value
      this.email = value
      this.validate()
    },
    onOrderChange (event) {
      const value = event.target.value
      this.orderId = value
      this.validate()
    },
    async lookupOrder () {
      this.$store.commit('ui/updateOrderId', this.orderId)
      this.$store.commit('ui/updateZipCode', this.zipCode)
      setGiftEmail(this.email)

      let params = {
        order_id: this.orderId,
        zip_code: this.zipCode
      }

      if (this.isGiftReturn) {
        params = { ...params, email: this.email }
      }
      this.$store
        .dispatch('order/' + FETCH_ORDER, params)
        .then((response) => {
          if (response) {
            const orderHash =
              response && response != null
                ? response.order_hash
                : this.order.orderId
            this.$router.push('/orders/' + orderHash)
          } else {
            this.$router.push('/orderandzipcode')
          }
        })
        .catch((err) => {
          console.error({ err })
        })
    }
  }
}
</script>

<style scoped>
.container {
  padding-top: 60px;
}
</style>
