import {
  FETCH_START,
  FETCH_ORDER_END,
  SET_ERROR,
  UPDATE_SELECTED_ITEM,
  UPDATE_SELECTED_ITEM_QUANTITY,
  SELECT_ALL_ITEMS,
  UPDATE_REFUND_LIST,
  UPDATE_REFUND_LIST_FOR_RETURN_USER,
  UPDATE_EXCHANGE_LIST_FOR_RETURN_USER,
  UPDATE_SELECTED_ITEM_EXCHANGE_ELIGIBILITY,
  EMPTY_REFUND_LIST,
  EMPTY_EXCHANGE_LIST,
  UPDATE_SELECTED_ITEM_WITH_EXCHANGE_VARIANTS,
  UPDATE_EXCHANGE_LIST,
  REMOVE_ITEM_FROM_EXCHANGE_LIST
} from '@/store/constants'
import { CONSTANTS } from '@/constants/index.js'

const { RMA_STATUS, RETURN_METHODS, GOODS_STATUS, RETURN_STATUS } = CONSTANTS

export default {
  [FETCH_START] (state) {
    state.isLoading = true
  },
  [FETCH_ORDER_END] (state, data) {
    state.order = {
      ...data,
      order: {
        ...data.order,
        items: data.order.items
          ? data.order.items.map((o) => {
            return {
              ...o,
              selected: false,
              isSingleQuantity: o.quantity === 1,
              subtitle: getSubtitle(o),
              price: getRefundPrice(o),
              formattedRefundPrice: getFormattedRefundPrice(o),
              original_quantity: o.quantity,
              quantity: o.quantity > 1 ? 1 : o.quantity,
              exchangeVariants: [],
              color: getValueFromAttribute(o, 'color'),
              size: getValueFromAttribute(o, 'size'),
              quantity_in_stock:
                  o?.variants?.length > 0
                    ? o.variants[0]?.quantity_in_stock
                    : 0
            }
          })
          : []
      }
    }

    state.rmaList = {
      rmaListItems: data.rma_list
        ? data.rma_list.map((returnItems) => {
          return {
            ...returnItems,
            return_status: getRMAStatus(returnItems),
            rma_items: returnItems.rma_items.map((rmaItem) => {
              return {
                ...rmaItem,
                order_item: {
                  ...rmaItem.order_item,
                  selected: false,
                  isSingleQuantity: rmaItem.order_item.quantity === 1,
                  subtitle: getSubtitle(rmaItem.order_item),
                  price: getRefundPrice(rmaItem.order_item),
                  formattedRefundPrice: getFormattedRefundPrice(
                    rmaItem.order_item
                  ),
                  original_quantity: rmaItem.order_item.quantity,
                  quantity:
                  rmaItem.order_item.quantity > 1
                    ? 1
                    : rmaItem.order_item.quantity,
                  exchangeVariants: [],
                  color: getValueFromAttribute(rmaItem.order_item, 'color'),
                  size: getValueFromAttribute(rmaItem.order_item, 'size'),
                  quantity_in_stock:
                      rmaItem?.variants?.length > 0
                        ? rmaItem.variants[0]?.quantity_in_stock
                        : 0
                }
              }
            })
          }
        })
        : []
    }

    state.isLoading = false
  },
  [SET_ERROR] (state) {
    state.isLoading = false
  },
  [UPDATE_SELECTED_ITEM] (state, identifier) {
    const item = state.order.order.items.find(
      (x) => x.identifier === identifier
    )
    item.selected = !item.selected
  },
  [UPDATE_SELECTED_ITEM_QUANTITY] (state, { identifier, quantity }) {
    const item = state.order.order.items.find(
      (x) => x.identifier === identifier
    )
    item.quantity = quantity
  },
  [UPDATE_SELECTED_ITEM_EXCHANGE_ELIGIBILITY] (state, { identifier, exchangeMetadata }) {
    const item = state.order.order.items.find(
      (x) => x.identifier === identifier
    )
    item.exchange_metadata = exchangeMetadata
  },
  [SELECT_ALL_ITEMS] (state) {
    state.order.order.items = state.order.order.items.map((l) => {
      return { ...l, selected: true }
    })
  },
  [UPDATE_REFUND_LIST_FOR_RETURN_USER] (state, list) {
    state.refundList = list
  },
  [UPDATE_EXCHANGE_LIST_FOR_RETURN_USER] (state, list) {
    state.exchangeList = list
  },
  [UPDATE_REFUND_LIST] (state, refundItem) {
    const existingIds = state.refundList.map((x) => x.identifier)
    if (
      state.refundList.indexOf(refundItem) === -1 &&
      !existingIds.includes(refundItem.identifier)
    ) {
      state.refundList.push(refundItem)

      // remove item from exchange list
      const filteredExchangeList = state.exchangeList.filter(
        (item) => item.identifier !== refundItem.identifier
      )
      state.exchangeList = filteredExchangeList
    } else {
      state.refundList = state.refundList.map((item) => {
        if (item.identifier !== refundItem.identifier) {
          return item
        } else {
          return refundItem
        }
      })
    }
  },

  [EMPTY_REFUND_LIST] (state) {
    state.refundList = []
  },

  [UPDATE_SELECTED_ITEM_WITH_EXCHANGE_VARIANTS] (state, { data, identifier }) {
    const item = state.order.order.items.find(
      (x) => x.identifier === identifier
    )
    item.exchangeVariants = data.map((item) => {
      return {
        ...item,
        price: getExchangePrice(item),
        formattedExchangePrice: getFormattedExchangePrice(item),
        sku: item.SKU,
        identifier: item.SKU
      }
    })
  },

  [REMOVE_ITEM_FROM_EXCHANGE_LIST] (state, identifier) {
    const filteredReturnList = state.exchangeList.filter(
      (item) => item.identifier !== identifier
    )
    state.exchangeList = filteredReturnList
  },

  [UPDATE_EXCHANGE_LIST] (state, exchangeItem) {
    // if exchangeItem doesn't exist in the list, add it and check if it's already in the refund list, if so remove it from the refund list
    // else update the item in the exhcange list

    const existingIds = state.exchangeList.map((x) => x.identifier)
    if (
      state.exchangeList.indexOf(exchangeItem) === -1 &&
      !existingIds.includes(exchangeItem.identifier)
    ) {
      state.exchangeList.push(exchangeItem)

      // remove from refund list if exists
      const filteredRefundList = state.refundList.filter(
        (item) => item.identifier !== exchangeItem.identifier
      )
      state.refundList = filteredRefundList
    } else {
      state.exchangeList = state.exchangeList.map((item) => {
        if (item.identifier !== exchangeItem.identifier) {
          return item
        } else {
          return exchangeItem
        }
      })
    }
  },
  [EMPTY_EXCHANGE_LIST] (state) {
    state.exchangeList = []
  }
}

function getRMAStatus (returnItem) {
  let rmaStatusRes = RETURN_STATUS.STARTED
  const rmaStatus = returnItem.status.toUpperCase()
  const goodsStatus = returnItem.goods_status.toUpperCase()
  var returnMethodType = ''
  if (returnItem && returnItem.return_method) {
    returnMethodType = returnItem.return_method.type
  }

  if (returnMethodType && returnMethodType === RETURN_METHODS.MAIL_BACK) {
    if (
      rmaStatus === RMA_STATUS.CREATED &&
      goodsStatus === GOODS_STATUS.UNKNOWN
    ) {
      rmaStatusRes = RETURN_STATUS.STARTED
    } else if (
      rmaStatus === RMA_STATUS.CREATED &&
      (goodsStatus === GOODS_STATUS.PRE_TRANSIT ||
        goodsStatus === GOODS_STATUS.TRANSIT)
    ) {
      rmaStatusRes = RETURN_STATUS.IN_TRANSIT
    } else if (
      (rmaStatus === RMA_STATUS.REFUNDED ||
        rmaStatus === RMA_STATUS.REFUND_ERROR) &&
      goodsStatus === GOODS_STATUS.TRANSIT
    ) {
      rmaStatusRes = RETURN_STATUS.RECEIVED // Transit status will be skipped and goes directly to RECEIVED status
    } else if (
      rmaStatus === RMA_STATUS.READY_TO_REFUND &&
      (goodsStatus === GOODS_STATUS.PRE_TRANSIT ||
        goodsStatus === GOODS_STATUS.DELIVERED ||
        goodsStatus === GOODS_STATUS.RECEIVED)
    ) {
      rmaStatusRes = RETURN_STATUS.RECEIVED
    } else if (
      (rmaStatus === RMA_STATUS.REFUNDED ||
        rmaStatus === RMA_STATUS.REFUND_ERROR ||
        rmaStatus === RMA_STATUS.PARTIAL_REFUNDED) &&
      (goodsStatus === GOODS_STATUS.DELIVERED ||
        goodsStatus === GOODS_STATUS.RECEIVED)
    ) {
      rmaStatusRes = RETURN_STATUS.COMPLETED
    }
  } else if (
    returnMethodType &&
    returnMethodType === RETURN_METHODS.EXPRESS_RETURNS
  ) {
    if (
      rmaStatus === RMA_STATUS.CREATED &&
      goodsStatus === GOODS_STATUS.CREATED
    ) {
      rmaStatusRes = RETURN_STATUS.STARTED
    } else if (
      rmaStatus === RMA_STATUS.CREATED &&
      goodsStatus === GOODS_STATUS.ACCEPTED
    ) {
      rmaStatusRes = RETURN_STATUS.IN_TRANSIT
    } else if (
      rmaStatus === RMA_STATUS.READY_TO_REFUND &&
      goodsStatus === GOODS_STATUS.ACCEPTED
    ) {
      rmaStatusRes = RETURN_STATUS.RECEIVED
    } else if (
      rmaStatus === RMA_STATUS.REFUNDED &&
      goodsStatus === GOODS_STATUS.ACCEPTED
    ) {
      rmaStatusRes = RETURN_STATUS.COMPLETED
    }
  } else if (returnMethodType && returnMethodType === RETURN_METHODS.IN_STORE) {
    if (
      rmaStatus === RMA_STATUS.CREATED &&
      goodsStatus === GOODS_STATUS.CREATED
    ) {
      rmaStatusRes = RETURN_STATUS.STARTED
    } else if (
      rmaStatus === RMA_STATUS.READY_TO_REFUND &&
      goodsStatus === GOODS_STATUS.ACCEPTED
    ) {
      rmaStatusRes = RETURN_STATUS.RECEIVED
    } else if (
      (rmaStatus === RMA_STATUS.REFUNDED ||
        rmaStatus === RMA_STATUS.REFUND_ERROR ||
        rmaStatus === RMA_STATUS.PARTIAL_REFUNDED) &&
      goodsStatus === GOODS_STATUS.RECEIVED
    ) {
      rmaStatusRes = RETURN_STATUS.COMPLETED
    }
  }

  if (rmaStatus === RMA_STATUS.CANCELED) {
    rmaStatusRes = RETURN_STATUS.CANCELED
  }

  return rmaStatusRes
}

function getSubtitle (orderItem) {
  if (!orderItem?.variants?.length > 0) {
    return ''
  }

  const variantAttributes = orderItem?.variants[0].attributes.slice()
  const sizeIndex = variantAttributes.findIndex(attribute => attribute.name.toLowerCase() === 'size')

  if (sizeIndex === -1) {
    return ''
  }

  const sizeValue = variantAttributes.splice(sizeIndex, 1)
  variantAttributes.unshift(sizeValue[0])

  return variantAttributes
    .filter((x) => x.name !== 'Disposition')
    .map((x) => x.value)
    .join(' ')
}

function getValueFromAttribute (orderItem, attr) {
  return orderItem.variants?.length > 0
    ? orderItem.variants[0].attributes.find(
      (x) => x.name.toLowerCase() === attr.toLowerCase()
    )?.value
    : ''
}

function getRefundPrice (orderItem) {
  return (orderItem && orderItem.calculated_unit_price_cents
    ? orderItem.calculated_unit_price_cents / 100
    : 0
  ).toFixed(2)
}

function getFormattedRefundPrice (item) {
  const price = getRefundPrice(item)
  return `$${Number(price).toFixed(2)}`
}

function getExchangePrice (item) {
  return (item && item.unit_price_amount_cents
    ? item.unit_price_amount_cents / 100
    : 0
  ).toFixed(2)
}

function getFormattedExchangePrice (item) {
  const price = getExchangePrice(item)
  return `$${Number(price).toFixed(2)}`
}
