<template>
  <div class="mb-16">
    <div
      :class="{
        'ml-3 mt-2': $vuetify.breakpoint.smAndDown,
        'ml-8 mt-8': $vuetify.breakpoint.mdAndUp,
      }"
      v-if="!this.isModal"
    >
      <back-button />
    </div>
    <div :class="{ 'mt-n12': $vuetify.breakpoint.mdAndUp }">
      <div
        style="max-width: 664px"
        :class="{
          'mx-3': $vuetify.breakpoint.smAndDown && !this.isModal,
          'mx-auto': $vuetify.breakpoint.mdAndUp,
        }"
      >
        <v-row
          class="mx-auto mb-4"
          :class="{ 'mx-3': this.isModal }"
          align="center"
        >
          <header-title>
            {{ this.title }}
          </header-title>
          <div v-if="showOrderDetails" class="ml-n3">
            <span>
              <o-text text-secondary md>
                {{ this.returnDate }}
              </o-text>
              <o-text text-secondary md>
                {{ this.subTitle }}
              </o-text>
            </span>
          </div>
        </v-row>

        <div v-if="policies.exchanges_allowed && exchangeItems.length > 0">
          <div class="d-flex justify-content-between align-center mt-12">
            <o-text base bold>
              {{ EXCHANGES_TITLE }}
            </o-text>
            <o-text sm>
              Ships immediately
            </o-text>
          </div>
          <v-row
            justify="center"
            class="mb-4 mt-0 mx-auto"
            :class="{}"
          >
            <div class="bg-well pt-1 pb-3 mt-2 mb-4">
              <template v-for="(item, index) in exchangeItems">
                <v-row :key="index">
                  <v-row>
                    <v-col cols="10">
                      <image-thumbnail
                        :key="index"
                        :item="item"
                        :index="index"
                        :title="ORIGINAL_PURCHASE"
                        :sub-title1="item.original_item.title"
                        :sub-title2="
                          item.original_item.color +
                            ' - ' +
                            item.original_item.size
                        "
                        :image-url="item.original_item.image_url"
                        :order-brand="order.order.concept"
                        product-thumbnail
                        is-small-thumbnail
                        from-return-item
                      />
                      <div class="ml-6">
                        <v-img
                          :src="require('@/assets/images/down_arrow.png')"
                          max-width="22"
                          max-height="22"
                          style="z-index: 1"
                          contain
                        />
                      </div>
                    </v-col>
                    <v-col
                      cols="2"
                      class="ml-auto"
                      style="justify-self: flex-end"
                    >
                      <div
                        class="d-flex"
                        style="justify-content: flex-end;"
                        v-if="item.quantity && item.quantity > 1"
                      >
                        <v-icon style="color: #000000;" x-small>
                          close
                        </v-icon>
                        <o-text
                          class="ml-1 mr-2 mb-0"
                          sm
                          semi-bold
                        >
                          {{ item.quantity }}
                        </o-text>
                      </div>
                    </v-col>
                  </v-row>
                  <v-row class="mb-1 mt-n3">
                    <v-col cols="10">
                      <image-thumbnail
                        :key="index"
                        :item="item"
                        :index="index"
                        :title="INCOMING_ITEM"
                        :sub-title1="item.title"
                        :sub-title2="item.color + ' - ' + item.size"
                        :image-url="item.image_url"
                        :order-brand="order.order.concept"
                        product-thumbnail
                        is-small-thumbnail
                        from-return-item
                      />
                    </v-col>
                    <v-col cols="2" align-self="center">
                      <div class="d-flex justify-center align-center">
                        <!-- <o-text sm>
                          {{ item.formattedExchangePrice }}
                        </o-text> -->
                      </div>
                    </v-col>
                  </v-row>
                  <div
                    v-if="index !== exchangeItems.length - 1"
                    class="py-2 mx-auto dviderLine"
                  />
                </v-row>
              </template>
            </div>
          </v-row>
        </div>

        <div v-if="this.refundList.length > 0">
          <o-text bold class="mt-4">
            {{ REFUNDS_TITLE }}
          </o-text>
          <v-row
            justify="center"
            class="mb-4 mt-0 mx-auto"
            :class="{}"
          >
            <div class="bg-well pr-4 pt-1 pb-3 mt-2 mb-4">
              <template v-for="(item, index) in this.refundList">
                <v-row :key="index" class="justify-space-between">
                  <v-col cols="8">
                    <image-thumbnail
                      :key="index"
                      :item="item"
                      :index="index"
                      :title="item.title"
                      :sub-title1="item.subtitle"
                      :image-url="item.image_url"
                      :order-brand="order.order.concept"
                      product-thumbnail
                      is-small-thumbnail
                    />
                  </v-col>
                  <v-col cols="2" class="ml-auto">
                    <div
                      class="d-flex align-center"
                      v-if="item.quantity && item.quantity > 1"
                    >
                      <v-icon style="color: #000000;" x-small>
                        close
                      </v-icon>
                      <o-text
                        class="ml-1 mr-2 mb-0"
                        sm
                        semi-bold
                      >
                        {{ item.quantity }}
                      </o-text>
                    </div>
                  </v-col>
                  <v-col style="max-width: 50px">
                    <o-text
                      sm
                      :key="index"
                      class="d-flex justify-end"
                    >
                      {{ getRefundPrice(item) }}
                    </o-text>
                  </v-col>
                </v-row>
              </template>
            </div>
          </v-row>
        </div>

        <o-text bold class="mt-4">
          {{ RETURN_METHOD_TITLE }}
        </o-text>
        <v-row
          justify="center"
          class="mb-4 mt-0 mx-auto"
          :class="{}"
        >
          <div class="bg-well px-4 pt-1 pb-3 mt-2 mb-4">
            <div class="d-flex justify-content-between align-center mt-4">
              <div>
                <image-thumbnail
                  is-small-thumbnail
                  v-bind="{ isCustomerKeeps: this.isCustomerKeeps()}"
                  :title="getReturnMethodTitle()"
                  :sub-title1="getReturnMethodSubTitle()"
                  :image-url="getReturnMethodIcon()"
                />
              </div>
              <div class="d-flex justify-end mt-n4">
                <o-text sm>
                  {{
                    this.isCustomerKeeps() || Number(getReturnMethodFee()) === 0
                      ? "Free"
                      : getReturnMethodFee()
                  }}
                </o-text>
              </div>
            </div>
          </div>
        </v-row>

        <o-text
          v-if="
            (this.isGiftReturn || this.isGift) &&
              this.RETURN_REVIEW_GIFT_RETURN_NOTICE.length > 0
          "
          text-secondary
          sm
        >
          {{ RETURN_REVIEW_GIFT_RETURN_NOTICE }}
        </o-text>
        <o-text bold class="mt-4">
          {{ RETURN_SUMMARY }}
        </o-text>
        <v-row justify="center" class="mb-0 mt-0">
          <div class="px-3 pt-1 pb-3 mt-0 mb-0">
            <div
              v-for="(item, idx) in review.returnSummary"
              :key="idx"
              class="d-flex justify-content-between align-center mt-2"
            >
              <o-text text-secondary md>
                {{ item.title }}
              </o-text>
              <o-text text-secondary md>
                {{
                  formattedPrice(
                    item.value,
                    item.type === "exchange-subtotal" ||
                      item.type === "shipping"
                  )
                }}
              </o-text>
            </div>
          </div>
        </v-row>

        <v-divider class="mt-1" />

        <div class="d-flex justify-content-between align-center mt-4">
          <o-text text-secondary md>
            {{ TOTAL_ESTIMATED_REFUND }}
          </o-text>
          <o-text
            text-secondary
            lg
            bold
          >
            {{ formattedPrice(returnTotal()) }}
          </o-text>
        </div>

        <div class="d-flex justify-content-between align-center mt-4">
          <o-text
            text-secondary
            sm
            v-html="ESTIMATED_REFUND_TIMING_TITLE"
          />
          <div class="d-flex align-center">
            <o-text
              v-if="this.isGiftReturn || this.isGift"
              default
              sm
              class="mr-2"
              v-html="GIFT_CARD"
            />
            <o-text
              v-else
              text-secondary
              sm
              bold
              v-html="ORIGINAL_PAYMENT"
              class="mr-2"
            />
            <v-img
              v-if="!(this.isGiftReturn || this.isGift)"
              :src="require('@/assets/images/payment_card.png')"
              max-width="48"
              max-height="32"
            />
            <v-img
              v-else
              :src="require('@/assets/images/gift-card-icon.png')"
              max-width="48"
              height="35"
            />
          </div>
        </div>

        <v-divider class="mt-4" />

        <div v-if="showConfirmActionView">
          <div class="d-flex justify-start">
            <o-text sm>
              {{ RETURN_REVIEW_CONFIRM_MESSAGE1 }} I agree to Optoro's
              <span
                style="cursor:pointer; text-decoration: underline"
                @click.stop="openTermsLink"
              >
                Terms
              </span>
              and
              <span
                style="cursor:pointer; text-decoration: underline"
                @click.stop="openPrivacyLink"
              >
                Privacy Policy
              </span>
            </o-text>
            <o-pop-up
              :show="errorDialog"
              @close="errorDialog = false"
              :button-message="this.CREATE_RMA_EXCHANGE_ERROR_TITLE"
              :message="this.CREATE_RMA_EXCHANGE_ERROR_DESCRIPTION"
              :title="'Error'"
              :on-click="returnToOrderDisplay"
            />
          </div>

          <div class="d-flex mt-8 pb-6 align-center justify-content-between">
            <action-button
              @click.stop="handleConfirm"
              primary-color
              large
              :loading="isLoading"
              :disabled="isLoading"
            >
              {{ CONFIRM_BUTTON }}
            </action-button>
          </div>
          <o-text
            sm
            bold
            class="red--text"
          >
            {{ errorMessage }}
          </o-text>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import OText from '@/components/atoms/OText.vue'
import HeaderTitle from '@/components/molecules/HeaderTitle.vue'
import {
  RETURN_REVIEW,
  RETURN_METHOD_SCREENS,
  CUSTOMER_KEEPS
} from 'branding/strings.json'
import BackButton from '@/components/atoms/BackButton.vue'
import ImageThumbnail from '@/components/organism/ImageThumbnail.vue'
import ActionButton from '@/components/atoms/ActionButton.vue'
import { mapGetters, mapMutations } from 'vuex'
import { CreateRMAService } from '@/common/api.service'
import logoImage from 'branding/assets/images/logo.png'
import { getFormattedDate } from '@/utils'
import { CONSTANTS } from '@/constants/index.js'
import { getIsGift, getGiftEmail } from '@/store/sessionStorage.js'
import {
  getSelectedReturnMethodDropOffDescription,
  isRMAFreeShipping
} from '@/controllers/ReturnMethodsController.js'
import OPopUp from '@/components/atoms/OPopUp.vue'
export default {
  name: 'ReviewView',
  components: {
    OText,
    HeaderTitle,
    BackButton,
    ImageThumbnail,
    ActionButton,
    OPopUp
  },
  props: {
    tracker: {
      type: Boolean,
      required: false,
      default: false
    },
    isModal: {
      type: Boolean,
      required: false
    },
    showConfirmActionView: {
      type: Boolean,
      required: false,
      default: true
    },
    title: {
      type: String,
      required: false,
      default: 'Review your return'
    },
    showOrderDetails: {
      type: Boolean,
      required: false,
      default: false
    },
    returnDate: {
      type: String,
      required: false,
      default: ''
    },
    isGift: {
      type: Boolean,
      required: false,
      default: false
    },
    isSelectedRmaFreeShipping: {
      type: Boolean,
      required: false,
      default: false
    },
    subTitle: {
      type: String,
      required: false,
      default: ''
    }
  },
  computed: {
    ...mapGetters({
      order: 'order/order',
      refundList: 'order/refundList',
      exchangeList: 'order/exchangeList',
      exchangeEligibility: 'exchange/getExchangeEligibility',
      policies: 'returns/policies',
      selectedReturnMethod: 'returns/getSelectedReturnMethod',
      reasons: 'returns/reasons',
      isFreeShipping: 'order/isFreeShipping',
      destinationResponse: 'ui/destinationResponse'
    }),
    isGiftReturn () {
      return this.tracker ? this.isGift : getIsGift()
    }
  },
  data () {
    return {
      RETURN_REVIEW_TITLE: RETURN_REVIEW.RETURN_REVIEW_TITLE,
      EXCHANGES_TITLE: RETURN_REVIEW.EXCHANGES_TITLE,
      EXCHANGE: CONSTANTS.EXCHANGE,
      REFUNDS_TITLE: RETURN_REVIEW.REFUNDS_TITLE,
      RETURN_METHOD_TITLE: RETURN_REVIEW.RETURN_METHOD_TITLE,
      RETURN_METHOD_SUB_TITLE: RETURN_REVIEW.RETURN_METHOD_SUB_TITLE,
      RETURN_METHOD_SUB_TITLE2: RETURN_REVIEW.RETURN_METHOD_SUB_TITLE2,
      RETURN_SUMMARY: RETURN_REVIEW.RETURN_SUMMARY,
      RETURN_SUB_TOTAL: RETURN_REVIEW.RETURN_SUB_TOTAL,
      EXCHANGE_SUB_TOTAL: RETURN_REVIEW.EXCHANGE_SUB_TOTAL,
      SHIPPING_FEE: RETURN_REVIEW.SHIPPING_FEE,
      TOTAL_ESTIMATED_REFUND: RETURN_REVIEW.TOTAL_ESTIMATED_REFUND,
      ORIGINAL_PAYMENT: RETURN_REVIEW.ORIGINAL_PAYMENT,
      MAIL_BACK: CONSTANTS.RETURN_METHODS.MAIL_BACK,
      EXPRESS_RETURNS: CONSTANTS.RETURN_METHODS.EXPRESS_RETURNS,
      IN_STORE: CONSTANTS.RETURN_METHODS.IN_STORE,
      CUSTOMER_KEEP: CONSTANTS.RETURN_METHODS.CUSTOMER_KEEP,
      STORE_CREDIT: RETURN_REVIEW.STORE_CREDIT,
      GIFT_CARD: RETURN_REVIEW.GIFT_CARD,
      ESTIMATED_REFUND_TIMING_TITLE:
        RETURN_REVIEW.ESTIMATED_REFUND_TIMING_TITLE,
      RETURN_REVIEW_CONFIRM_MESSAGE1:
        RETURN_REVIEW.RETURN_REVIEW_CONFIRM_MESSAGE1,
      CONFIRM_BUTTON: RETURN_REVIEW.CONFIRM_BUTTON,
      TERMS_CONDITIONS: RETURN_REVIEW.TERMS_CONDITIONS,
      PACKAGE_AND_PRINT: RETURN_METHOD_SCREENS.PACKAGE_AND_PRINT,
      NO_PACKAGE_AND_PRINT: RETURN_METHOD_SCREENS.NO_PACKAGE_AND_PRINT,
      email: getGiftEmail(),
      ORIGINAL_PURCHASE: RETURN_REVIEW.ORIGINAL_PURCHASE,
      INCOMING_ITEM: RETURN_REVIEW.INCOMING_ITEM,
      CARRIERS: CONSTANTS.CARRIERS,
      REVIEW_VIEW_RETURN_SUMMARY: CONSTANTS.REVIEW_VIEW_RETURN_SUMMARY,
      CREATE_RMA_EXCHANGE_ERROR:
        CONSTANTS.ERROR_CODES.CREATE_RMA_EXCHANGE_ERROR,
      CREATE_RMA_EXCHANGE_ERROR_TITLE:
        RETURN_REVIEW.CREATE_RMA_EXCHANGE_ERROR_TITLE,
      CREATE_RMA_EXCHANGE_ERROR_DESCRIPTION:
        RETURN_REVIEW.CREATE_RMA_EXCHANGE_ERROR_DESCRIPTION,
      RETURN_REVIEW_GIFT_RETURN_NOTICE:
        RETURN_REVIEW.RETURN_REVIEW_GIFT_RETURN_NOTICE,
      CUSTOMER_KEEPS_REVIEW_TITLE: CUSTOMER_KEEPS.REVIEW_TITLE,
      CUSTOMER_KEEPS_REVIEW_SUB_TITLE: CUSTOMER_KEEPS.REVIEW_SUB_TITLE,
      exchangeItems: [],
      review: {
        name: 'Modrno',
        refunds: [],
        returnSummary: []
      },
      errorMessage: '',
      isLoading: false,
      logoImage,
      todayDate: '',
      errorDialog: false
    }
  },
  methods: {
    ...mapMutations({
      updateCreatedAt: 'ui/updateRmaCreatedAt'
    }),
    getRefundPrice (item) {
      const price = item ? item.price * item.quantity : 0
      return `$${Number(price).toFixed(2)}`
    },
    refundSubTitle (item) {
      return item.size + ' ' + item.color
    },
    async handleConfirm () {
      const {
        name,
        type,
        customer_facing_carrier: cfc
      } = this.selectedReturnMethod
      const params = {
        concept: process.env.VUE_APP_OPTITURN_ID,
        giftee_email: this.isGiftReturn ? this.email : '',
        rma_items: this.refundList.map((item) => {
          return {
            order_id: this.order.order_id,
            order_line_item_identifier: item.identifier,
            sku: item.sku,
            upc: item.upc,
            concept: item.concept
              ? item.concept
              : process.env.VUE_APP_OPTITURN_ID,
            quantity: item.quantity,
            return_reason: item.reason,
            return_method: { name, type, customer_facing_carrier: cfc },
            exchange_metadata: {
              exchange_offered: item.exchange_metadata
                ? item.exchange_metadata.repurchase_model_decision
                : false,
              exchange_used: false,
              request_id: item.exchange_metadata
                ? item.exchange_metadata.request_id
                : '',
              exchange_item_amount_cents: 0
            }
          }
        })
      }
      const rmaexchangeList = this.exchangeItems.map((item) => {
        const exchangeItem = {
          unit_price_amount_cents: item.unit_price_amount_cents,
          sku: item.sku,
          identifier: item.identifier,
          quantity: item.quantity,
          title: item.title,
          size: item.size,
          is_eligible: item.is_eligible,
          color: item.color,
          image_url: item.image_url,
          original_item: {
            tax_amount_cents: item.original_item.tax_amount_cents,
            quantity: item.original_item.quantity,
            title: item.original_item.title,
            size: item.original_item.size,
            color: item.original_item.color,
            image_url: item.original_item.image_url,
            calculated_unit_price_cents:
              item.original_item.calculated_unit_price_cents
          }
        }
        return {
          order_id: this.order.order_id,
          order_line_item_identifier: item.identifier,
          sku: item.original_item.sku,
          upc: item.original_item.upc,
          concept: item.concept
            ? item.concept
            : process.env.VUE_APP_OPTITURN_ID,
          quantity: item.quantity,
          return_reason: item.reason,
          return_method: { name, type, customer_facing_carrier: cfc },
          is_exchange: true,
          exchange_item: exchangeItem,
          exchange_metadata: {
            exchange_offered:
              item.original_item.exchange_metadata.repurchase_model_decision,
            exchange_used: true,
            request_id: item.original_item.exchange_metadata.request_id,
            exchange_item_amount_cents:
              item.original_item.calculated_unit_price_cents
          }
        }
      })
      if (this.policies.destination_api_allowed) {
        params.destination_api_call_uuid = this.destinationResponse.request_uuid
      }
      params.rma_items.push(...rmaexchangeList)
      this.isLoading = true
      CreateRMAService.createRMA(params)
        .then(({ data }) => {
          if (data) {
            this.updateCreatedAt(data.created_at)
            this.$router.push(
              '/orders/' + this.order.order_hash + '/confirm/' + data.identifier
            )
          }
        })
        .catch((error) => {
          if (error?.response?.status === this.CREATE_RMA_EXCHANGE_ERROR) {
            this.errorDialog = true
          } else {
            this.errorMessage = error.response.data
          }
        })
        .finally(() => {
          this.isLoading = false
        })
    },
    formattedPrice: function (item, isNegative) {
      if (isNegative) {
        return `($${Number(item).toFixed(2)})`
      }
      return `$${Number(item).toFixed(2)}`
    },
    returnToOrderDisplay () {
      this.$router.push('/orders/' + this.order.order_hash)
    },
    getReturnMethodIcon: function () {
      var icon = require('@/assets/images/placeholder.png')
      switch (this.selectedReturnMethod.type) {
        case this.CUSTOMER_KEEP: {
          icon = require('@/assets/images/customer_keep.svg')
          break
        }
        case this.IN_STORE: {
          icon = logoImage
          break
        }
        case this.EXPRESS_RETURNS: {
          switch (
            this.selectedReturnMethod?.customer_facing_carrier?.toLowerCase()
          ) {
            case this.CARRIERS.STAPLES: {
              icon = require('@/assets/images/staples.png')
              break
            }
            default: {
              icon = require('@/assets/images/placeholder.png')
            }
          }
          break
        }
        case this.MAIL_BACK: {
          switch (
            this.selectedReturnMethod?.customer_facing_carrier?.toLowerCase()
          ) {
            case this.CARRIERS.UPS: {
              icon = require('@/assets/images/ups.png')
              break
            }
            case this.CARRIERS.FEDEX: {
              icon = require('@/assets/images/fedex.png')
              break
            }
            case this.CARRIERS.USPS: {
              icon = require('@/assets/images/usps.png')
              break
            }
            default: {
              icon = require('@/assets/images/placeholder.png')
            }
          }
          break
        }
        default: {
          icon = require('@/assets/images/placeholder.png')
        }
      }
      return icon
    },
    getReturnMethodTitle: function () {
      if (this.isCustomerKeeps()) {
        return this.CUSTOMER_KEEPS_REVIEW_TITLE
      }

      return getSelectedReturnMethodDropOffDescription(
        this.selectedReturnMethod
      )
    },
    getReturnMethodSubTitle: function () {
      if (this.isCustomerKeeps()) {
        return this.CUSTOMER_KEEPS_REVIEW_SUB_TITLE
      }

      var requirePackageAndPrint = false
      this.policies.return_methods.forEach((method) => {
        if (method.type === this.selectedReturnMethod.type) {
          requirePackageAndPrint = method.requirePackageAndPrint
            ? method.requirePackageAndPrint
            : method.package_and_print
        }
      })
      return requirePackageAndPrint
        ? this.PACKAGE_AND_PRINT
        : this.NO_PACKAGE_AND_PRINT
    },
    getReturnMethodFee: function (addDollarSign = true) {
      var fee = 0
      // Check 1: Check if the return_method chosen has free shipping
      // Check 2: Check if the order has a tag that has free shipping
      // Check 3: Check if any of the return reasons selected exempt shipping for the RMA while being created or item had free shipping tag
      // Check 4: Check if the item selected in Tracker View had a free shipping return reason or had a free shipping item level tag
      if (!this.selectedReturnMethod.free_shipping && !this.isFreeShipping && !isRMAFreeShipping() && !this.isSelectedRmaFreeShipping) {
        const amount = this.tracker ? this.selectedReturnMethod?.flat_fee / 100 : this.selectedReturnMethod.flat_fee?.amount / 100
        fee = addDollarSign
          ? this.selectedReturnMethod.free_shipping || this.isCustomerKeeps()
            ? 'Free'
            : amount?.toLocaleString('en-US', {
              style: 'currency',
              currency: 'USD'
            })
          : amount?.toFixed(2)
      }
      return fee
    },
    refundSubTotal () {
      const subTotal = this.refundList
        .map((item) => Number(item.price.replace('$', '')) * item.quantity)
        .reduce((prev, curr) => prev + curr, 0)
      return subTotal
    },
    returnTotal () {
      const total =
        this.review.returnSummary
          .filter(
            (item) => item.type === this.REVIEW_VIEW_RETURN_SUMMARY.SUBTOTAL
          )
          .map((item) => Number(item.value))
          .reduce((prev, curr) => prev + curr, 0) -
        this.review.returnSummary
          .filter(
            (item) =>
              item.type === this.REVIEW_VIEW_RETURN_SUMMARY.EXCHANGE_SUBTOTAL
          )
          .map((item) => Number(item.value))
          .reduce((prev, curr) => prev + curr, 0) -
        this.review.returnSummary
          .filter(
            (item) => item.type === this.REVIEW_VIEW_RETURN_SUMMARY.SHIPPING
          )
          .map((item) => Number(item.value))
          .reduce((prev, curr) => prev + curr, 0)
      return total
    },
    addSummaryDetails () {
      let exchangeSubTotal = 0
      if (this.exchangeItems?.length > 0) {
        exchangeSubTotal = this.exchangeList
          .map(
            (item) =>
              Number(item.original_item.calculated_unit_price_cents / 100) *
              item.quantity
          )
          .reduce((prev, curr) => prev + curr, 0)
      }
      /** DO NOT change the below order */
      this.review.returnSummary.push({
        type: this.REVIEW_VIEW_RETURN_SUMMARY.SUBTOTAL,
        title: 'Return subtotal',
        value: this.refundSubTotal() + exchangeSubTotal
      })
      if (this.policies.exchanges_allowed && this.exchangeItems.length > 0) {
        this.review.returnSummary.push({
          type: this.REVIEW_VIEW_RETURN_SUMMARY.EXCHANGE_SUBTOTAL,
          title: 'Exchange subtotal',
          value: exchangeSubTotal
        })
      }
      this.review.returnSummary.push({
        type: this.REVIEW_VIEW_RETURN_SUMMARY.SHIPPING,
        title: 'Shipping fee',
        value: this.isCustomerKeeps() ? 0 : `${this.getReturnMethodFee(false)}`
      })
    },
    openTermsLink () {
      this.$router.push('/terms-of-service')
    },
    openPrivacyLink () {
      this.$router.push('/privacy-policy')
    },
    getCurrentDate () {
      this.todayDate = 'Date ' + getFormattedDate(this.returnDate)
    },
    isCustomerKeeps () {
      return this.destinationResponse && this.destinationResponse.customer_keep
    }
  },
  created () {},
  mounted () {
    this.exchangeItems = this.exchangeList
    var refundTimingEstimate = this.policies.refund_timing_estimate
      ? this.policies.refund_timing_estimate
      : '5-7'
    this.ESTIMATED_REFUND_TIMING_TITLE = RETURN_REVIEW.ESTIMATED_REFUND_TIMING_TITLE.replace(
      '$$refundTimingEstimate',
      refundTimingEstimate
    )
    this.getCurrentDate()
    this.addSummaryDetails()
  }
}
</script>
