<template>
  <div>
    <v-list
      id="mobile-scrolling-map"
      :class="$vuetify.breakpoint.smAndDown ? 'virtual-scroll-sm' : 'virtual-scroll'"
    >
      <template>
        <v-list-item
          v-for="(carrier, i) in carriers"
          :key="i"
          class="list-item"
        >
          <shipping-location-card
            :carrier="carrier.carrier"
            :index="i"
            :ref="'item' + String(i)"
            :tile-clicked="scrollToView"
            :handle-carrier-selection="handleCarrierSelection"
          />
        </v-list-item>
      </template>
    </v-list>
    <v-spacer />
  </div>
</template>

<script>
import { setSelectedReturnMethod } from '@/controllers/MapController.js'
import ShippingLocationCard from '@/components/organism/ShippingLocationCard.vue'
import { mapGetters } from 'vuex'

export default {
  name: 'MapTiles',
  components: { ShippingLocationCard },
  data: () => ({
  }),
  mounted () {},
  computed: {
    ...mapGetters({
      order: 'order/order'
    })
  },
  props: {
    carriers: {
      type: Array,
      required: true
    },
    scrollToView: {
      type: Function,
      required: true
    }
  },
  methods: {
    handleCarrierSelection (carrier) {
      setSelectedReturnMethod(carrier)
      this.$router.push('/orders/' + this.order.order_hash + '/review')
    }
  }
}
</script>

<style scoped>
  .virtual-scroll {
    overflow-y: scroll;
    height: 750px;
    padding: 0;
  }
  .virtual-scroll::-webkit-scrollbar, .virtual-scroll-sm::-webkit-scrollbar {
    display: none;  /* Safari and Chrome */
  }
  .virtual-scroll-sm {
    display: flex;
    overflow-x: scroll;
    height: unset;
    overflow-y: unset;
  }
  .list-item {
    padding: 0;
  }
</style>
