<template>
  <div
    class="default"
    :role="heading ? 'heading' : 'text'"
    :aria-level="level !== null ? level : ''"
    :class="{
      xl3: xl3,
      xl2: xl2,
      xl: xl,
      lg: lg,
      med: med,
      base: base,
      sm: sm,
      xs: xs,
      bold: bold,
      textPrimary: textPrimary,
      textSecondary: textSecondary,
      disabled: disabled,
      semiBold: semiBold,
      textBlue: textBlue,
      textRed: textRed,
      textGreen: textGreen,
      textWhite: textWhite,
      baseBold: baseBold,
      heading: Boolean,
      level: {
        type: Number,
        required: false,
        default: null,
      },
    }"
    v-on="$listeners"
    :title="text"
    :aria-label="text"
  >
    <slot />
  </div>
</template>

<script>
import { trim } from '@/utils'

export default {
  name: 'OText',
  props: {
    xl3: Boolean,
    xl2: Boolean,
    xl: Boolean,
    lg: Boolean,
    med: Boolean,
    base: Boolean,
    sm: Boolean,
    xs: Boolean,
    heading: Boolean,
    level: {
      type: Number,
      required: false,
      default: null
    },
    bold: Boolean,
    textPrimary: Boolean,
    textSecondary: Boolean,
    disabled: Boolean,
    semiBold: Boolean,
    textBlue: Boolean,
    textRed: Boolean,
    textGreen: Boolean,
    textWhite: Boolean,
    baseBold: Boolean
  },
  data () {
    return {
      text: ''
    }
  },
  mounted () {
    this.text = this.$slots.default
      ? trim(this.$slots.default[0].text)
      : this.$el.innerText
        ? trim(this.$el.innerText)
        : ''
  }
}
</script>

