<template>
  <div>
    <back-button
      v-show="!disableModalGoBack && !disableBackButton"
      override-go-back
      :override-on-click="() => openModal({ type: DIALOG_CARRIER_LOCATIONS })"
    />
    <div style="max-width: 664px; margin: auto;">
      <div class="d-flex justify-between">
        <header-title>
          {{ this.getSelectedReturnMethodDropOffDescription() }}
        </header-title>
        <o-text class="ml-auto mt-4" bold>
          {{ this.getShippingAmt(selectedCarrier) }}
        </o-text>
      </div>
      <div v-if="!isMailBack">
        <o-text>
          {{ selectedCarrier.distance || "" }}  -
          {{ selectedCarrier.formattedAddress || "" }}
        </o-text>

        <o-text>{{ selectedCarrier.formattedPhoneNumber || "" }}</o-text>
      </div>
      <div v-if="isRmaSubmitted">
        <a :href="'https://www.google.com/maps/dir/?api=1&destination=' + selectedCarrier.formattedAddress">Get directions</a>
      </div>
      <div class="mt-6">
        <div v-if="weekdayText && !isMailBack">
          <o-text
            text-primary
            lg
          >
            Opening hours
          </o-text>
          <div>
            <div
              v-for="({day, hours}, idx) in weekdayText"
              :key="idx"
              class="d-flex justify-content-between align-center mt-2"
            >
              <o-text text-secondary md>
                {{ day }}
              </o-text>
              <o-text
                v-if="day.closed"
                text-secondary
                md
              >
                CLOSED
              </o-text>
              <o-text
                v-else
                text-secondary
                md
              >
                {{ hours }}
              </o-text>
            </div>
          </div>
        </div>
        <v-divider
          class="mt-6 mb-8"
          v-if="weekdayText"
        />
        <!--          HOW IT WORKS SECTION             -->
        <div class="d-flex flex-column">
          <o-text
            text-primary
            lg
          >
            {{ HOW_IT_WORKS_TITLE }}
          </o-text>
          <!-- in-store -->
          <div
            v-if="selectedCarrier.type === IN_STORE"
            class="d-flex flex-column align-center mt-4"
          >
            <div class="d-flex align-center justify-between">
              <v-icon md>
                receipt
              </v-icon>
              <o-text class="ml-8">
                {{ SHOW_RECEIPT_STAFF }}
              </o-text>
            </div>
          </div>
          <!-- FIXME: enable with exchanges via policy API -->
          <!-- <div
            v-if="selectedCarrier.type === IN_STORE"
            class="d-flex flex-column align-center mt-4"
          >
            <div class="d-flex align-center justify-between">
              <v-icon md>
                mdi-archive-outline
              </v-icon>
              <o-text class="ml-8">
                {{ EXCHANGE_ITEM_RETURN_SHIP }}
              </o-text>
            </div>
          </div> -->
          <!-- express-return -->
          <div
            v-else-if="selectedCarrier.type === EXPRESS_RETURNS"
            class="d-flex flex-column align-center mt-4"
          >
            <div class="d-flex align-center justify-between">
              <v-icon md>
                qr_code
              </v-icon>
              <o-text class="ml-8">
                {{ QR_CODE_IN_STORE }}
              </o-text>
            </div>
          </div>
          <!-- mail-back -->
          <div
            v-else-if="selectedCarrier.type === MAIL_BACK"
            class="d-flex flex-column align-center mt-4"
          >
            <div class="d-flex align-center justify-between">
              <v-icon md>
                receipt
              </v-icon>
              <o-text class="ml-8">
                {{ this.mailBackHowItWorksDesc }}
              </o-text>
            </div>
          </div>
          <div
            v-if="(selectedCarrier.type === MAIL_BACK || selectedCarrier.type === EXPRESS_RETURNS) && isExchange"
            class="d-flex flex-column align-center mt-4"
          >
            <div class="d-flex align-center justify-between">
              <v-icon md>
                mdi-archive-outline
              </v-icon>
              <o-text class="ml-8">
                {{ EXCHANGE_HOW_IT_WORKS }}
              </o-text>
            </div>
          </div>
        </div>
        <v-divider
          class="mt-8 mb-6"
        />
        <!--        BENEFITS SECTION          -->
        <div
          v-if="selectedCarrier.type !== MAIL_BACK"
          class="d-flex flex-column"
        >
          <o-text text-primary lg>
            {{ BENEFITS_TITLE }}
          </o-text>
          <!-- in-store -->
          <div
            v-if="selectedCarrier.type === IN_STORE"
            class="d-flex flex-column mt-4"
          >
            <div class="d-flex justify-between">
              <v-icon md>
                print_disabled
              </v-icon>
              <o-text class="ml-8">
                {{ BENEFITS_NO_PRINT_DESC }}
              </o-text>
            </div>
            <div class="d-flex align-center justify-between mt-6">
              <v-icon md>
                spa
              </v-icon>
              <o-text class="ml-8">
                {{ BENEFITS_SUSTAINABLE_DESC }}
              </o-text>
            </div>
          </div>
          <!-- express-return -->
          <div
            v-else-if="selectedCarrier.type === EXPRESS_RETURNS"
            class="d-flex flex-column mt-4"
          >
            <div class="d-flex justify-between">
              <v-icon md>
                print_disabled
              </v-icon>
              <o-text class="ml-8">
                {{ BENEFITS_NO_PRINT_DESC }}
              </o-text>
            </div>
          </div>
          <!-- mail-back -->
        </div>
        <!--        REQUIREMENTS SECTION          -->
        <div
          v-if="selectedCarrier.type === MAIL_BACK"
          class="d-flex flex-column"
        >
          <o-text text-primary lg>
            {{ REQUIREMENTS_TITLE }}
          </o-text>
          <!-- mail-back -->
          <div
            v-if="selectedCarrier.type === MAIL_BACK"
            class="d-flex flex-column mt-4"
          >
            <div class="d-flex justify-between mt-6">
              <v-icon md>
                mdi-archive-outline
              </v-icon>
              <o-text class="ml-8">
                {{ REQUIREMENTS_SHIPPING_PACKAGE }}
              </o-text>
            </div>
            <div class="d-flex justify-between mt-6">
              <v-icon md>
                print
              </v-icon>
              <o-text class="ml-8">
                {{ REQUIREMENTS_PRINTING_LABEL }}
              </o-text>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import OText from '@/components/atoms/OText.vue'
import HeaderTitle from '@/components/molecules/HeaderTitle.vue'
import { RETURN_METHOD_DIALOGS } from 'branding/strings.json'
import BackButton from '@/components/atoms/BackButton.vue'
import { mapGetters, mapMutations } from 'vuex'
import { DIALOG_CARRIER_LOCATIONS } from '@/store/constants'
import { CONSTANTS } from '@/constants/index.js'
import { isExchangeOrder, isMailBackReturn, isUSPSCarrier, getSelectedReturnMethodDropOffDescription, getShippingAmt } from '@/controllers/ReturnMethodsController.js'

export default {
  components: { OText, HeaderTitle, BackButton },
  computed: {
    ...mapGetters({
      selectedCarrier: 'returns/getSelectedCarrier',
      disableModalGoBack: 'returns/getDisableModalGoBack',
      selectedReturnMethod: 'returns/getSelectedReturnMethod',
      exchangeList: 'order/exchangeList'
    }),
    weekdayText () {
      const weekdayText = this.selectedCarrier?.openingHours?.weekday_text
      if (weekdayText) {
        return weekdayText.map((day) => {
          const arr = day.split(/:(.+)/)
          return { day: arr[0], hours: arr[1] }
        })
      } else {
        return ''
      }
    },
    isExchange () {
      return isExchangeOrder(this.exchangeList)
    },
    isUSPS () {
      return isUSPSCarrier(this.selectedCarrier)
    },
    isMailBack () {
      return isMailBackReturn(this.selectedReturnMethod)
    }
  },
  props: {
    disableBackButton: {
      required: false,
      type: Boolean,
      default: false
    },
    isRmaSubmitted: {
      required: true,
      type: Boolean
    }
  },
  methods: {
    ...mapMutations({
      openModal: 'returns/openModal'
    }),
    getSelectedReturnMethodDropOffDescription () {
      return getSelectedReturnMethodDropOffDescription(this.selectedCarrier)
    },
    getShippingAmt () {
      return getShippingAmt(this.selectedCarrier)
    }
  },
  data () {
    return {
      HOW_IT_WORKS_TITLE: RETURN_METHOD_DIALOGS.HOW_IT_WORKS_TITLE,
      QR_CODE_IN_STORE: RETURN_METHOD_DIALOGS.QR_CODE_IN_STORE,
      BENEFITS_NO_PRINT_DESC: RETURN_METHOD_DIALOGS.BENEFITS_NO_PRINT_DESC,
      BENEFITS_CASHBACK_DESC: RETURN_METHOD_DIALOGS.BENEFITS_CASHBACK_DESC,
      BENEFITS_SUSTAINABLE_DESC:
        RETURN_METHOD_DIALOGS.BENEFITS_SUSTAINABLE_DESC,
      MAIL_BACK: CONSTANTS.RETURN_METHODS.MAIL_BACK,
      IN_STORE: CONSTANTS.RETURN_METHODS.IN_STORE,
      EXPRESS_RETURNS: CONSTANTS.RETURN_METHODS.EXPRESS_RETURNS,
      BENEFITS_TITLE: RETURN_METHOD_DIALOGS.BENEFITS_TITLE,
      BENEFITS_PRINT_DESC: RETURN_METHOD_DIALOGS.BENEFITS_PRINT_DESC,
      REQUIREMENTS_TITLE: RETURN_METHOD_DIALOGS.REQUIREMENTS_TITLE,
      REQUIREMENTS_SHIPPING_PACKAGE: RETURN_METHOD_DIALOGS.REQUIREMENTS_SHIPPING_PACKAGE,
      REQUIREMENTS_PRINTING_LABEL: RETURN_METHOD_DIALOGS.REQUIREMENTS_PRINTING_LABEL,
      SHOW_RECEIPT_STAFF: RETURN_METHOD_DIALOGS.SHOW_RECEIPT_STAFF,
      MAIL_BACK_HOW_IT_WORKS_DESC: RETURN_METHOD_DIALOGS.MAIL_BACK_HOW_IT_WORKS_DESC,
      EXCHANGE_HOW_IT_WORKS: RETURN_METHOD_DIALOGS.EXCHANGE_HOW_IT_WORKS,
      MAIL_BACK_HOW_IT_WORKS_DESC_MAIL_INNOVATIONS: RETURN_METHOD_DIALOGS.MAIL_BACK_HOW_IT_WORKS_DESC_MAIL_INNOVATIONS,
      EXCHANGE_ITEM_RETURN_SHIP:
        RETURN_METHOD_DIALOGS.EXCHANGE_ITEM_RETURN_SHIP,
      DIALOG_CARRIER_LOCATIONS,
      mailBackHowItWorksDesc: ''
    }
  },
  mounted () {
    this.mailBackHowItWorksDesc = this.isUSPS ? this.MAIL_BACK_HOW_IT_WORKS_DESC_MAIL_INNOVATIONS : this.MAIL_BACK_HOW_IT_WORKS_DESC
  }
}
</script>

