<template>
  <div class="mb-16">
    <v-row
      :class="{
        'mt-4 mb-16': $vuetify.breakpoint.smAndDown,
        'mx-2 my-6': $vuetify.breakpoint.mdAndUp,
      }"
    >
      <o-toast
        color="snackbarGreen"
        :timeout="timeout"
        left-icon="check_circle"
        :visible="show"
        @close="show = false"
        :title="this.TOAST_MESSAGE"
      />
      <v-col
        fixed
        top
        v-if="$vuetify.breakpoint.mdAndUp"
        md="3"
        class="mr-8 justify-md-start"
      >
        <v-col style="position: fixed" md="3">
          <return-reason-header
            :count="selectedItems.length"
            :index="currentItemIndex"
            :title="title"
            :show-feedback="this.step === RETURN_REASON_STEP.PRIMARY"
            @click.stop="backButtonClicked"
            :item="selectedItems[currentItemIndex]"
            @feedbackClicked="showReturnFeedback = true"
            :show-thumbnail="this.step === RETURN_REASON_STEP.PRIMARY"
            :order-brand="order.order.concept"
          />
        </v-col>
      </v-col>
    </v-row>
    <v-row class="justify-center mx-2 mt-n12">
      <v-col
        md="7"
        lg="5"
        xs="12"
        sm="8"
      >
        <div v-if="$vuetify.breakpoint.smAndDown">
          <return-reason-header
            :count="selectedItems.length"
            :index="currentItemIndex"
            :title="title"
            :show-feedback="this.step === RETURN_REASON_STEP.PRIMARY"
            @click.stop="backButtonClicked"
            :item="selectedItems[currentItemIndex]"
            class="mt-n4"
            @feedbackClicked="showReturnFeedback = true"
            :show-thumbnail="this.step === RETURN_REASON_STEP.PRIMARY"
            :order-brand="order.order.concept"
          />
        </div>
        <v-list
          class="mb-2"
          :class="{
            'mt-5': $vuetify.breakpoint.smAndDown,
            'mt-1': $vuetify.breakpoint.mdAndUp,
          }"
          md="3"
          v-if="this.step === RETURN_REASON_STEP.PRIMARY"
        >
          <div v-if="exchangesLoading">
            <list-item-skeleton :list="this.reasons" />
          </div>
          <template v-for="(reasonToDisplay, index) in this.reasons" v-else>
            <list-item
              :key="index"
              :index="index"
              :item="reasonToDisplay"
              @onClick.stop="returnReasonClicked(index)"
              class="ma-0 pa-0"
              :show-loading="isLoading && reason.id === reasonToDisplay.id"
            />
          </template>
        </v-list>
        <div class="d-flex justify-center mb-8">
          <action-button
            v-if="this.showFeedbackButton"
            text
            color="primary"
            @click.stop="showReturnFeedback = true"
          >
            {{ LEAVE_FEEDBACK }}
          </action-button>
        </div>
        <refund-or-exchange
          v-if="this.step === RETURN_REASON_STEP.ACTION"
          :return-reason-clicked="returnReasonClicked"
          :is-map-loading="isMapLoading"
        />
        <return-reason-feedback
          :visible="showReturnFeedback"
          @close="showReturnFeedback = false"
          @click.stop="feedbackSubmitted"
        />
        <v-overlay :value="loading">
          <v-progress-circular
            v-if="loading"
            :width="3"
            :active="loading"
            color="gray"
            :indeterminate="loading"
            class="justify-center align-center"
          />
        </v-overlay>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import ActionButton from '@/components/atoms/ActionButton.vue'
import ListItem from '@/components/molecules/ListItem.vue'
import ListItemSkeleton from '@/components/molecules/ListItemSkeleton.vue'
import ReturnReasonHeader from '@/components/organism/ReturnReasonHeader.vue'
import ReturnReasonFeedback from './ReturnReasonFeedback.vue'
import { EXCHANGE_REFUND, RETURN_REASON } from 'branding/strings.json'
import OToast from '@/components/atoms/OToast.vue'
import { mapGetters } from 'vuex'
import RefundOrExchange from '@/components/organism/RefundOrExchange.vue'
import { checkForExpressDropoffOptions } from '@/controllers/MapController.js'
import { CONSTANTS } from '@/constants'
import { IS_TRUST_WORTHY_USER, UPDATE_SELECTED_ITEM_EXCHANGE_ELIGIBILITY, DESTINATION } from '@/store/constants.js'
import { canExchange } from '@/controllers/ExchangesController'
import { getIsGift, getGiftEmail } from '@/store/sessionStorage.js'

export default {
  components: {
    ListItem,
    ListItemSkeleton,
    ReturnReasonHeader,
    ReturnReasonFeedback,
    ActionButton,
    OToast,
    RefundOrExchange
  },
  data: () => ({
    LETS_MAKE_IT_RIGHT_TITLE: EXCHANGE_REFUND.LETS_MAKE_IT_RIGHT_TITLE,
    IT_DID_NOT_WORK_OUT_TITLE: EXCHANGE_REFUND.IT_DID_NOT_WORK_OUT_TITLE,
    WHY_ARE_YOU_RETURNING: RETURN_REASON.WHY_ARE_YOU_RETURNING,
    TELL_US_MORE: RETURN_REASON.TELL_US_MORE,
    PLEASE_EXPLAIN_FURTHER: RETURN_REASON.PLEASE_EXPLAIN_FURTHER,
    LEAVE_FEEDBACK: RETURN_REASON.LEAVE_FEEDBACK,
    TOAST_MESSAGE: RETURN_REASON.TOAST_MESSAGE,
    loading: false,
    showReturnFeedback: false,
    title: RETURN_REASON.WHY_ARE_YOU_RETURNING,
    show: false,
    timeout: 5000,
    isMapLoading: false,
    RETURN_REASON_STEP: CONSTANTS.RETURN_REASON_STEP,
    isLoading: false,
    state_Email: getGiftEmail()
  }),
  watch: {
    loading (val) {
      if (!val) return
      setTimeout(() => this.refresh(), 1000)
    },
    $route (val) {
      if (val.name === 'reasons') {
        this.$store.commit(
          'returns/updateStep',
          this.RETURN_REASON_STEP.PRIMARY
        )
      } else {
        this.$store.commit(
          'returns/updateStep',
          this.RETURN_REASON_STEP.ACTION
        )
      }
    }
  },
  beforeUnmount () {},
  computed: {
    ...mapGetters({
      order: 'order/order',
      exchangeEligibility: 'exchange/getExchangeEligibility',
      reasons: 'returns/reasons',
      step: 'returns/step',
      currentItemIndex: 'returns/currentItemIndex',
      selectedItems: 'order/selectedItems',
      reason: 'returns/reason',
      policies: 'returns/policies',
      exchangesLoading: 'exchange/isLoading',
      policyReturnMethods: 'returns/policyReturnMethods',
      returnMethods: 'returns/returnMethods',
      refundList: 'order/refundList',
      exchangeList: 'order/exchangeList'
    }),
    item () {
      return this.selectedItems[this.currentItemIndex]
    },
    showFeedbackButton () {
      return (
        this.step === this.RETURN_REASON_STEP.PRIMARY &&
        this.policies.return_feedback_allowed
      )
    },
    isGiftReturn () {
      return getIsGift()
    }

  },
  mounted () {
    if (this.order.order_hash === '') {
      this.$router.push('/orders/' + this.$route.params.id)
    }
    if (this.$route.name === 'reasons') {
      this.$store.commit('returns/updateStep', this.RETURN_REASON_STEP.PRIMARY)
    } else {
      this.$store.commit('returns/updateStep', this.RETURN_REASON_STEP.ACTION)
    }
  },
  methods: {
    refresh () {
      this.loading = false
    },
    async checkForMap () {
      this.isMapLoading = true
      const willHaveMap = await checkForExpressDropoffOptions(
        this.returnMethods,
        this.policyReturnMethods
      )
      this.isMapLoading = false
      return willHaveMap
    },
    async checkForCustomerKeeps () {
      if (!this.policies.destination_api_allowed) {
        return false
      }
      const items = []
      let isCustomerKeeps
      const isGift = getIsGift()
      this.refundList.forEach(item => {
        const formattedItem = {
          return_reason: item.reason,
          sku: item.sku,
          upc: item.upc,
          concept: item.concept,
          tags: item.tags,
          calculated_unit_price_amount_cents: item.calculated_unit_price_cents
        }
        if (item.quantity > 1) {
          items.push(...Array(item.quantity).fill(formattedItem))
        } else {
          items.push(formattedItem)
        }
      }
      )
      const params = {
        items,
        email: isGift ? getGiftEmail() : this.order.shopper_email,
        is_gift: isGift
      }

      this.isLoading = true
      await this.$store
        .dispatch('order/' + DESTINATION, params)
        .then((response) => {
          this.$store.commit('ui/updateDestinationResponse', response)

          isCustomerKeeps = response.customer_keep
          this.isLoading = false
        })
      return isCustomerKeeps
    },
    async returnReasonClicked (index = null) {
      switch (this.step) {
        case this.RETURN_REASON_STEP.PRIMARY:
          this.returnReasonStepPrimary(
            this.reasons[index],
            this.selectedItems[this.currentItemIndex]
          )
          break
        case this.RETURN_REASON_STEP.ACTION:
          this.returnReasonStepAction()
          break
        default:
          break
      }
    },
    async returnReasonStepAction () {
      if (this.currentItemIndex === this.selectedItems.length - 1) {
        const hasMap = await this.checkForMap()
        const isCustomerKeeps = await this.checkForCustomerKeeps()
        if (isCustomerKeeps) {
          this.$router.push('/orders/' + this.order.order_hash + '/customer-keeps')
        } else if (hasMap) {
          this.$router.push(
            '/orders/' + this.order.order_hash + '/mapshipping'
          )
        } else {
          this.$router.push('/orders/' + this.order.order_hash + '/shipping')
        }
      } else {
        this.title = this.WHY_ARE_YOU_RETURNING
        this.$store.commit(
          'returns/updateStep',
          this.RETURN_REASON_STEP.PRIMARY
        )
        this.$store.commit('returns/incrementIndex', { root: true })
        this.show = true
      }
    },
    returnReasonStepPrimary (reason, selectedItem) {
      this.$store.commit('returns/updateReason', {
        reason: reason,
        sku: selectedItem.sku
      })

      if (canExchange(reason.recommendation_type)) {
        const momentaryRma = this.returnTrustWorthyPayload(reason, selectedItem)
        let params = {
          order_id: this.order.order_id,
          momentary_rma: momentaryRma,
          current_item: {
            sku: selectedItem.sku,
            total_refund_amount_cents: selectedItem.calculated_product_amount_cents,
            return_cause: reason.name,
            units: selectedItem.quantity,
            event_occurred_at: new Date()
          }
        }

        if (this.isGiftReturn) {
          params = { ...params, giftee_email: this.state_Email }
        }

        this.isLoading = true
        this.$store
          .dispatch('order/' + IS_TRUST_WORTHY_USER, params)
          .then((response) => {
            if (response) {
              this.$store.commit('exchange/setExchangeEligibility', response)
              this.$store.commit(
                'order/' + UPDATE_SELECTED_ITEM_EXCHANGE_ELIGIBILITY, {
                  identifier: selectedItem.identifier,
                  exchangeMetadata: response
                })
            }
          })
          .catch((err) => {
            console.error({ err })
          })
          .finally(() => {
            this.isTrustWorthyUserResponseCallback()
          })
      } else {
        this.isTrustWorthyUserResponseCallback()
      }
    },
    isTrustWorthyUserResponseCallback () {
      this.$router.push('/orders/' + this.order.order_hash + '/refund-amount')
      this.$store.commit('returns/updateStep', this.RETURN_REASON_STEP.ACTION)
      this.isLoading = false
    },
    returnTrustWorthyPayload (reason, selectedItem) {
      return this.selectedItems.map(selectedLocalItem => {
        let returnCause = null
        let refundedItem = null
        const currentItem = selectedItem.variant_identifier === selectedLocalItem.variant_identifier

        if (currentItem && reason) {
          returnCause = reason.name
        } else if (this.refundList.length) {
          refundedItem = this.refundList.find(item => {
            return item.variant_identifier === selectedLocalItem.variant_identifier
          })
        } else if (!refundedItem && this.exchangeList.length) {
          refundedItem = this.exchangeList.find(item => {
            return item.original_item.variant_identifier === selectedLocalItem.variant_identifier
          })
        } if (refundedItem?.reason) {
          returnCause = refundedItem.reason.name
        }

        return {
          sku: selectedLocalItem.sku,
          total_refund_amount_cents: selectedLocalItem.calculated_product_amount_cents,
          return_cause: returnCause,
          units: selectedLocalItem.quantity,
          event_occurred_at: new Date()
        }
      })
    },
    async feedbackSubmitted (event) {
      this.showReturnFeedback = false
      this.$store.commit('returns/updateStep', this.RETURN_REASON_STEP.ACTION)
      this.title = ''
    },
    async backButtonClicked () {
      switch (this.step) {
        case this.RETURN_REASON_STEP.PRIMARY:
          if (this.currentItemIndex === 0) {
            this.$router.push('/orders/' + this.order.order_hash)
          } else {
            this.$store.commit('returns/decrementIndex')
            this.title = ''
            this.$store.commit(
              'returns/updateStep',
              this.RETURN_REASON_STEP.ACTION
            )
          }
          break
        case this.RETURN_REASON_STEP.ACTION:
          this.title = this.WHY_ARE_YOU_RETURNING
          this.$store.commit(
            'returns/updateStep',
            this.RETURN_REASON_STEP.PRIMARY
          )
          this.$router.push('/orders/' + this.order.order_hash + '/reasons')
          break
        default:
          break
      }
    }
  }
}
</script>

<style scoped></style>
