import { CONSTANTS } from '@/constants'

export default {
  order (state) {
    return state.order
  },
  isLoading (state) {
    return state.isLoading
  },
  refundList (state) {
    return state.refundList
  },
  exchangeList (state) {
    return state.exchangeList
  },
  selectedItems (state) {
    return state.order.order.items
      ? state.order.order.items.filter((l) => l.selected)
      : []
  },
  isExchangeOrder (state) {
    return state.order.order?.is_exchange_order || CONSTANTS.ORDER_DEFAULTS.IS_EXCHANGE_ORDER
  },
  isFreeShipping (state) {
    return state.order.order?.is_free_shipping || CONSTANTS.ORDER_DEFAULTS.IS_FREE_SHIPPING
  },
  destinaitonDetails (state) {
    return state?.refundlist?.some(item => item.destination && item.destination.customer_keep) ||
      state?.exchangeList?.some(item => item.destination && item.destination.customer_keep)
  },
  rmaList (state) {
    return state.rmaList
  },
  policy (state) {
    return state.policy
  }
}
