import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/components/pages/Home.vue'
import CustomerKeepsView from '@/components/pages/CustomerKeepsView.vue'
import ZipCodeView from '@/components/pages/ZipCodeView.vue'
import EmailView from '@/components/pages/EmailView.vue'
import OrderIdView from '@/components/pages/OrderIdView.vue'
import OrderandZipCodeView from '@/components/pages/OrderandZipCodeView.vue'
import ReturnListView from '@/components/pages/ReturnListView.vue'
import HowItWorksView from '@/components/pages/HowItWorksView.vue'
import ReturnGiftView from '@/components/pages/ReturnGiftView.vue'
import ReturnReasonWrapper from '@/components/pages/ReturnReasonWrapper.vue'
import ReturnMethods from '@/components/pages/ReturnMethods.vue'
import OrdersView from '@/components/pages/orders.vue'
import Typography from '@/components/pages/Typography.vue'
import PDPPage from '@/components/pages/PDPPage.vue'
import ReviewView from '@/components/pages/ReviewView.vue'
import ConfirmationView from '@/components/pages/ConfirmationView.vue'
import PageNotFound from '@/components/pages/PageNotFound.vue'
import TrackerView from '@/components/pages/TrackerView.vue'
import Login from '@/components/pages/Login.vue'
import PrivacyPolicy from '@/components/pages/PrivacyPolicy.vue'
import TermsOfService from '@/components/pages/TermsOfService.vue'
import ReturnMethodsMap from '@/components/pages/ReturnMethodsMap.vue'
import { SEARCH, PAGE_TITLES } from 'branding/strings.json'
import { clearGiftEmail, clearSession } from '@/store/sessionStorage.js'
import { appLoginRequired } from '@/common/config'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {
      title: PAGE_TITLES.ORDER_LOOKUP
    }
  },
  {
    path: '/zipcode',
    name: 'zipcode',
    component: ZipCodeView,
    meta: {
      title: PAGE_TITLES.SHIPPING_ZIP_CODE
    }
  },
  {
    path: '/email',
    name: 'email',
    component: EmailView
  },
  {
    path: '/order',
    name: 'order',
    component: OrderIdView
  },
  {
    path: '/orderandzipcode',
    name: 'orderandzipcode',
    component: OrderandZipCodeView,
    meta: {
      title: PAGE_TITLES.ORDER_NOT_FOUND
    }
  },
  {
    path: '/orders/:id',
    component: OrdersView,
    children: [
      {
        path: '',
        component: ReturnListView,
        meta: {
          title: PAGE_TITLES.ITEM_SELECTION
        }
      },
      {
        path: 'reasons',
        name: 'reasons',
        component: ReturnReasonWrapper,
        meta: {
          title: PAGE_TITLES.RETURN_REASON
        }
      },
      {
        path: 'customer-keeps',
        name: 'customer-keeps',
        component: CustomerKeepsView
      },
      {
        path: 'refund-amount',
        name: 'refund-amount',
        component: ReturnReasonWrapper,
        meta: {
          title: '' // switches based off tabs
        }
      },
      {
        path: 'shipping',
        component: ReturnMethods,
        meta: {
          title: PAGE_TITLES.RETURN_METHOD
        }
      },
      {
        path: 'mapshipping',
        name: 'mapshipping',
        component: ReturnMethodsMap,
        meta: {
          title: PAGE_TITLES.RETURN_METHOD
        }
      },
      {
        path: 'review',
        component: ReviewView,
        meta: {
          title: PAGE_TITLES.REVIEW
        },
        props: true
      },
      {
        path: 'confirm/:identifier',
        component: ConfirmationView,
        meta: {
          title: PAGE_TITLES.CONFIRMATION
        },
        props: {
          header: true,
          content: true
        }
      },
      {
        path: 'tracker',
        component: TrackerView,
        meta: {
          title: PAGE_TITLES.TRACKER
        }
      },
      {
        path: 'tracker/:identifier',
        component: TrackerView,
        meta: {
          title: PAGE_TITLES.TRACKER
        }
      }
    ]
  },
  {
    path: '/howitworks',
    name: 'howitworks',
    component: HowItWorksView,
    meta: {
      title: PAGE_TITLES.HOW_IT_WORKS
    }
  },
  {
    path: '/returngift',
    name: 'returngift',
    component: ReturnGiftView,
    meta: {
      title: '' // TODO: update with new return gift flow
    }
  },
  {
    path: '/pdppage',
    name: 'pdppage',
    component: PDPPage,
    meta: {
      title: PAGE_TITLES.PDP_PAGE
    }
  },
  {
    path: '/Typography',
    name: 'Typography',
    component: Typography,
    meta: {
      title: PAGE_TITLES.TYPOGRAPHY
    }
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {
      title: PAGE_TITLES.LOG_IN
    }
  },
  {
    path: '/privacy-policy',
    name: 'privacy-policy',
    component: PrivacyPolicy,
    meta: {
      title: PAGE_TITLES.PRIVACY_POLICY
    }
  },
  {
    path: '/terms-of-service',
    name: 'terms-of-service',
    component: TermsOfService,
    meta: {
      title: PAGE_TITLES.TERMS_OF_SERVICE
    }
  },
  {
    path: '*',
    component: PageNotFound,
    meta: {
      title: PAGE_TITLES.PAGE_NOT_FOUND
    }
  }
]

const router = new VueRouter({
  mode: 'history',
  routes,
  scrollBehavior: (to, from, savedPosition) => {
    if (to.hash) return { selector: to.hash }
    if (savedPosition) return savedPosition
    return { x: 0, y: 0 }
  }
})

router.beforeEach((to, from, next) => {
  const isAuthenticated = localStorage.getItem('isAuthenticated')
  const meta = document.createElement('meta')
  meta.name = 'description'
  meta.content = SEARCH.DESCRIPTION
  document.getElementsByTagName('head')[0].appendChild(meta)
  document.title = to.meta.title

  if (to.name === 'Home') { // clear is_gift and email on home page
    clearSession()
  } else if (to.name === 'orderandzipcode') { // clear email
    clearGiftEmail()
  }

  if (
    appLoginRequired === 'true' &&
    isAuthenticated !== 'true' &&
    to.name !== 'login'
  ) {
    next({ name: 'login' })
  } else next()
})

export default router
