<template>
  <div>
    <div>
      <div
        :class="{
          'ml-6 mt-2': $vuetify.breakpoint.smAndDown,
          'ml-8 mt-8': $vuetify.breakpoint.mdAndUp,
        }"
        v-if="!this.isModal"
      >
        <back-button />
      </div>
      <div :class="{ 'mt-n12': $vuetify.breakpoint.mdAndUp }">
        <div
          style="max-width: 664px"
          :class="{
            'mx-6': $vuetify.breakpoint.smAndDown && !this.isModal,
            'mx-auto': $vuetify.breakpoint.mdAndUp,
          }"
        >
          <v-row
            class="mb-4"
            :class="{
              'mx-3': this.isModal,
              'justify-content-between': $vuetify.breakpoint.smAndDown,
            }"
            align="center"
          >
            <div class="mt-4">
              <header-title bold>
                {{ getPageTitle() }}
              </header-title>
              <div class="mt-4 d-flex align-center justify-content-between">
                <header-small-title>
                  {{ getReturnOrderNumber() }}
                </header-small-title>
                <action-button
                  tertiary-color
                  text
                  @click.stop="showReturnViewPage(true)"
                  class="px-0"
                  v-if="showTracker"
                >
                  {{ getReturnDetailsButton() }}
                </action-button>
              </div>
              <o-dialog
                :visible="showReturnDetails"
                is-full-screen
                @close="showReturnViewPage(false)"
              >
                <ReviewView
                  is-modal
                  :show-confirm-action-view="false"
                  :tracker="true"
                  :title="this.RETURN_DETAILS_TITLE"
                  :sub-title="getReturnOrderNumber()"
                  show-order-details
                  :return-date="getReturnDate()"
                  :is-selected-rma-free-shipping="this.getSelectedReturnItem.is_rma_free_shipping"
                  :is-gift="this.getSelectedReturnItem.is_gift"
                />
              </o-dialog>
            </div>
          </v-row>
          <div v-if="showTracker">
            <div :class="{ 'mx-n12': $vuetify.breakpoint.smAndDown }">
              <v-stepper alt-labels elevation="0">
                <v-stepper-header>
                  <v-stepper-step
                    step=""
                    :class="{
                      'complete': getPastCompleteStep(this.RETURN_STATUS.STARTED)
                    }"
                    :complete="stepComplete(this.RETURN_STATUS.STARTED)"
                    :color="stepStatus(this.RETURN_STATUS.STARTED)"
                  >
                    {{ getStepperlabel(this.RETURN_STATUS.STARTED) }}
                  </v-stepper-step>

                  <v-divider
                    :class="{
                      'dashedDivider': !stepComplete(this.RETURN_STATUS.IN_TRANSIT)
                    }"
                    v-if="this.showInTransit()"
                  />
                  <v-stepper-step
                    step=""
                    :class="{
                      'complete': getPastCompleteStep(this.RETURN_STATUS.IN_TRANSIT)
                    }"
                    :complete="stepComplete(this.RETURN_STATUS.IN_TRANSIT)"
                    :color="stepStatus(this.RETURN_STATUS.IN_TRANSIT)"
                    v-if="this.showInTransit()"
                  >
                    {{ getStepperlabel(this.RETURN_STATUS.IN_TRANSIT) }}
                  </v-stepper-step>

                  <v-divider
                    :class="{
                      'dashedDivider': !stepComplete(this.RETURN_STATUS.RECEIVED)
                    }"
                    v-if="
                      this.getSelectedReturnItem.return_method &&
                        this.getSelectedReturnItem.return_method.type !==
                        IN_STORE
                    "
                  />
                  <v-stepper-step
                    step=""
                    :class="{
                      'complete': getPastCompleteStep(this.RETURN_STATUS.RECEIVED)
                    }"
                    :complete="stepComplete(this.RETURN_STATUS.RECEIVED)"
                    :color="stepStatus(this.RETURN_STATUS.RECEIVED)"
                    v-if="
                      this.getSelectedReturnItem.return_method &&
                        this.getSelectedReturnItem.return_method.type !==
                        IN_STORE
                    "
                  >
                    {{ getStepperlabel(this.RETURN_STATUS.RECEIVED) }}
                  </v-stepper-step>

                  <v-divider
                    :class="{
                      'dashedDivider': !stepComplete(this.RETURN_STATUS.COMPLETED)
                    }"
                  />
                  <v-stepper-step
                    :class="{
                      'complete': getPastCompleteStep(this.RETURN_STATUS.COMPLETED)
                    }"
                    :complete="stepComplete(this.RETURN_STATUS.COMPLETED)"
                    :color="stepStatus(this.RETURN_STATUS.COMPLETED)"
                    step=""
                  >
                    {{ getStepperlabel(this.RETURN_STATUS.COMPLETED) }}
                  </v-stepper-step>
                </v-stepper-header>
              </v-stepper>
            </div>
            <o-text
              lg
              bold
              class="mt-4"
            >
              {{ getReturnMethodTitle() }}
            </o-text>
            <o-text m class="mt-3">
              {{ getReturnMethodSubTitle() }}
            </o-text>
            <div class="d-flex mt-6 justify-start justify-space-between">
              <action-button
                primary-color
                v-if="shouldShowViewQRCodeButton()"
                @click.stop="toggleShowReturnCodes(true)"
              >
                {{ VIEW_QR_CODE }}
              </action-button>
              <o-dialog
                :visible="showReturnCodes"
                is-full-screen
                @close="toggleShowReturnCodes(false)"
              >
                <QRCodeView
                  v-if="
                    this.selectedReturnMethod &&
                      this.selectedReturnMethod.type !== MAIL_BACK
                  "
                  :qr-data="this.getSelectedReturnItem.express_details"
                  :from-return-methods="false"
                  :is-exchange-or-refund="this.isExchangeOrRefund"
                />
              </o-dialog>
              <action-button
                @click.stop="openReturnLabelLink"
                primary-color
                v-if="shouldShowDownloadShippingLabelButton()"
              >
                {{ DOWNLOAD_SHIPPING_LABEL }}
              </action-button>
              <action-button
                tertiary-color
                text
                :block="
                  $vuetify.breakpoint.smAndDown &&
                    this.selectedReturnMethod.type === IN_STORE
                "
                @click="
                  openModal({
                    type: DIALOG_CARRIER_LOCATIONS,
                    selectedCarrier: carrier,
                  })
                "
                v-if="shouldShowLocationsButton()"
              >
                {{ SEE_LOCATIONS }}
              </action-button>
              <o-dialog
                :visible="isModalOpen"
                is-full-screen
                @close="closeModal"
              >
                <return-locations
                  v-if="activeModal === DIALOG_CARRIER_LOCATIONS"
                  :show-select-locations="false"
                  :title="EXPLORE_LOCATIONS"
                  :return-type="getReturnMethodType()"
                  :from-return-methods="false"
                />
                <return-store-info
                  v-if="activeModal === DIALOG_CARRIER_INFORMATION"
                  is-rma-submitted
                />
                <search-return-location
                  v-if="activeModal === DIALOG_SEARCH_LOCATION"
                  source="trackerView"
                />
              </o-dialog>
              <action-button
                tertiary-color
                text
                :block="$vuetify.breakpoint.smAndDown"
                v-if="shouldShowTrackShippmentButton()"
                @click.stop="openTrackShipmentLabelLink"
              >
                Track Shipment
              </action-button>
            </div>
          </div>
          <div v-else>
            <div v-if="this.currentStep === this.RETURN_STATUS.CANCELED">
              <o-text
                lg
                bold
                class="mt-4"
              >
                {{ this.CANCELED_INSTRUCTIONS_TITLE }}
              </o-text>
              <o-text m class="mt-3">
                {{ this.canceledInstructions }}
              </o-text>
            </div>
            <div v-else>
              <o-text
                lg
                bold
                class="mt-4"
              >
                {{ this.NOT_FOUND_INSTRUCTIONS_TITLE }}
              </o-text>
              <o-text m class="mt-3">
                {{ this.notFoundInstructions }}
              </o-text>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import dayjs from 'dayjs'
import OText from '@/components/atoms/OText.vue'
import HeaderTitle from '@/components/molecules/HeaderTitle.vue'
import {
  TRACKING_VIEW,
  RETURN_REVIEW,
  CONFIRMATION_VIEW,
  RETURN_METHOD_DIALOGS,
  PAGE_TITLES
} from 'branding/strings.json'
import BackButton from '@/components/atoms/BackButton.vue'
import ActionButton from '@/components/atoms/ActionButton.vue'
import { mapGetters, mapMutations } from 'vuex'
import ReturnLocations from '@/components/organism/ReturnLocations.vue'
import { getFormattedDate, returnFormattedOrderNumber } from '@/utils'
import ODialog from '@/components/atoms/ODialog.vue'
import ReviewView from '@/components/pages/ReviewView.vue'
import QRCodeView from '@/components/pages/QRCodeView.vue'
import { CONSTANTS } from '@/constants/index.js'
import {
  DIALOG_CARRIER_INFORMATION,
  DIALOG_CARRIER_LOCATIONS,
  DIALOG_SEARCH_LOCATION
} from '@/store/constants'
import ReturnStoreInfo from '@/components/organism/ReturnStoreInfo.vue'
import SearchReturnLocation from '@/components/organism/SearchReturnLocation.vue'
import {
  FETCH_ORDER,
  UPDATE_REFUND_LIST_FOR_RETURN_USER,
  UPDATE_EXCHANGE_LIST_FOR_RETURN_USER
} from '@/store/constants.js'
import { isExchangeOrRefundRMAItems } from '@/controllers/OrdersController.js'
import { isMailBackReturn } from '@/controllers/ReturnMethodsController.js'

export default {
  name: 'TrackerView',

  components: {
    OText,
    HeaderTitle,
    BackButton,
    ActionButton,
    ReturnLocations,
    ODialog,
    ReviewView,
    QRCodeView,
    ReturnStoreInfo,
    SearchReturnLocation
  },

  props: {
    isModal: {
      type: Boolean,
      required: false
    },
    showConfirmActionView: {
      type: Boolean,
      required: false,
      default: true
    },
    title: {
      type: String,
      required: false,
      default: 'Review your return'
    },
    showOrderDetails: {
      type: Boolean,
      required: false,
      default: false
    },
    returnDate: {
      type: String,
      required: false,
      default: ''
    }
  },
  computed: {
    ...mapGetters({
      order: 'order/order',
      refundList: 'order/refundList',
      policies: 'returns/policies',
      rmaList: 'order/rmaList',
      selectedReturnMethod: 'returns/getSelectedReturnMethod',
      getSelectedReturnItem: 'returns/getSelectedReturnItem',
      isModalOpen: 'returns/getIsModalOpen',
      activeModal: 'returns/getActiveModal',
      carrier: 'returns/getSelectedCarrier',
      dropOffWindow: 'returns/returnDropOffWindow'
    }),
    isExchangeOrRefund () {
      return isExchangeOrRefundRMAItems()
    },
    isMailBack () {
      return isMailBackReturn(this.selectedReturnMethod)
    }
  },
  data () {
    return {
      DIALOG_CARRIER_INFORMATION,
      DIALOG_CARRIER_LOCATIONS,
      DIALOG_SEARCH_LOCATION,
      MAIL_BACK: CONSTANTS.RETURN_METHODS.MAIL_BACK,
      IN_STORE: CONSTANTS.RETURN_METHODS.IN_STORE,
      EXPRESS_RETURNS: CONSTANTS.RETURN_METHODS.EXPRESS_RETURNS,
      TRACKING_VIEW_TITLE: TRACKING_VIEW.TRACKING_VIEW_TITLE,
      STARTED: TRACKING_VIEW.STARTED,
      IN_TRANSIT: TRACKING_VIEW.IN_TRANSIT,
      RECEIVED: TRACKING_VIEW.RECEIVED,
      COMPLETED: TRACKING_VIEW.COMPLETED,
      SEE_LOCATIONS: CONFIRMATION_VIEW.SEE_LOCATIONS,
      VIEW_RETURN_DETAILS_BUTTON: CONFIRMATION_VIEW.VIEW_RETURN_DETAILS_BUTTON,
      VIEW_RETURN_DETAILS_BUTTON_EXCHANGE: CONFIRMATION_VIEW.VIEW_RETURN_DETAILS_BUTTON_EXCHANGE,
      VIEW_RETURN_CODES_BUTTON: CONFIRMATION_VIEW.VIEW_RETURN_CODES_BUTTON,
      DOWNLOAD_SHIPPING_LABEL: CONFIRMATION_VIEW.DOWNLOAD_SHIPPING_LABEL,
      VIEW_QR_CODE: CONFIRMATION_VIEW.VIEW_QR_CODE,
      PAGE_TITLE_STEP1: TRACKING_VIEW.PAGE_TITLE_STEP1,
      PAGE_TITLE_STEP2: TRACKING_VIEW.PAGE_TITLE_STEP2,
      PAGE_TITLE_STEP3: TRACKING_VIEW.PAGE_TITLE_STEP3,
      PAGE_TITLE_STEP4: TRACKING_VIEW.PAGE_TITLE_STEP4,
      MAIL_BACK_TITLE_STEP1: TRACKING_VIEW.MAIL_BACK_TITLE_STEP1,
      MAIL_BACK_TITLE_STEP2: TRACKING_VIEW.MAIL_BACK_TITLE_STEP2,
      MAIL_BACK_TITLE_STEP3: TRACKING_VIEW.MAIL_BACK_TITLE_STEP3,
      MAIL_BACK_SUB_TITLE_STEP1: TRACKING_VIEW.MAIL_BACK_SUB_TITLE_STEP1,
      MAIL_BACK_SUB_TITLE_STEP2: TRACKING_VIEW.MAIL_BACK_SUB_TITLE_STEP2,
      MAIL_BACK_SUB_TITLE_STEP3: TRACKING_VIEW.MAIL_BACK_SUB_TITLE_STEP3,
      MAIL_BACK_SUB_TITLE_STEP4: TRACKING_VIEW.MAIL_BACK_SUB_TITLE_STEP4,
      DROP_OFF_SUB_TITLE_STEP1: TRACKING_VIEW.DROP_OFF_SUB_TITLE_STEP1,
      TRACKING_COMPLETE_STEP_TITLE: TRACKING_VIEW.TRACKING_COMPLETE_STEP_TITLE,
      TRACKING_COMPLETE_STEP_SUB_TITLE:
        TRACKING_VIEW.TRACKING_COMPLETE_STEP_SUB_TITLE,
      EXPRESS_RETURN_SUB_TITLE_STEP1:
        TRACKING_VIEW.EXPRESS_RETURN_SUB_TITLE_STEP1,
      CANCELED_INSTRUCTIONS_TITLE: TRACKING_VIEW.CANCELED_INSTRUCTIONS_TITLE,
      CANCELED_INSTRUCTIONS_DESCRIPTION:
        TRACKING_VIEW.CANCELED_INSTRUCTIONS_DESCRIPTION,
      NOT_FOUND_INSTRUCTIONS_TITLE: TRACKING_VIEW.NOT_FOUND_INSTRUCTIONS_TITLE,
      NOT_FOUND_INSTRUCTIONS_DESCRIPTION:
        TRACKING_VIEW.NOT_FOUND_INSTRUCTIONS_DESCRIPTION,
      SEE_RETURN_LOCATIONS: RETURN_METHOD_DIALOGS.SEE_RETURN_LOCATIONS,
      RETURN_STATUS: CONSTANTS.RETURN_STATUS,
      RETURN_DETAILS_TITLE: RETURN_REVIEW.RETURN_DETAILS_TITLE,
      RETURN_DETAILS_DATE_PREFIX: RETURN_REVIEW.RETURN_DETAILS_DATE_PREFIX,
      el: 1,
      showReturnDetails: false,
      currentStep: CONSTANTS.RETURN_STATUS.STARTED,
      showReturnCodes: false,
      EXPLORE_LOCATIONS: RETURN_METHOD_DIALOGS.EXPLORE_LOCATIONS,
      showTracker: false,
      canceledInstructions: '',
      notFoundInstructions: ''
    }
  },

  mounted () {
    if (this.order.order_hash === '') {
      this.getData()
    } else {
      this.configureTracker(this.getSelectedReturnItem.return_status)
    }
  },

  methods: {
    ...mapMutations({
      openModal: 'returns/openModal',
      closeModal: 'returns/closeModal',
      setSelectedReturnMethod: 'returns/setSelectedReturnMethod'
    }),
    async getData () {
      const hash = this.$route.params.id
      const identifierFromUrl = this.$route.params.identifier
      await this.$store
        .dispatch('order/' + FETCH_ORDER, { order_hash: hash })
        .then((response) => {
          if (response) {
            const selectedRMA = this.rmaList?.rmaListItems?.find(
              (rma) => rma.identifier === identifierFromUrl
            )
            if (selectedRMA) {
              this.$store.commit(
                'returns/updateSelectedReturnItem',
                selectedRMA
              )
              const refundList = []
              const exchangeList = []

              selectedRMA.rma_items.map((item) => {
                if (!item?.is_exchange) {
                  refundList.push(item.order_item)
                } else {
                  exchangeList.push(item.exchange_item)
                }
              })
              this.$store.commit(
                'order/' + UPDATE_REFUND_LIST_FOR_RETURN_USER,
                refundList
              )
              this.$store.commit(
                'order/' + UPDATE_EXCHANGE_LIST_FOR_RETURN_USER,
                exchangeList
              )

              this.setSelectedReturnMethod(selectedRMA.return_method)
              this.configureTracker(this.getSelectedReturnItem.return_status)
            } else {
              this.notFoundInstructions = this.NOT_FOUND_INSTRUCTIONS_DESCRIPTION.replace(
                '$$email',
                this.policies.retailer_info.customer_support_email
              ).replace(
                '$$phone',
                this.policies.retailer_info.customer_support_contact
              )
            }
          }
        })
        .catch((err) => {
          this.$router.push('/orders/' + hash)
          console.error({ err })
        })
    },
    configureTracker (currentStatus) {
      this.updateDescription()
      this.currentStep = currentStatus
      this.showTracker = this.currentStep !== this.RETURN_STATUS.CANCELED
      if (!this.showTracker && this.currentStep !== this.RETURN_STATUS.CANCELED) {
        document.title = PAGE_TITLES.NO_RETURN_FOUND
      }
    },
    getReturnOrderNumber () {
      return this.order ? returnFormattedOrderNumber(this.order.order_id) : ''
    },

    toggleShowReturnCodes (opening) {
      if (opening) {
        this.showReturnCodes = true
        document.title = PAGE_TITLES.NEXT_STEPS
      } else {
        this.showReturnCodes = false
        document.title = PAGE_TITLES.TRACKER
      }
    },

    stepComplete (step) {
      var isComplete = false

      if (this.currentStep === this.RETURN_STATUS.STARTED) {
        if (step === this.RETURN_STATUS.STARTED) {
          isComplete = true
        }
      } else if (this.currentStep === this.RETURN_STATUS.IN_TRANSIT) {
        if (
          step === this.RETURN_STATUS.STARTED ||
          step === this.RETURN_STATUS.IN_TRANSIT
        ) {
          isComplete = true
        }
      } else if (this.currentStep === this.RETURN_STATUS.RECEIVED) {
        if (
          step === this.RETURN_STATUS.STARTED ||
          step === this.RETURN_STATUS.IN_TRANSIT ||
          step === this.RETURN_STATUS.RECEIVED
        ) {
          isComplete = true
        }
      } else if (this.currentStep === this.RETURN_STATUS.COMPLETED) {
        if (
          step === this.RETURN_STATUS.STARTED ||
          step === this.RETURN_STATUS.IN_TRANSIT ||
          step === this.RETURN_STATUS.RECEIVED ||
          step === this.RETURN_STATUS.COMPLETED
        ) {
          isComplete = true
        }
      }
      return isComplete
    },

    getReturnDetailsButton () {
      return this.isExchangeOrRefund === CONSTANTS.EXCHANGE ? this.VIEW_RETURN_DETAILS_BUTTON_EXCHANGE : this.VIEW_RETURN_DETAILS_BUTTON
    },

    stepStatus (step) {
      return this.currentStep === step ? '#333333' : '#dddddd'
    },

    getStepperlabel (step) {
      var title = ''
      if (step === this.RETURN_STATUS.STARTED) {
        title = this.STARTED
      } else if (step === this.RETURN_STATUS.IN_TRANSIT) {
        title = this.IN_TRANSIT
      } else if (step === this.RETURN_STATUS.RECEIVED) {
        title = this.RECEIVED
      } else if (step === this.RETURN_STATUS.COMPLETED) {
        title = this.COMPLETED
      }
      return title
    },

    getPageTitle: function () {
      var title = this.PAGE_TITLE_STEP1
      if (this.currentStep === this.RETURN_STATUS.STARTED) {
        title =
          this.isExchangeOrRefund === CONSTANTS.EXCHANGE
            ? TRACKING_VIEW.PAGE_TITLE_STEP1_EXCHANGE
            : this.PAGE_TITLE_STEP1
      } else if (this.currentStep === this.RETURN_STATUS.IN_TRANSIT) {
        title = this.PAGE_TITLE_STEP2
      } else if (this.currentStep === this.RETURN_STATUS.RECEIVED) {
        title = this.PAGE_TITLE_STEP3
      } else if (this.currentStep === this.RETURN_STATUS.COMPLETED) {
        title = this.PAGE_TITLE_STEP4
      }
      return title
    },

    getReturnMethodTitle: function () {
      var title = ''
      const returnMethod = this.getSelectedReturnItem.return_method
      if (this.currentStep === this.RETURN_STATUS.STARTED) {
        if (
          returnMethod &&
          (returnMethod.type === this.IN_STORE ||
            returnMethod.type === this.EXPRESS_RETURNS)
        ) {
          title = TRACKING_VIEW.DROP_OFF_RETURN_METHOD_TITLE
        } else if (returnMethod && returnMethod.type === this.MAIL_BACK) {
          title = TRACKING_VIEW.MAIL_BACK_RETURN_METHOD_TITLE
        }
        if (returnMethod) {
          title = title
            .replace('$$returnMethodName', returnMethod.customer_facing_carrier)
            .replace(
              '$$returnByDate',
              dayjs(this.getSelectedReturnItem.created_at)
                .add(this.dropOffWindow, 'day')
                .format('MMM DD')
            )
        }
      } else if (this.currentStep === this.RETURN_STATUS.IN_TRANSIT) {
        title = this.MAIL_BACK_TITLE_STEP2
      } else if (this.currentStep === this.RETURN_STATUS.RECEIVED) {
        title = this.MAIL_BACK_TITLE_STEP3
      } else if (this.currentStep === this.RETURN_STATUS.RECEIVED && this.isExchangeOrRefund === CONSTANTS.REFUND) {
        title = this.MAIL_BACK_TITLE_STEP4
      } else if (this.currentStep === this.RETURN_STATUS.COMPLETED) {
        title = this.TRACKING_COMPLETE_STEP_TITLE
      }
      return title
    },

    showInTransit () {
      const isMailBack =
        this.getSelectedReturnItem?.return_method?.type === this.MAIL_BACK
      const isExpressReturns =
        this.getSelectedReturnItem?.return_method?.type ===
        this.EXPRESS_RETURNS
      var showTransit = isMailBack || isExpressReturns
      if (
        this.policies?.refund_trigger_status.length > 0 &&
        ((isMailBack &&
          this.policies?.refund_trigger_status?.includes('PRE_TRANSIT')) ||
          (isExpressReturns &&
            this.policies?.refund_trigger_status?.includes('ACCEPTED')))
      ) {
        showTransit = false
      }
      return showTransit
    },

    getReturnMethodSubTitle: function () {
      var title = ''
      const returnMethod = this.getSelectedReturnItem.return_method
      const refundTimingEstimate = this.policies.refund_timing_estimate
        ? this.policies.refund_timing_estimate
        : '5-7'

      if (returnMethod && returnMethod.type === this.IN_STORE) {
        if (this.currentStep === this.RETURN_STATUS.STARTED) {
          title = this.DROP_OFF_SUB_TITLE_STEP1
        }
      } else if (returnMethod && returnMethod.type === this.EXPRESS_RETURNS) {
        if (this.currentStep === this.RETURN_STATUS.STARTED) {
          if (this.isExchangeOrRefund === CONSTANTS.EXCHANGE) {
            title = TRACKING_VIEW.EXPRESS_RETURN_SUB_TITLE_STEP1_EXCHANGE.replace(
              '$$CARRIER',
              returnMethod.customer_facing_carrier
            )
          } else if (this.isExchangeOrRefund === CONSTANTS.REFUND) {
            title = TRACKING_VIEW.EXPRESS_RETURN_SUB_TITLE_STEP1.replace(
              '$$refundTimingEstimate',
              refundTimingEstimate
            ).replace('$$CARRIER', returnMethod?.customer_facing_carrier)
          } else {
            title = TRACKING_VIEW.EXPRESS_RETURN_SUB_TITLE_STEP1_EXCHANGE_REFUND.replace(
              '$$CARRIER',
              returnMethod.customer_facing_carrier
            ).replace(
              '$$refundTimingEstimate',
              refundTimingEstimate
            )
          }
        }
      } else if (returnMethod && returnMethod.type === this.MAIL_BACK) {
        if (this.currentStep === this.RETURN_STATUS.STARTED) {
          if (this.isExchangeOrRefund === CONSTANTS.EXCHANGE) {
            title = TRACKING_VIEW.MAIL_BACK_SUB_TITLE_STEP1_EXCHANGE.replace(
              '$$mailBack',
              returnMethod.customer_facing_carrier
            )
          } else if (this.isExchangeOrRefund === CONSTANTS.REFUND) {
            title = TRACKING_VIEW.MAIL_BACK_SUB_TITLE_STEP1.replace(
              '$$refundTimingEstimate',
              refundTimingEstimate
            ).replace('$$mailBack', returnMethod?.customer_facing_carrier)
          } else {
            title = TRACKING_VIEW.MAIL_BACK_SUB_TITLE_STEP1_EXCHANGE_REFUND.replace(
              '$$refundTimingEstimate',
              refundTimingEstimate
            ).replace('$$mailBack', returnMethod?.customer_facing_carrier)
          }
        }
      }

      if (this.currentStep === this.RETURN_STATUS.IN_TRANSIT) {
        title = this.MAIL_BACK_SUB_TITLE_STEP2
      } else if (this.currentStep === this.RETURN_STATUS.RECEIVED) {
        title = this.MAIL_BACK_SUB_TITLE_STEP3 + (this.isExchangeOrRefund === CONSTANTS.EXCHANGE_AND_REFUND ? ' ' + this.MAIL_BACK_SUB_TITLE_STEP4 : '')
      } else if (this.currentStep === this.RETURN_STATUS.COMPLETED) {
        title = this.TRACKING_COMPLETE_STEP_SUB_TITLE
      }
      return title
    },
    getReturnDate () {
      return (
        this.RETURN_DETAILS_DATE_PREFIX +
        getFormattedDate(this.getSelectedReturnItem.created_at)
      )
    },
    shouldShowLocationsButton () {
      return this.currentStep === this.RETURN_STATUS.STARTED && !this.isMailBack
    },
    shouldShowViewQRCodeButton () {
      const returnMethod = this.getSelectedReturnItem.return_method
      return (
        returnMethod &&
        returnMethod.type === this.EXPRESS_RETURNS &&
        this.currentStep === this.RETURN_STATUS.STARTED
      )
    },
    shouldShowDownloadShippingLabelButton () {
      const returnMethod = this.getSelectedReturnItem.return_method
      return (
        returnMethod &&
        returnMethod.type === this.MAIL_BACK &&
        this.currentStep === this.RETURN_STATUS.STARTED
      )
    },
    shouldShowTrackShippmentButton () {
      const returnMethod = this.getSelectedReturnItem.return_method
      return (
        returnMethod &&
        returnMethod.type === this.MAIL_BACK &&
        this.currentStep === this.RETURN_STATUS.IN_TRANSIT
      )
    },
    openReturnLabelLink () {
      const labelUrl = this.getSelectedReturnItem?.shipping_label_details
        ?.label_url
      if (labelUrl) {
        window.open(labelUrl)
      }
    },
    openTrackShipmentLabelLink () {
      const labelUrl = this.getSelectedReturnItem?.shipping_label_details
        ?.tracking_url
      if (labelUrl) {
        window.open(labelUrl)
      }
    },
    showReturnViewPage (opening) {
      if (opening) {
        this.showReturnDetails = true
        document.title = PAGE_TITLES.REVIEW
      } else {
        this.showReturnDetails = false
        document.title = PAGE_TITLES.TRACKER
      }
    },
    getReturnMethodType: function () {
      return this.getSelectedReturnItem?.return_method?.type
    },
    getPastCompleteStep (step) {
      return this.stepComplete(step) && this.currentStep !== step
    },
    updateDescription () {
      const refundTimingEstimate = this.policies.refund_timing_estimate
        ? this.policies.refund_timing_estimate
        : '5-7'
      this.MAIL_BACK_SUB_TITLE_STEP1 = TRACKING_VIEW.MAIL_BACK_SUB_TITLE_STEP1.replace(
        '$$refundTimingEstimate',
        refundTimingEstimate
      ).replace(
        '$$mailBack',
        this.selectedReturnMethod?.customer_facing_carrier
      )
      this.MAIL_BACK_SUB_TITLE_STEP2 = TRACKING_VIEW.MAIL_BACK_SUB_TITLE_STEP2.replace(
        '$$refundTimingEstimate',
        refundTimingEstimate
      )
      this.TRACKING_COMPLETE_STEP_SUB_TITLE = TRACKING_VIEW.TRACKING_COMPLETE_STEP_SUB_TITLE.replace(
        '$$refundTimingEstimate',
        refundTimingEstimate
      )
      this.DROP_OFF_SUB_TITLE_STEP1 = TRACKING_VIEW.DROP_OFF_SUB_TITLE_STEP1.replace(
        '$$refundTimingEstimate',
        refundTimingEstimate
      )
      this.EXPRESS_RETURN_SUB_TITLE_STEP1 = TRACKING_VIEW.EXPRESS_RETURN_SUB_TITLE_STEP1.replace(
        '$$refundTimingEstimate',
        refundTimingEstimate
      )
      this.canceledInstructions = this.CANCELED_INSTRUCTIONS_DESCRIPTION.replace(
        '$$email',
        this.policies?.retailer_info?.customer_support_email
      ).replace(
        '$$phone',
        this.policies?.retailer_info?.customer_support_contact
      )
    }
  }
}
</script>

