<template>
  <div
    class="px-4"
    :class="{
      'bg-well': index === 0,
      'white': index !== 0,
      'mr-2 ml-2 map-tile-sm': $vuetify.breakpoint.smAndDown,
      'mr-1 map-tile-lg': $vuetify.breakpoint.mdAndUp,
    }"
    @click="tileClicked(index, true)"
  >
    <div class="d-flex align-center justify-content-between pt-2">
      <o-text
        text-primary
        bold
        :md="$vuetify.breakpoint.smAndDown"
        :lg="$vuetify.breakpoint.mdAndUp"
        class="pt-2"
      >
        {{ carrier.customer_facing_carrier || 'N/A' }}
      </o-text>
      <o-text
        text-secondary
        base
        bold
      >
        {{ this.getShippingAmtText() }}
      </o-text>
    </div>
    <div class="mt-1">
      <section class="d-flex pb-4">
        <o-text
          :sm="$vuetify.breakpoint.mdAndDown"
          :md="$vuetify.breakpoint.lgAndUp"
          text-secondary
          class="mb-0"
        >
          {{ addressShown || 'N/A' }}
        </o-text>
      </section>
      <section class="d-flex justify-space-between pb-4 mb-2">
        <div>
          <div v-if="carrier.openingHours.isOpen !== undefined" class="d-flex align-center">
            <v-icon small class="mr-2">
              schedule
            </v-icon>
            <o-text sm>
              {{ carrier.openingHours.weeklyRange.dateRange || 'N/A' }}
              {{ carrier.openingHours.weeklyRange.timeRange || 'N/A' }}
            </o-text>
          </div>
          <div v-if="SUSTAINABLE" class="d-flex align-center">
            <v-icon small class="mr-2">
              spa
            </v-icon>
            <o-text sm>
              {{ SUSTAINABLE }}
            </o-text>
          </div>
        </div>
        <action-button
          @click="handleCarrierSelection(carrier)"
          primary-color
          large
        >
          Choose
        </action-button>
      </section>
    </div>
    <v-divider
      class="my-0 map-tile-divider"
      v-if="$vuetify.breakpoint.mdAndUp"
    />
  </div>
</template>

<script>
import ActionButton from '@/components/atoms/ActionButton.vue'
import OText from '@/components/atoms/OText.vue'
import { RETURN_METHOD_SCREENS } from 'branding/strings.json'
import { DIALOG_CARRIER_INFORMATION, DIALOG_CARRIER_LOCATIONS } from '@/store/constants'
import { getShippingAmt } from '@/controllers/ReturnMethodsController.js'

export default {
  name: 'ShippingLocationCard',
  components: {
    ActionButton,
    OText
  },
  mounted () {},
  data () {
    return ({
      PACKAGE_AND_PRINT: RETURN_METHOD_SCREENS.PACKAGE_AND_PRINT,
      NO_PACKAGE_AND_PRINT: RETURN_METHOD_SCREENS.NO_PACKAGE_AND_PRINT,
      DIALOG_CARRIER_INFORMATION,
      DIALOG_CARRIER_LOCATIONS
    })
  },
  computed: {
    SUSTAINABLE () {
      return this.carrier?.isSustainable ? RETURN_METHOD_SCREENS.SUSTAINABLE : undefined
    },
    addressShown () {
      const splitAddress = this.carrier?.formattedAddress ? this.carrier?.formattedAddress.split(',') : []
      if (splitAddress.length < 2) {
        return undefined
      }
      return splitAddress[0] + ', ' + splitAddress[1]
    }
  },
  methods: {
    getShippingAmtText () {
      return getShippingAmt(this.carrier)
    }
  },
  props: {
    carrier: {
      type: Object,
      required: true
    },
    handleCarrierSelection: {
      type: Function,
      required: true
    },
    index: {
      type: Number,
      required: true
    },
    tileClicked: {
      type: Function,
      required: true
    }
  }
}
</script>

<style scoped>
  .map-tile-divider {
    border-color: grey; width: 100%;
  }
  .map-tile-lg {
    width: 100%;
  }
  .map-tile-sm {
    width: 375px;
    box-shadow: 2px 2px 4px #d8d8d8;
  }
</style>
