<template>
  <div>
    <v-overlay
      :value="isLoading"
      :z-index="1"
    >
      <v-progress-circular
        class="loader"
        indeterminate
        size="50"
      />
    </v-overlay>
    <o-shipping-address
      v-if="!isLoading"
      :dialog-type="DIALOG_SEARCH_LOCATION"
      :formatted-address="formattedAddress"
      :open-map-modal="openMapModal"
      :class="{
        'ml-4 mt-3': $vuetify.breakpoint.smAndDown,
        'mt-5': $vuetify.breakpoint.mdAndUp
      }"
      class="mb-5 shipping-address"
    />
    <div :class="$vuetify.breakpoint.smAndDown ? 'map-view-sm' : 'map-view-lg'">
      <div id="map" :class="$vuetify.breakpoint.smAndDown ? 'map-sm' : 'map-lg'" />
      <not-found-return-method
        v-if="placesToCoords.length === 0 && !isLoading"
        :class="$vuetify.breakpoint.smAndDown ? 'text-center' : 'text-center mr-8'"
        :style="$vuetify.breakpoint.smAndDown ? '' : 'width: 50%'"
        :title="noDropoffLocationsFound"
        :customer-support-contact="customerSupportContact"
      />
      <map-tiles
        v-else-if="!isLoading && placesToCoords.length !== 0"
        :carriers="placesToCoords"
        :class="$vuetify.breakpoint.smAndDown ? 'map-tiles-sm' : 'map-tiles-lg'"
        ref="map-tiles"
        :scroll-to-view="scrollToView"
      />
      <v-col v-if="isLoading" :style="$vuetify.breakpoint.smAndDown ? 'display: flex;' : ''">
        <v-skeleton-loader
          v-for="n in skeletonLoadingAmount"
          :key="n"
          :class="$vuetify.breakpoint.mdAndUp ? '' : 'mt-3'"
          type="list-item-two-line"
          :style="$vuetify.breakpoint.mdAndUp ? 'width:90%; background: #f2f5ff;' : 'width:90%; height: 195px; background: #f2f5ff;'"
        />
      </v-col>
    </div>
    <o-dialog
      :visible="
        isMapModalOpen"
      :is-full-screen="true"
      @close="closeMapModal"
    >
      <search-return-location source="mapView" />
    </o-dialog>
    <o-dialog
      :visible="isCarrierInfoModalOpen"
      :is-full-screen="true"
      @close="closeCarrierInfoModal"
    >
      <return-store-info :disable-back-button="true" />
    </o-dialog>
  </div>
</template>

<script>
import { findAllExpressReturnLocations } from '@/controllers/MapController.js'
import { mapGetters, mapMutations } from 'vuex'
import { CONSTANTS } from '@/constants/index.js'
import NotFoundReturnMethod from '@/components/molecules/NotFoundReturnMethod'
import {
  DIALOG_SEARCH_LOCATION
} from '@/store/constants'
import OShippingAddress from '@/components/atoms/OShippingAddress.vue'
import ODialog from '@/components/atoms/ODialog.vue'
import SearchReturnLocation from '@/components/organism/SearchReturnLocation.vue'
import MapTiles from '@/components/organism/MapTiles.vue'
import { RETURN_METHOD_SCREENS } from 'branding/strings.json'
import ReturnStoreInfo from '@/components/organism/ReturnStoreInfo.vue'

export default {
  /* eslint-disable no-new */
  /* eslint-disable no-undef */
  name: 'ExpressReturnMethod',
  components: { OShippingAddress, ODialog, SearchReturnLocation, MapTiles, ReturnStoreInfo, NotFoundReturnMethod },
  data () {
    return {
      shippingAddress: {},
      DIALOG_SEARCH_LOCATION: DIALOG_SEARCH_LOCATION,
      placesToCoords: [],
      isLoading: true,
      map: null,
      markers: [],
      skeletonLoadingAmount: 5,
      isMapModalOpen: false,
      centerIndex: 0,
      centerIcon: {
        url: require('@/assets/images/StaplesIcon.png'),
        scaledSize: new google.maps.Size(28, 28)
      },
      mainIcon: {
        url: require('@/assets/images/StaplesIcon.png'),
        scaledSize: new google.maps.Size(20, 20)
      },
      homeIcon: {
        url: require('@/assets/images/location-icon.png'),
        scaledSize: new google.maps.Size(40, 40)
      },
      openedInfoWindow: null,
      customerSupportContact: '',
      isCarrierInfoModalOpen: false
    }
  },
  computed: {
    ...mapGetters({
      policyReturnMethods: 'returns/policyReturnMethods',
      returnMethods: 'returns/returnMethods',
      formattedAddress: 'returns/formattedShippingAddress',
      isModalOpen: 'returns/getIsModalOpen',
      unformattedAddress: 'returns/getAddress',
      policies: 'returns/policies'
    }),
    noDropoffLocationsFound () {
      const distance = this.policies?.policy_return_methods?.find(
        (method) => method.type === CONSTANTS.RETURN_METHODS.EXPRESS_RETURNS
      )?.distance_threshold
      return RETURN_METHOD_SCREENS.NO_DROPOFF_LOCATIONS_FOUND.replace('$$distanceRadius', distance)
    }
  },
  watch: {
    formattedAddress: {
      deep: true,
      handler () {
        this.loadDropoffLocationData()
      }
    }
  },
  async mounted () {
    if (this.$vuetify.breakpoint.smAndDown) {
      this.skeletonLoadingAmount = 1
    }
    try {
      this.customerSupportContact = this.policies?.retailer_info?.customer_support_contact
      this.initMap()
      await this.setInitialMarkers()
    } catch (e) {
      console.error(e)
    }
  },
  methods: {
    ...mapMutations({
      setSelectedCarrier: 'returns/setSelectedCarrier'
    }),
    openMapModal () {
      this.isMapModalOpen = true
    },
    openCarrierInfoModal () {
      this.isCarrierInfoModalOpen = true
    },
    closeCarrierInfoModal () {
      this.isCarrierInfoModalOpen = false
    },
    closeMapModal () {
      this.isMapModalOpen = false
    },
    async loadDropoffLocationData () {
      // remove all existing markers
      this.markers.map(marker => {
        marker.marker.setMap(null)
      })

      // reset markers
      this.markers = []

      // close any necessary info window
      if (this.openedInfoWindow !== null) {
        this.openedInfoWindow.close()
      }

      // close modal if open
      if (this.isMapModalOpen) {
        this.closeMapModal()
      }

      try {
        await this.setInitialMarkers()
      } catch (e) {
        console.error(e)
      }
      // reset refs
      var lngth = Object.keys(this.$refs['map-tiles'].$refs).length
      var initDiv = 'item' + String(0)
      for (let j = this.placesToCoords.length; j < lngth; j++) {
        const divName = 'item' + String(j)
        delete this.$refs['map-tiles'].$refs[divName]
      }
      // reset center index
      this.centerIndex = 0
      this.$refs['map-tiles'].$refs[initDiv][0].$el.classList.add('bg-well')
      this.$refs['map-tiles'].$refs[initDiv][0].$el.classList.remove('white')
    },
    scrollToView (i, openWindow) {
      const refName = 'item' + String(i)
      this.$refs['map-tiles'].$refs[refName][0].$el.scrollIntoView({ behavior: 'smooth' })
      for (let j = 0; j < Object.keys(this.$refs['map-tiles'].$refs).length; j++) {
        const divName = 'item' + String(j)
        if (j === i) { // grey out clicked on tile
          this.$refs['map-tiles'].$refs[divName][0].$el.classList.add('bg-well')
          this.$refs['map-tiles'].$refs[divName][0].$el.classList.remove('white')
        } else { // white out every other tile
          this.$refs['map-tiles'].$refs[divName][0].$el.classList.add('white')
          this.$refs['map-tiles'].$refs[divName][0].$el.classList.remove('bg-well')
        }
      }
      if (openWindow) { // clicked on tile so open window
        this.openInfoWindow(this.map, this.markers[i].marker, i, false)
      }
    },
    initMap () {
      this.map = new google.maps.Map(document.getElementById('map'), {
        center: { lng: CONSTANTS.BASE_LNG, lat: CONSTANTS.BASE_LAT },
        zoom: 12,
        options: {
          zoomControl: false,
          mapTypeControl: false,
          scaleControl: false,
          streetViewControl: false,
          rotateControl: false,
          fullscreenControl: false
        }
      })
    },
    openInfoWindow (map, marker, index, scrollToView) {
      // map.setCenter(marker.getPosition())

      if (scrollToView) { // clicked on marker to scroll tile
        this.scrollToView(index, false)
      }

      // not shipping address
      if (index !== -1) {
        marker.setIcon(this.centerIcon)
      }
      this.markers[this.centerIndex].marker.setIcon(this.mainIcon)

      if (index !== -1) {
        this.centerIndex = index
      }

      // open/close info windows
      if (this.openedInfoWindow !== null) {
        this.openedInfoWindow.close()
      }
      const infoWindow = new google.maps.InfoWindow({
        content: this.createContentString(index)
      })

      infoWindow.addListener('domready', () => {
        const window = document.getElementById('listen-for-click')
        window.addEventListener('click', () => {
          if (this.placesToCoords && this.placesToCoords.length > 0) {
            this.setSelectedCarrier(this.placesToCoords[index].carrier)
            this.openCarrierInfoModal()
          }
        })
      })

      infoWindow.open(map, marker)

      this.openedInfoWindow = infoWindow
    },
    createMarker (map, openInfoWindow, index) {
      let marker

      if (index === this.centerIndex && index !== -1) { // first marker automatically bigger, center index is 0 atm
        marker = new google.maps.Marker({
          position: this.placesToCoords[index].position,
          map,
          icon: this.centerIcon
        })
      } else if (index !== -1) { // all other icons
        marker = new google.maps.Marker({
          position: this.placesToCoords[index].position,
          map,
          icon: this.mainIcon
        })
      } else { // home icon, no click listener
        marker = new google.maps.Marker({
          position: this.shippingAddress.position,
          map,
          icon: this.homeIcon
        })
      }

      if (index !== -1) {
        marker.addListener('click', (function (map, marker, index) {
          return function () {
            openInfoWindow(map, marker, index, true)
          }
        }(map, marker, index)))
      }

      this.markers.push({ marker: marker, distance: index !== -1 ? this.placesToCoords[index].distance : null, closingTime: index !== -1 ? this.placesToCoords[index].closingTime : null })
    },
    async setInitialMarkers () {
      this.isLoading = true

      const result = await findAllExpressReturnLocations(this.returnMethods, this.policyReturnMethods, true)
      this.shippingAddress = result.shippingAddress
      this.placesToCoords = result.placesToCoords

      const { map, openInfoWindow } = this
      for (let i = 0; i < this.placesToCoords.length; i++) {
        this.createMarker(map, openInfoWindow, i)
      }
      this.createMarker(map, openInfoWindow, -1)
      map.setCenter(this.markers[0].marker.getPosition()) // center is always first address for the moment

      // have the first window automatically opened
      const infoWindow = new google.maps.InfoWindow({
        content: this.createContentString(0)
      })

      infoWindow.addListener('domready', () => {
        const window = document.getElementById('listen-for-click')
        window.addEventListener('click', () => {
          if (this.placesToCoords && this.placesToCoords.length > 0) {
            this.setSelectedCarrier(this.placesToCoords[0].carrier)
            this.openCarrierInfoModal()
          }
        })
      })

      if (this.markers[0].distance !== null || this.markers[0].closingTime !== null) { // not home icon
        infoWindow.open(map, this.markers[0].marker)
        this.openedInfoWindow = infoWindow
      }

      this.isLoading = false
    },
    createContentString (index) {
      if (this.markers[index].distance !== null && this.markers[index].closingTime !== null) {
        const distance = this.markers[index].distance !== CONSTANTS.UNKNOWN ? (this.markers[index].distance) : ''
        const closesAt = this.markers[index].closingTime !== CONSTANTS.UNKNOWN ? ('closes at ' + String(this.markers[index].closingTime)) : ''
        const middle = (closesAt === '' || distance === '') ? '' : '  ' + '&middot;' + '  '
        return '<div id="listen-for-click" class="info-window-content"><span class="info-window-text">' + distance + middle + closesAt +
        '</span></div>'
      }
      return ''
    }
  }
}
</script>

<style>
  .gmnoprint, .gm-style-cc {
    display: none !important;
    visibility: hidden !important;
  }
  .loader {
    z-index: 11;
    display: flex;
    justify-content: center;
  }
  .map-lg {
    width: 50%;
    height: auto;
  }
  .gm-ui-hover-effect {
    display: none !important;
  }
  .info-window-text {
    font-family: var(--font-family);
    font-size: 11px;
    font-weight: 700 !important;
    color: var(--text-primary);
  }
  .gm-style .gm-style-iw-c {
    border-radius: 16px !important;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    width: 175px;
    height: 30px;
    padding: 10px;
  }
  .gm-ui-hover-effect {
    top: -3px !important;
    right: 0px !important;
  }
  .map-view-lg {
    display: flex;
    height: 720px;
    margin-bottom: 8px;
    flex-direction: row-reverse;
  }
  .map-tiles-lg {
    width: 50%;
  }
  .map-sm {
    height: 342px;
    width: 100%;
  }
  .map-tiles-sm {
    width: 100%;
  }
  .map-view-sm {
    flex-direction: column;
    height: unset;
  }
  .shipping-address {
    overflow: hidden !important;
  }
</style>
