import { VariantInventoryService } from '@/common/api.service'
import {
  FETCH_VARIANTS,
  UPDATE_SELECTED_ITEM_WITH_EXCHANGE_VARIANTS
} from '@/store/constants'
import { flattenVariants } from '@/utils'

export default {
  async [FETCH_VARIANTS] ({ commit }, itemsToExchange) {
    try {
      commit('setLoading', true)
      const itemSKUs = itemsToExchange.map((item) => item.sku)

      await Promise.all(
        itemSKUs.map(async (sku) => {
          const itemToExchange = itemsToExchange.find((i) => i.sku === sku)

          const params = {
            sku: sku,
            unit_price_amount_cents: itemToExchange.unit_price_amount_cents
          }

          const { data } = await VariantInventoryService.getVariantsBySKU(params)
          if (!data) {
            return
          }

          const flattenedVariants = flattenVariants(data.variants)
          await commit(
            'order/' + UPDATE_SELECTED_ITEM_WITH_EXCHANGE_VARIANTS,
            { data: flattenedVariants, identifier: itemToExchange.identifier },
            { root: true }
          )
        })
      )
    } catch (error) {
      console.error({ error })
    } finally {
      commit('setLoading', false)
    }
  }
}
