<template>
  <div>
    <template v-for="(returnItem, returnItemIndex) in this.rmaList.rmaListItems">
      <v-row :key="returnItemIndex">
        <div class="bg-well my-2">
          <div class="mx-5">
            <div class="d-flex justify-space-between align-center my-5">
              <div>
                <o-text sm bold>
                  {{ getReturnDate(returnItem.created_at) }}
                </o-text>
              </div>
              <div class="d-flex align-center">
                <v-icon small bold>
                  {{ getStepperlabel(returnItem.return_status).icon }}
                </v-icon>
                <o-text
                  sm
                  bold
                  class="ml-2"
                >
                  {{ getStepperlabel(returnItem.return_status).title }}
                </o-text>
              </div>
            </div>

            <v-divider />

            <template v-for="(item, index) in returnItem.rma_items">
              <div :key="index" class="mt-4">
                <o-text
                  sm
                  bold
                  v-if="isExchange(item)"
                >
                  {{ EXCHANGE }}
                </o-text>
                <v-row>
                  <div v-if="isExchange(item)" class="d-flex mb-4">
                    <v-row>
                      <v-row>
                        <v-col cols="10">
                          <image-thumbnail
                            :key="index"
                            :item="item.exchange_item"
                            :index="index"
                            :title="ORIGINAL_PURCHASE"
                            :sub-title1="item.exchange_item.original_item.title"
                            :sub-title2="
                              item.exchange_item.original_item.color +
                                ' - ' +
                                item.exchange_item.original_item.size
                            "
                            :image-url="
                              item.exchange_item.original_item.image_url
                            "
                            :order-brand="order.order.concept"
                            product-thumbnail
                            is-small-thumbnail
                            from-return-item
                          />
                          <div class="ml-6">
                            <v-img
                              :src="require('@/assets/images/down_arrow.png')"
                              max-width="22"
                              max-height="22"
                              style="z-index: 1"
                              contain
                            />
                          </div>
                        </v-col>
                      </v-row>
                      <v-row class="mb-1 mt-n3">
                        <v-col cols="10">
                          <image-thumbnail
                            :key="index"
                            :item="item.exchange_item"
                            :index="index"
                            :title="INCOMING_ITEM"
                            :sub-title1="item.exchange_item.title"
                            :sub-title2="
                              item.exchange_item.color +
                                ' - ' +
                                item.exchange_item.size
                            "
                            :image-url="item.exchange_item.image_url"
                            :order-brand="order.order.concept"
                            product-thumbnail
                            is-small-thumbnail
                            from-return-item
                          />
                        </v-col>
                      </v-row>
                    </v-row>
                  </div>
                </v-row>
              </div>
            </template>

            <div v-if="isRefundOrder(returnItem.rma_items)" class="mt-4">
              <o-text
                sm
                bold
              >
                {{ RETURN }}
              </o-text>

              <template v-for="(item, index) in returnItem.rma_items">
                <v-row :key="index">
                  <div v-if="!isExchange(item)" class="d-flex mb-4">
                    <v-row>
                      <v-col>
                        <image-thumbnail
                          :key="index"
                          :item="item"
                          :index="index"
                          :title="item.order_item.title"
                          :sub-title1="item.order_item.subtitle"
                          :image-url="item.order_item.image_url"
                          :order-brand="order.order.concept"
                          product-thumbnail
                          is-small-thumbnail
                          from-return-item
                        />
                      </v-col>
                      <v-col style="max-width: 50px">
                        <o-text
                          sm
                          :key="index"
                          class="d-flex justify-end"
                        >
                          {{ item.order_item.formattedRefundPrice }}
                        </o-text>
                      </v-col>
                    </v-row>
                  </div>
                </v-row>
              </template>
            </div>
          </div>

          <v-divider />

          <div
            class="d-flex justify-content-between align-center justify-center my-5"
          >
            <action-button
              @click.stop="trackMyReturn(returnItem)"
              primary-color
              :block="$vuetify.breakpoint.smAndDown"
              v-if="!returnItem.customer_keep"
            >
              {{
                isOnlyExchangeOrder(returnItem.rma_items)
                  ? TRACK_MY_EXCHANGE
                  : TRACK_MY_RETURN
              }}
            </action-button>
          </div>
        </div>
      </v-row>
    </template>
  </div>
</template>

<script>
import ImageThumbnail from '@/components/organism/ImageThumbnail.vue'
import { mapGetters, mapMutations } from 'vuex'
import OText from '@/components/atoms/OText.vue'
import { getFormattedDate } from '@/utils'
import ActionButton from '@/components/atoms/ActionButton.vue'
import {
  UPDATE_REFUND_LIST_FOR_RETURN_USER,
  UPDATE_EXCHANGE_LIST_FOR_RETURN_USER
} from '@/store/constants.js'
import { TRACKING_VIEW, RETURN_REVIEW } from 'branding/strings.json'
import { CONSTANTS } from '@/constants'
import {
  isExchangeOrder, isRefundOrder, isExchangeAndRefundOrder, isOnlyExchangeOrder
} from '@/controllers/OrdersController.js'

export default {
  name: 'ReturnUserView',
  components: {
    ImageThumbnail,
    OText,
    ActionButton
  },
  data () {
    return {
      COMPLETED: TRACKING_VIEW.COMPLETED,
      ACTIVE: TRACKING_VIEW.ACTIVE,
      CANCELED: TRACKING_VIEW.CANCELED,
      RETURN: TRACKING_VIEW.RETURN,
      EXCHANGE: TRACKING_VIEW.EXCHANGE,
      TRACK_MY_RETURN: TRACKING_VIEW.TRACK_MY_RETURN,
      TRACK_MY_EXCHANGE: TRACKING_VIEW.TRACK_MY_EXCHANGE,
      ORIGINAL_PURCHASE: RETURN_REVIEW.ORIGINAL_PURCHASE,
      INCOMING_ITEM: RETURN_REVIEW.INCOMING_ITEM,
      RETURN_STATUS: CONSTANTS.RETURN_STATUS,
      rmaStatus: ''
    }
  },
  computed: {
    ...mapGetters({ rmaList: 'order/rmaList', order: 'order/order' })
  },
  props: {},
  methods: {
    ...mapMutations({
      setSelectedReturnMethod: 'returns/setSelectedReturnMethod'
    }),
    getReturnDate (createdAt) {
      return 'Requested on ' + getFormattedDate(createdAt)
    },
    isExchangeOrder (rmaItems) {
      return isExchangeOrder(rmaItems)
    },
    isRefundOrder (rmaItems) {
      return isRefundOrder(rmaItems)
    },
    isExchangeAndRefundOrder (rmaItems) {
      return isExchangeAndRefundOrder(rmaItems)
    },
    isOnlyExchangeOrder (rmaItems) {
      return isOnlyExchangeOrder(rmaItems)
    },
    async trackMyReturn (returnItem) {
      this.$store.commit('returns/updateSelectedReturnItem', returnItem)
      const refundList = []
      const exchangeList = []
      returnItem.rma_items.map((item) => {
        if (!item?.is_exchange) {
          refundList.push(item.order_item)
        } else {
          exchangeList.push(item.exchange_item)
        }
      })
      this.$store.commit(
        'order/' + UPDATE_REFUND_LIST_FOR_RETURN_USER,
        refundList
      )
      this.$store.commit(
        'order/' + UPDATE_EXCHANGE_LIST_FOR_RETURN_USER,
        exchangeList
      )
      this.setSelectedReturnMethod(returnItem.return_method)
      this.$router.push(
        '/orders/' + this.order.order_hash + '/tracker/' + returnItem.identifier
      )
    },
    isExchange (item) {
      return item.is_exchange && item.exchange_item
    },
    getStepperlabel (returnStatus) {
      var title = ''
      var icon = ''
      if (
        returnStatus === this.RETURN_STATUS.STARTED ||
        returnStatus === this.RETURN_STATUS.IN_TRANSIT ||
        returnStatus === this.RETURN_STATUS.RECEIVED
      ) {
        title = this.ACTIVE
        icon = 'hourglass_top'
      } else if (returnStatus === this.RETURN_STATUS.COMPLETED) {
        title = this.COMPLETED
        icon = 'done'
      } else if (returnStatus === this.RETURN_STATUS.CANCELED) {
        title = this.CANCELED
        icon = 'block'
      }
      return { title, icon }
    }
  },
  mounted () {
    this.getStepperlabel()
  }
}
</script>
<style scoped></style>
