<template>
  <div>
    <v-row class="justify-center mt-2">
      <v-col
        md="5"
        sm="8"
        xs="12"
      >
        <div class="mx-4 mb-6">
          <product-list-header
            :selected-count="this.selectedCount"
            :order-number="this.order.order_id"
            :class="{ 'ml-n1': $vuetify.breakpoint.smAndDown }"
            :total-items-count-title="getCountTitle"
            :rma-items-count-title="getRMACountTitle"
            :is-return-user="this.getRmaItemsLength > 0"
          />
        </div>
        <v-list
          class="mb-12 mt-2"
          :class="{ 'pb-16': $vuetify.breakpoint.smAndDown }"
        >
          <div v-if="isLoading">
            <o-skeleton-card
              v-for="(item, index) in $vuetify.breakpoint.smAndDown ? 6 : 10"
              :key="index"
            />
          </div>

          <div
            v-if="this.getOrderItemsLength > 0 && this.getRmaItemsLength > 0"
          >
            <v-tabs
              v-model="active_tab"
              color="var(--button-tertiary)"
              id="return-list-tab"
              slider-color="var(--button-tertiary)"
              height="56px"
              slider-size="1"
              grow
            >
              <v-tab
                v-for="tab of tabs"
                :key="tab.index"
                :ripple="false"
                @click="tabChanged(tab.index)"
              >
                <v-col class="d-flex justify-center align-center">
                  <o-text class="pb-3" v-html="getTabName(tab.index)" />
                  <div
                    v-if="tab.index !== active_tab"
                    class="v-tabs-slider-wrapper"
                    style="height: 1px; width: 100%; color: #D9D9D9"
                  >
                    <div class="v-tabs-slider" />
                  </div>
                </v-col>
              </v-tab>
            </v-tabs>
            <v-tabs-items v-model="active_tab">
              <v-tab-item v-for="tab of tabs" :key="tab.index">
                <order-list v-if="tab.index === 0" class="mt-3" />
                <div v-if="tab.index === 1">
                  <return-user-view class="mt-3" />
                </div>
              </v-tab-item>
            </v-tabs-items>
          </div>

          <div
            v-if="this.getOrderItemsLength > 0 && this.getRmaItemsLength === 0"
          >
            <order-list />
          </div>

          <div
            v-if="this.getRmaItemsLength > 0 && this.getOrderItemsLength === 0"
          >
            <return-user-view />
          </div>
        </v-list>
      </v-col>
    </v-row>
    <div class="d-flex justify-center" v-if="showReturnActionButton">
      <action-button
        fixed
        bottom
        :class="{
          'mb-n4 mobile-button': $vuetify.breakpoint.smAndDown,
          'mb-n2 desktop-button': $vuetify.breakpoint.mdAndUp,
        }"
        :disabled="selectedCount === 0"
        @click="nextPage"
        :loading="isLoading"
        primary-color
      >
        {{ getStickyLabel }}
      </action-button>
    </div>
  </div>
</template>

<script>
import {
  FETCH_ORDER,
  EMPTY_REFUND_LIST,
  EMPTY_EXCHANGE_LIST,
  FETCH_VARIANTS
} from '@/store/constants.js'
import { PAGE_TITLES } from 'branding/strings.json'
import { mapGetters } from 'vuex'
import ActionButton from '@/components/atoms/ActionButton.vue'
import ProductListHeader from '@/components/organism/ProductListHeader.vue'
import OSkeletonCard from '@/components/atoms/OSkeletonCard.vue'
import OrderList from './OrderList.vue'
import ReturnUserView from './ReturnUserView.vue'
import OText from '@/components/atoms/OText.vue'
import { isExchangesEnabled } from '@/controllers/ExchangesController'
import { CONSTANTS } from '@/constants'

export default {
  components: {
    ActionButton,
    ProductListHeader,
    OSkeletonCard,
    OrderList,
    ReturnUserView,
    OText
  },

  props: {},

  beforeRouteLeave (to, from, next) {
    // If the form is dirty and the user did not confirm leave,
    // prevent losing unsaved changes by canceling navigation
    this.$store.commit('ui/updateIsStickyButtonVisible', false)
    next()
  },
  beforeDestroy () {
    this.$store.commit('ui/updateIsStickyButtonVisible', false)
  },
  beforeUnmount () {
    this.$store.commit('ui/updateIsStickyButtonVisible', false)
  },
  created () {
    this.$store.commit('ui/updateIsStickyButtonVisible', true)
  },

  data () {
    return {
      active_tab: this.getReturnSelectedTab,
      showReturnActionButton: true,
      tabs: []
    }
  },
  mounted () {
    this.active_tab = this.getReturnSelectedTab
    if (this.order.order_hash === '') {
      this.getData()
    } else {
      this.showOrHideReturnItemsButton(this.order.order.items)
    }
    var tabObj1 = { index: 0, name: '' }
    var tabObj2 = { index: 1, name: '' }
    var tabsArray = []
    tabsArray.push(tabObj1)
    tabsArray.push(tabObj2)
    this.tabs = tabsArray
    this.$store.commit('returns/resetIndex')
  },

  methods: {
    getTabName (index) {
      var title = ''
      if (index === 0) {
        title =
          "<div class='d-flex flex-column'><o-text style='text-transform: none; font-weight: 900'>All</o-text><o-text style='text-transform: none' sm>Select to return</o-text></div>"
      } else {
        title = `<div class='d-flex flex-column'><o-text style='text-transform: none; font-weight: 900'>Past returns</o-text><o-text style='text-transform: none' sm>${this.getCountSubTitle()}</o-text></div>`
      }
      return title
    },
    async nextPage () {
      this.$store.commit('ui/updateIsStickyButtonVisible', false)
      this.$store.commit('order/' + EMPTY_REFUND_LIST)
      this.$store.commit('order/' + EMPTY_EXCHANGE_LIST)
      this.$router.push('/orders/' + this.order.order_hash + '/reasons')
      if (isExchangesEnabled()) {
        this.$store.dispatch(`exchange/${FETCH_VARIANTS}`, this.selectedItems)
      }
    },
    async getData () {
      const hash = this.$route.params.id
      let params = { order_hash: hash }

      if (this.$route.params.id === CONSTANTS.API_VERSION && this.$route.query?.order_id && this.$route.query?.zip_code) {
        params = {
          order_id: this.$route.query?.order_id,
          zip_code: this.$route.query?.zip_code
        }
      }
      await this.$store
        .dispatch('order/' + FETCH_ORDER, params)
        .then((response) => {
          if (response) {
            this.showOrHideReturnItemsButton(response.order.items)
          }
        })
        .catch((err) => {
          this.$router.push('/orderandzipcode')
          console.error({ err })
        })
    },
    tabChanged (index) {
      if (index === 0) {
        document.title = PAGE_TITLES.ITEM_SELECTION
      } else {
        document.title = PAGE_TITLES.PAST_RETURNS
      }
      this.$store.commit('returns/updateReturnSelectedTab', index)
      this.showOrHideReturnItemsButton(this.order.order.items)
    },
    showOrHideReturnItemsButton (items) {
      var allItemsReturned = true
      items.forEach((item) => {
        if (item.quantity > 0) {
          allItemsReturned = false
          return allItemsReturned
        }
      })
      this.showReturnActionButton =
        this.order?.rma_list?.length === 0 ||
        (this.getReturnSelectedTab === 0 && !allItemsReturned)
    },
    getCountSubTitle () {
      const completedCount = this.rmaList?.rmaListItems?.filter(
        (returnItem) => returnItem.return_status === 'COMPLETED'
      )?.length
      const canceledCount = this.rmaList?.rmaListItems?.filter(
        (returnItem) => returnItem.return_status === 'CANCELED'
      )?.length
      const inProgressCount =
        this.rmaList?.rmaListItems?.length - completedCount - canceledCount
      var progressTitle = ''
      if (inProgressCount >= 0 && completedCount > 0) {
        progressTitle = `${inProgressCount} in progress · ${completedCount} completed`
      } else if (completedCount > 0) {
        progressTitle = `${completedCount} completed`
      } else if (inProgressCount >= 0) {
        progressTitle = `${inProgressCount} in progress`
      }
      return progressTitle
    }
  },

  computed: {
    ...mapGetters({
      order: 'order/order',
      isLoading: 'order/isLoading',
      getReturnSelectedTab: 'returns/getReturnSelectedTab',
      selectedItems: 'order/selectedItems',
      rmaList: 'order/rmaList',
      policies: 'returns/policies'
    }),
    selectedCount () {
      const selectedItems = this.order.order.items.filter(
        (item) => item.selected
      )

      return selectedItems.reduce((accSum, item) => {
        return accSum + item.quantity
      }, 0)
    },
    getStickyLabel () {
      return this.selectedCount === 0
        ? 'Select items to return'
        : `Return ${this.selectedCount} ${
            this.selectedCount > 1 ? 'items' : 'item'
          }`
    },
    getCountTitle () {
      const totalItems = this.order.order.items.reduce((accSum, item) => {
        return accSum + item.original_quantity
      }, 0)

      return ` · ${totalItems} ${totalItems > 1 ? 'items' : 'item'}`
    },
    getRMACountTitle () {
      let title = ''
      if (this.order?.rma_list?.length > 0) {
        title =
          ' · ' +
          this.order?.rma_list?.length +
          (this.order?.rma_list?.length > 1 ? ' returns' : ' return')
      }
      return title
    },
    getOrderItemsLength () {
      return this.order?.order?.items?.length
        ? this.order.order.items.length
        : 0
    },
    getRmaItemsLength () {
      return this.order?.rma_list?.length ? this.order.rma_list.length : 0
    }
  }
}
</script>

<style scoped>
.container {
  padding-top: 30px;
}
.desktop-button {
  width: 40%;
  min-height: 66px;
  z-index: 99 !important;
}
.mobile-button {
  border-radius: 0em;
  min-height: 66px;
  width: 100%;
  z-index: 99 !important;
}
.rounded-circle {
  border-radius: 50%;
  width: 36px;
  height: 36px;
  padding: 8px;

  background: #f6f6f6;
  color: black;
  text-align: center;
}
#return-list-tab .v-slide-group__prev {
  display: none !important;
}
</style>
