<template>
  <div class="mb-16" v-if="!this.isLoading">
    <div
      :class="{
        'ml-5 mt-6': $vuetify.breakpoint.smAndDown,
        'ml-8 mt-7': $vuetify.breakpoint.mdAndUp,
      }"
    >
      <back-button />
    </div>
    <div
      :class="{
        'mt-2': $vuetify.breakpoint.smAndDown,
        'mt-n12': $vuetify.breakpoint.mdAndUp,
      }"
    >
      <div
        :style=" 'max-width: 810px; margin: auto;'"
        :class="{
          'mx-auto': $vuetify.breakpoint.mdAndUp,
          'm-2': $vuetify.breakpoint.xs
        }"
      >
        <v-row
          class="mb-2"
          :class="{
            'ml-4': $vuetify.breakpoint.smAndDown,
            'mx-auto': $vuetify.breakpoint.mdAndUp,
          }"
          align="center"
        >
          <header-title>
            {{ CUSTOMER_KEEPS_TITLE }}
          </header-title>
          <header-small-title class="mb-2">
            {{ customerKeepSubtitle }}
          </header-small-title>
        </v-row>

        <div
          :class="{
            'd-flex align-center justify-center flex-wrap': this.$vuetify.breakpoint
              .smAndDown,
            'd-flex align-start justify-center flex-wrap': this.$vuetify.breakpoint.mdAndUp,
          }"
        >
          <product-card
            class="keeps-card mt-4"
            v-for="(item, i) in selectedItems"
            :key="i"
            :height="determineImageSize()"
            :width="determineImageSize()"
            v-bind="item"
            :src="item.image_url"
            :selected="false"
            enable-text
            display-pricing
            no-border
            basic
          />
        </div>
        <v-col>
          <action-button
            @click.stop="handleNextClick"
            primary-color
            :loading="isLoading"
            :padding="'0px'"
            block
            large
          >
            {{ "Next" }}
          </action-button>
        </v-col>
      </div>
    </div>
  </div>
  <div v-else>
    <v-skeleton-loader type="card" />
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { setSelectedReturnMethod } from '@/controllers/MapController.js'
import { CUSTOMER_KEEPS } from 'branding/strings.json'
import HeaderSmallTitle from '@/components/molecules/HeaderSmallTitle.vue'
import HeaderTitle from '@/components/molecules/HeaderTitle.vue'
import BackButton from '@/components/atoms/BackButton.vue'

export default {
  name: 'CustomerKeepsView',
  components: {
    ActionButton: () => import('@/components/atoms/ActionButton.vue'),
    ProductCard: () => import('@/components/organism/ProductCard.vue'),
    HeaderSmallTitle,
    HeaderTitle,
    BackButton
  },
  data: () => ({
    CUSTOMER_KEEPS_TITLE: CUSTOMER_KEEPS.TITLE,
    CUSTOMER_KEEPS_SUB_TITLE: CUSTOMER_KEEPS.SUB_TITLE,
    customerKeepSubtitle: '',
    imageSize: {
      xs: '358px',
      sm: '358px',
      md: '358px',
      lg: '520px',
      xl: '358px',
      desktopMultiple: '316px'
    }
  }),
  computed: {
    ...mapGetters({
      order: 'order/order',
      currentItemIndex: 'returns/currentItemIndex',
      selectedItems: 'order/selectedItems',
      isLoading: 'exchange/isLoading',
      policies: 'returns/policies'
    }),
    items () {
      return this.selectedItems
    }
  },
  methods: {
    determineImageSize () {
      const currentSize = this.$vuetify.breakpoint.name
      const largerSizes = ['md', 'lg', 'xl']

      if (largerSizes.includes(currentSize) && this.selectedItems.length > 1) {
        return '316px'
      }

      return this.imageSize[this.$vuetify.breakpoint.name]
    },
    handleNextClick () {
      setSelectedReturnMethod({ type: 'customer_keep' })
      this.$router.push('/orders/' + this.order.order_hash + '/review')
    }

  },
  mounted () {
    document.title = this.CUSTOMER_KEEPS_TITLE
    const refundTimingEstimate = this.policies.refund_timing_estimate ? this.policies.refund_timing_estimate : '5-7'
    this.customerKeepSubtitle = this.CUSTOMER_KEEPS_SUB_TITLE.replace(
      '$$refundTimingEstimate',
      refundTimingEstimate
    )
  }
}
</script>

<style scoped>
.keeps-card {
  pointer-events: none;
  flex: 0;
  border: 0px !important;
}
.keeps-wrapper{
  max-width: 64vw !important;
}
</style>
