<template>
  <div style="max-width: 664px; margin: auto">
    <div class="d-flex justify-between">
      <header-title>
        {{ NEXT_STEPS }}
      </header-title>
    </div>
    <div class="d-flex justify-center my-8">
      <img
        class="qrCode"
        :src="this.qrImage"
        v-if="this.qrImage"
      >
      <o-text
        md
        disabled
        v-else
      >
        QR code not found. Please try again
      </o-text>
    </div>
    <div class="d-flex flex-column">
      <o-text
        text-primary
        md
        bold
      >
        {{ getReturnMethodTitle() }}
      </o-text>
      <div class="d-flex align-center mt-4">
        <o-text>
          {{ qrDescription }}
        </o-text>
      </div>
    </div>
    <action-button
      tertiary-color
      text
      v-if="!isMailBack"
      class="ml-n4 mt-4"
      @click="openModal({type: DIALOG_CARRIER_LOCATIONS, selectedCarrier: carrier})"
    >
      {{ SEE_LOCATIONS }}
    </action-button>
    <o-dialog
      is-full-screen
      :visible="isModalOpen"
      @close="closeModal"
    >
      <return-locations
        :show-select-locations="false"
        :title="SEE_RETURN_LOCATIONS"
        v-if="activeModal === DIALOG_CARRIER_LOCATIONS"
        :return-type="getReturnMethodType()"
        :from-return-methods="this.fromReturnMethods"
      />
      <return-store-info
        v-if="activeModal === DIALOG_CARRIER_INFORMATION"
        is-rma-submitted
      />
      <search-return-location v-if="activeModal === DIALOG_SEARCH_LOCATION" source="qrCodeView" />
    </o-dialog>
  </div>
</template>

<script>
import OText from '@/components/atoms/OText.vue'
import HeaderTitle from '@/components/molecules/HeaderTitle.vue'
import {
  CONFIRMATION_VIEW,
  RETURN_METHOD_DIALOGS
} from 'branding/strings.json'
import ActionButton from '@/components/atoms/ActionButton.vue'
import ReturnLocations from '@/components/organism/ReturnLocations.vue'
import ODialog from '@/components/atoms/ODialog.vue'
import { mapGetters, mapMutations } from 'vuex'
import { DIALOG_CARRIER_INFORMATION, DIALOG_CARRIER_LOCATIONS, DIALOG_SEARCH_LOCATION } from '@/store/constants'
import ReturnStoreInfo from '@/components/organism/ReturnStoreInfo.vue'
import SearchReturnLocation from '@/components/organism/SearchReturnLocation.vue'
import { isMailBackReturn, getSelectedReturnMethodDropOffDescription } from '@/controllers/ReturnMethodsController.js'
import { convertImageData } from '@/utils'
import { CONSTANTS } from '@/constants/index.js'

export default {
  name: 'QRCodeView',

  components: {
    OText,
    HeaderTitle,
    ActionButton,
    ReturnLocations,
    ODialog,
    ReturnStoreInfo,
    SearchReturnLocation
  },

  data () {
    return {
      QR_DROP_OFF_DATE: CONFIRMATION_VIEW.QR_DROP_OFF_DATE,
      QR_CODE_DESCRIPTION_REFUND_ONLY: CONFIRMATION_VIEW.QR_CODE_DESCRIPTION_REFUND_ONLY,
      QR_CODE_DESCRIPTION_EXCHANGE_ONLY: CONFIRMATION_VIEW.QR_CODE_DESCRIPTION_EXCHANGE_ONLY,
      QR_CODE_DESCRIPTION_REFUND_EXCHANGE: CONFIRMATION_VIEW.QR_CODE_DESCRIPTION_REFUND_EXCHANGE,
      SEE_LOCATIONS: CONFIRMATION_VIEW.SEE_LOCATIONS,
      SEE_RETURN_LOCATIONS: RETURN_METHOD_DIALOGS.SEE_RETURN_LOCATIONS,
      NEXT_STEPS: CONFIRMATION_VIEW.NEXT_STEPS,
      qrImage: String,
      DIALOG_CARRIER_INFORMATION,
      DIALOG_CARRIER_LOCATIONS,
      DIALOG_SEARCH_LOCATION,
      qrDescription: ''
    }
  },

  props: {
    qrData: {
      type: Object,
      required: true
    },
    fromReturnMethods: {
      type: Boolean,
      required: true,
      default: false
    },
    isExchangeOrRefund: {
      type: String,
      required: true,
      default: ''
    }
  },

  computed: {
    ...mapGetters({
      selectedReturnMethod: 'returns/getSelectedReturnMethod',
      isModalOpen: 'returns/getIsModalOpen',
      activeModal: 'returns/getActiveModal',
      policies: 'returns/policies'
    }),
    isMailBack () {
      return isMailBackReturn(this.selectedReturnMethod)
    }
  },

  methods: {
    ...mapMutations({
      openModal: 'returns/openModal',
      closeModal: 'returns/closeModal'
    }),

    getReturnMethodTitle: function () {
      return getSelectedReturnMethodDropOffDescription(this.selectedReturnMethod)
    },

    getReturnMethodType: function () {
      return this.selectedReturnMethod.type
    }
  },

  mounted () {
    const refundTimingEstimate = this.policies.refund_timing_estimate ? this.policies.refund_timing_estimate : '5-7'
    const carrier = this.selectedReturnMethod?.customer_facing_carrier

    this.qrImage = convertImageData(this.qrData.image)
    if (this.isExchangeOrRefund === CONSTANTS.REFUND) {
      this.qrDescription = this.QR_CODE_DESCRIPTION_REFUND_ONLY.replace(
        '$$refundTimingEstimate',
        refundTimingEstimate
      ).replace('$$CARRIER', carrier)
    } else if (this.isExchangeOrRefund === CONSTANTS.EXCHANGE) {
      this.qrDescription = this.QR_CODE_DESCRIPTION_EXCHANGE_ONLY.replace('$$CARRIER', carrier)
    } else {
      this.qrDescription = this.QR_CODE_DESCRIPTION_REFUND_EXCHANGE.replace(
        '$$refundTimingEstimate',
        refundTimingEstimate
      ).replace('$$CARRIER', carrier)
    }
  }
}
</script>

<style scoped>
.qrCode {
  max-width: 300px;
  max-height: 300px;
}
</style>
