<template>
  <o-dialog
    :visible="show"
    is-full-screen
    @close="show = false"
  >
    <div
      :style="{
        overflow: 'hidden',
      }"
      :class="{
        'pa-0 mb-16': $vuetify.breakpoint.smAndDown,
        'mt-12': $vuetify.breakpoint.mdAndUp,
      }"
    >
      <header-medium-title v-if="!this.isLoading">
        {{ this.selectedItem.title || "" }}
      </header-medium-title>
      <v-row>
        <v-col md="4" order-md="1">
          <v-img
            :src="this.selectedItem.image_url"
            :class="{ 'pa-0': $vuetify.breakpoint.smAndDown }"
            :style="{ 'max-height': 'fit-content' }"
          />
        </v-col>
        <v-col
          md="5"
          order-md="2"
          :class="{ 'd-flex align-start': $vuetify.breakpoint.mdAndUp }"
        >
          <v-card flat>
            <o-text
              s
              v-if="this.selectedColor"
              :class="{ 'mx-6': $vuetify.breakpoint.mdAndUp }"
              v-html="this.determineColorTitle()"
            />
            <product-slide-view
              :enable-text="enableText"
              :cards="exchangeVariantColorOptions"
              :width="72"
              :height="72"
              :on-card-selected="onColorSelected"
              :default-value="exchangeVariantColorOptions[0]"
            />
            <o-text
              s
              class="my-4 pt-3"
              :class="{ 'mx-6': $vuetify.breakpoint.mdAndUp }"
            >
              Size
            </o-text>
            <size-dropdown
              :default-value="this.defaultSizeValue"
              :items="exchangeVariantSizeOptions"
              :on-change="onSizeSelected"
            />
            <div :class="{ 'mx-6': $vuetify.breakpoint.mdAndUp }" class="mt-4">
              <action-button
                @click.stop="onClickMethod"
                primary-color
                :disabled="isDisabled"
                :loading="isLoading"
                block
                large
              >
                {{
                  this.isDisabled
                    ? "Make a selection to exchange"
                    : "Exchange for this item"
                }}
              </action-button>
            </div>
          </v-card>
        </v-col>
      </v-row>
    </div>
  </o-dialog>
</template>

<script>
import ODialog from '@/components/atoms/ODialog.vue'
import { mapGetters } from 'vuex'
import {
  saveSelectedItemToStore,
  getSizeOptionsForSelectedVariant,
  handleColorOrSizeOptionSelected,
  getColorOptionsForPDP,
  getDefaultValue,
  isSameVariant
} from '@/controllers/ExchangesController'
import { CONSTANTS } from '@/constants'
import HeaderMediumTitle from '../molecules/HeaderMediumTitle.vue'

export default {
  name: 'PDPPage',
  components: {
    ODialog,
    ProductSlideView: () =>
      import('@/components/organism/ProductSlideView.vue'),
    OText: () => import('@/components/atoms/OText.vue'),
    SizeDropdown: () => import('@/components/molecules/SizeDropdown.vue'),
    ActionButton: () => import('@/components/atoms/ActionButton.vue'),
    HeaderMediumTitle
  },
  props: {
    visible: Boolean,
    enableText: Boolean,
    returnReasonClicked: {
      type: Function,
      required: true
    }
  },
  data: () => ({
    selectedImage: '',
    isDisabled: true,
    selectedSize: null,
    selectedColor: null,
    RETURN_REASON_ACTION: CONSTANTS.RETURN_REASON_ACTION,
    ACTION_TYPE: CONSTANTS.ACTION_TYPE,
    selectedItem: {},
    exchangeVariantColorOptions: [],
    exchangeVariantSizeOptions: []
  }),
  computed: {
    ...mapGetters({
      isLoading: 'exchange/isLoading',
      currentItemIndex: 'returns/currentItemIndex',
      selectedItems: 'order/selectedItems',
      exchangeList: 'order/exchangeList',
      reason: 'returns/reason',
      reasons: 'returns/reasons'
    }),
    item () {
      return this.selectedItems[this.currentItemIndex]
    },
    show: {
      get () {
        return this.visible
      },
      set (value) {
        if (!value) {
          this.$emit('close')
        }
      }
    },
    defaultSizeValue () {
      return getDefaultValue(this.ACTION_TYPE.SIZE, this.selectedColor)
    }
  },
  methods: {
    onColorSelected (selectedItem) {
      if (!selectedItem.disabled) {
        this.selectedColor = selectedItem.color
        this.handleOptionSelected(selectedItem)
      }
    },
    onSizeSelected (selectedItem) {
      if (!selectedItem.disabled) {
        this.selectedSize = selectedItem.size
        this.handleOptionSelected(selectedItem, this.ACTION_TYPE.SIZE)
      }
    },
    onClickMethod (event) {
      let optionForSelectedSizeAndColor = this.item?.exchangeVariants.find(
        (variant) =>
          variant.size === this.selectedSize &&
          variant.color === this.selectedColor
      )

      if (optionForSelectedSizeAndColor) {
        optionForSelectedSizeAndColor = {
          ...optionForSelectedSizeAndColor,
          disabled: false,
          subtitle: ''
        }
        saveSelectedItemToStore(optionForSelectedSizeAndColor)
        this.returnReasonClicked()
        this.show = false

        const newReason = this.reasons.find(
          (r) => r.recommendation_type === this.RETURN_REASON_ACTION.OTHER
        )
        if (newReason) {
          this.$store.commit('returns/updateReason', {
            reason: newReason,
            itemSku: optionForSelectedSizeAndColor.sku
          })
        }

        this.$emit('click', event)
      }
    },
    updateIsDisabled () {
      this.isDisabled = !(
        this.selectedSize &&
        this.selectedColor &&
        this.selectedItem &&
        !isSameVariant(this.selectedItem) &&
        !this.selectedItem.disabled &&
        !this.isLoading
      )
    },
    handleOptionSelected (selectedItem, action = this.ACTION_TYPE.COLOR) {
      const {
        selectedSize,
        selectedColor,
        selectedItem: updatedSelectedItem
      } = handleColorOrSizeOptionSelected(
        selectedItem,
        action,
        this.item,
        this.selectedSize,
        this.selectedColor
      )

      this.selectedSize = selectedSize
      this.selectedColor = selectedColor
      this.selectedItem = updatedSelectedItem

      if (action === this.ACTION_TYPE.COLOR) {
        const sizes = getSizeOptionsForSelectedVariant(this.selectedColor)
        this.exchangeVariantSizeOptions = sizes
        if (!this.selectedSize && sizes.length === 1 && !sizes[0].isDisabled) {
          this.selectedSize = sizes[0].size
        } else {
          this.selectedSize = this.defaultSizeValue.size
        }
      }
      this.updateIsDisabled()
    },
    determineColorTitle () {
      if (this.item.color === this.selectedColor) {
        return `Color: <b>${this.selectedColor}</b> – Original Purchase`
      }
      return `Color: <b>${this.selectedColor}</b>`
    }
  },
  mounted () {
    this.selectedItem = this.item
    const colorOptions = getColorOptionsForPDP()
    this.exchangeVariantColorOptions = colorOptions
    this.selectedColor = colorOptions[0].color

    this.exchangeVariantSizeOptions = getSizeOptionsForSelectedVariant(
      this.item?.color
    )
    this.selectedSize = this.defaultSizeValue?.size
    this.handleOptionSelected(this.selectedItem)
    this.updateIsDisabled()
  }
}
</script>

<style scoped>
.fullWidth {
  width: 100%;
}
.partialWidth {
  width: 300px;
}
.boldText {
  font-weight: bold;
}
.v-card > div {
  margin-left: 0 !important;
}
</style>
