<template>
  <div class="mb-16">
    <div
      :class="{
        'ml-3 mt-2': $vuetify.breakpoint.smAndDown,
        'ml-8 mt-8': $vuetify.breakpoint.mdAndUp,
      }"
      v-if="!hasMap"
    >
      <back-button />
    </div>
    <not-found-return-method
      v-if="!this.returnMethods || this.returnMethods === 0"
      class="text-center mt-16"
      :customer-support-contact="customerSupportContact"
      :title="noReturnMethods"
    />
    <div
      v-else
      :class="{
        'mt-2': $vuetify.breakpoint.smAndDown,
        'mt-n12': $vuetify.breakpoint.mdAndUp && !hasMap,
      }"
    >
      <div
        :style="!hasMap ? 'max-width: 810px; margin: auto;' : 'margin: auto;'"
        :class="{
          'mx-auto': $vuetify.breakpoint.mdAndUp,
          'm-2': $vuetify.breakpoint.xs && hasMap
        }"
      >
        <v-row
          v-if="!hasMap"
          :class="{
            'ml-4': $vuetify.breakpoint.smAndDown,
            'mx-auto': $vuetify.breakpoint.mdAndUp,
          }"
          align="center"
        >
          <header-title>
            {{ CHOOSE_METHOD }}
          </header-title>
        </v-row>
        <v-row
          justify="center"
          class="mb-4 mt-4 mx-auto"
        >
          <shipping-carrier-card
            :class="hasMap ? 'mt-3' : ''"
            :is-show-case="true"
            :carrier="showCaseCarrier"
            :handle-carrier-selection="handleCarrierSelection"
          />
        </v-row>
        <o-text
          bold
          :class="{
            'ml-4': $vuetify.breakpoint.smAndDown,
            'mx-auto': $vuetify.breakpoint.mdAndUp,
          }"
          v-if="accordionOptions.length > 0"
        >
          Other options
        </o-text>
        <v-row justify="center" class="mb-4 mt-4">
          <accordion
            :options="accordionOptions"
            :handle-selection="handleAccordionSelection"
            :opened-panel="computeOpenedPanel"
          >
            <shipping-carrier-card
              :carrier="openAccordionCarrier"
              :handle-carrier-selection="handleCarrierSelection"
            />
          </accordion>
        </v-row>
      </div>
      <o-dialog
        :visible="isModalOpen"
        is-full-screen
        @close="closeModal"
      >
        <return-locations
          v-if="activeModal === DIALOG_CARRIER_LOCATIONS"
          show-search-area
          :title="EXPLORE_LOCATIONS"
          :return-type="getReturnMethodType()"
          :show-select-locations="false"
          from-return-methods
        />
        <return-store-info
          v-if="activeModal === DIALOG_CARRIER_INFORMATION"
          :is-rma-submitted="false"
        />
        <search-return-location v-if="activeModal === DIALOG_SEARCH_LOCATION" source="returnMethods" />
      </o-dialog>
    </div>
  </div>
</template>

<script>
import OText from '@/components/atoms/OText.vue'
import Accordion from '@/components/molecules/Accordion.vue'
import ShippingCarrierCard from '@/components/organism/ShippingCarrierCard.vue'
import ReturnLocations from '@/components/organism/ReturnLocations.vue'
import ODialog from '@/components/atoms/ODialog.vue'
import ReturnStoreInfo from '@/components/organism/ReturnStoreInfo.vue'
import BackButton from '@/components/atoms/BackButton.vue'
import HeaderTitle from '@/components/molecules/HeaderTitle.vue'
import {
  RETURN_METHOD_SCREENS,
  RETURN_METHOD_DIALOGS,
  PAGE_TITLES
} from 'branding/strings.json'
import { mapGetters, mapMutations } from 'vuex'
import SearchReturnLocation from '@/components/organism/SearchReturnLocation.vue'
import NotFoundReturnMethod from '@/components/molecules/NotFoundReturnMethod.vue'
import {
  DIALOG_SEARCH_LOCATION,
  DIALOG_CARRIER_INFORMATION,
  DIALOG_CARRIER_LOCATIONS
} from '@/store/constants'
import { returnMethodsWithinDistance, setSelectedReturnMethod } from '@/controllers/MapController.js'
import { getReturnsHelperText, getShippingAmt } from '@/controllers/ReturnMethodsController.js'

export default {
  components: {
    ShippingCarrierCard,
    Accordion,
    OText,
    ReturnLocations,
    ODialog,
    ReturnStoreInfo,
    BackButton,
    HeaderTitle,
    SearchReturnLocation,
    NotFoundReturnMethod
  },
  computed: {
    ...mapGetters({
      order: 'order/order',
      isModalOpen: 'returns/getIsModalOpen',
      activeModal: 'returns/getActiveModal',
      returnMethods: 'returns/returnMethods',
      selectedCarrier: 'returns/getSelectedCarrier',
      policies: 'returns/policies',
      policyReturnMethods: 'returns/policyReturnMethods'
    }),
    noReturnMethods () {
      const distance = this.showCaseCarrier?.distance_threshold
      return RETURN_METHOD_SCREENS.NO_RETURN_METHODS.replace('$$distanceRadius', distance)
    },
    showCaseCarrier () {
      let returnMethod = null
      const returnMethods = returnMethodsWithinDistance(this.hasMap)
      if (returnMethods && returnMethods.length) {
        returnMethod = returnMethods[0]
      }
      return returnMethod
    },
    accordionOptions () {
      var accordionReturnMethods = []
      const returnMethods = returnMethodsWithinDistance(this.hasMap)
      if (returnMethods && returnMethods.length) {
        accordionReturnMethods = returnMethods.slice(1)
      }
      this.closeAllPanels()
      return accordionReturnMethods.map((opt) => ({
        primaryText: `${getReturnsHelperText(opt.type)} ${opt.customer_facing_carrier}`,
        secondaryText: `${getShippingAmt(opt)}`,
        id: opt.id
      }))
    },
    computeOpenedPanel () {
      return this.openedPanel
    }
  },
  methods: {
    ...mapMutations({
      openModal: 'returns/openModal',
      closeModal: 'returns/closeModal',
      setSelectedCarrier: 'returns/setSelectedCarrier',
      setSelectedReturnMethod: 'returns/setSelectedReturnMethod'
    }),
    openPanel (index) {
      this.openedPanel = index
    },
    closeAllPanels () {
      this.openedPanel = null
    },
    handleCarrierSelection (carrier) {
      setSelectedReturnMethod(carrier)
      this.$router.push('/orders/' + this.order.order_hash + '/review')
    },
    handleAccordionSelection ({ id }) {
      const option = this.returnMethods.find((m) => m.id === id)
      this.openAccordionCarrier = option
      this.setSelectedCarrier(option)
      this.openPanel(option)
    },
    getReturnMethodType: function () {
      return this.selectedCarrier ? this.selectedCarrier.type : ''
    }
  },
  data () {
    return {
      openAccordionCarrier: null,
      openedPanel: null,
      customerSupportContact: '',
      CHOOSE_METHOD: RETURN_METHOD_SCREENS.CHOOSE_METHOD,
      DIALOG_SEARCH_LOCATION,
      DIALOG_CARRIER_INFORMATION,
      DIALOG_CARRIER_LOCATIONS,
      EXPLORE_LOCATIONS: RETURN_METHOD_DIALOGS.EXPLORE_LOCATIONS,
      hasMap: false
    }
  },
  created () {
    this.hasMap = this.$route.name === 'mapshipping'
  },
  mounted () {
    document.title = PAGE_TITLES.RETURN_METHOD
    this.customerSupportContact = this.policies?.retailer_info?.customer_support_contact
  }
}
</script>

