<template>
  <div style="max-width: 664px" class="mx-auto">
    <div>
      <header-title>{{ this.title }}</header-title>
    </div>
    <o-shipping-address :dialog-type="DIALOG_SEARCH_LOCATION" :formatted-address="formattedAddress" />
    <div v-if="showSearchArea">
      <div class="mt-4">
        <o-select
          :items="shippingOptions"
          :item-text="(x) => x.action + ' ' + x.customer_facing_carrier + ' · ' + x.price"
          :default-value="dropdownDefaultValue"
          @change="onChange"
        />
      </div>
    </div>
    <div>
      <div v-if="isLoading">
        <o-skeleton-card
          v-for="(item, index) in $vuetify.breakpoint.smAndDown ? 3 : 5"
          :key="index"
          :show-image="false"
        />
      </div>
      <div v-if="!isLoading && nearbyCarriers.length === 0">
        No Locations
      </div>
      <div v-if="!isLoading && nearbyCarriers.length > 0">
        <div v-for="(location, idx) in nearbyCarriers" :key="idx">
          <div class="d-flex justify-between align-center w-100 mt-4">
            <div class="mr-auto">
              <o-text
                md
                text-secondary
                bold
                class="mb-1"
              >
                {{ location.name }}
              </o-text>
              <div class="d-flex">
                <o-text text-secondary class="mb-0">
                  {{ location.distance }}
                </o-text>
                <span class="mr-1 ml-1" style="color: grey">&middot;</span>
                <o-text text-secondary class="mb-0">
                  {{ location.formattedAddress }}
                </o-text>
              </div>
              <div class="d-flex" v-if="location.openingHours.isOpen !== undefined">
                <o-text text-secondary class="mb-0">
                  {{ location.openingHours.isOpen ? "Open" : "Closed" }}
                </o-text>
                <span class="mr-1 ml-1" style="color: grey">&middot;</span>
                <o-text
                  text-secondary
                  class="mb-0"
                  v-if="location.openingHours.todayClosingTime !== 'N/A' || location.openingHours.todayOpeningTime !== 'N/A'"
                >
                  {{ location.openingHours.isOpen ? `Closes at ${location.openingHours.todayClosingTime}`:`Opens at ${location.openingHours.todayOpeningTime}` }}
                </o-text>
              </div>
            </div>
            <div>
              <v-icon
                md
                @click="
                  () =>
                    openModal({
                      type: DIALOG_CARRIER_INFORMATION,
                      selectedCarrier: location,
                    })
                "
                color="#3356e4"
                style="cursor: pointer"
              >
                info_outline
              </v-icon>
            </div>
          </div>
          <v-divider style="border-color: grey" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import OSelect from '@/components/atoms/OSelect.vue'
import OText from '@/components/atoms/OText.vue'
import HeaderTitle from '@/components/molecules/HeaderTitle.vue'
import {
  DIALOG_SEARCH_LOCATION,
  DIALOG_CARRIER_INFORMATION
} from '@/store/constants'
import { mapGetters, mapMutations } from 'vuex'
import OSkeletonCard from '@/components/atoms/OSkeletonCard.vue'
import OShippingAddress from '@/components/atoms/OShippingAddress.vue'
import { PAGE_TITLES } from 'branding/strings.json'
import { returnShippingOptions } from '@/controllers/ReturnMethodsController.js'

export default {
  data () {
    return {
      DIALOG_SEARCH_LOCATION,
      DIALOG_CARRIER_INFORMATION,
      activeOption: {},
      dropdownDefaultValue: {},
      shippingOptions: []
    }
  },
  props: {
    showSearchArea: Boolean,
    title: {
      type: String,
      required: true,
      default: ''
    },
    returnType: {
      type: String,
      required: true,
      default: ''
    },
    fromReturnMethods: {
      type: Boolean,
      required: true,
      default: false
    }
  },
  components: { OText, HeaderTitle, OSelect, OSkeletonCard, OShippingAddress },
  computed: {
    ...mapGetters({
      policyReturnMethods: 'returns/policyReturnMethods',
      returnMethods: 'returns/returnMethods',
      selectedCarrier: 'returns/getSelectedCarrier',
      formattedAddress: 'returns/formattedShippingAddress',
      getAddress: 'returns/getAddress',
      isLoading: 'returns/getIsLoading',
      nearbyCarriers: 'returns/getNearbyCarriers',
      selectedReturnMethodOption: 'returns/getSelectedReturnMethodOption'
    })
  },
  methods: {
    ...mapMutations({
      openModal: 'returns/openModal',
      setSelectedCarrier: 'returns/setSelectedCarrier',
      updateSelectedReturnMethodOption: 'returns/updateSelectedReturnMethodOption'
    }),
    onChange (active) {
      this.setSelectedCarrier(active)
      this.activeOption = active
      this.updateSelectedReturnMethodOption(active)
      this.fetchNearbyCarriers()
    },
    fetchNearbyCarriers () {
      this.$store.dispatch('returns/fetchNearbyCarriers', {
        carrier: this.activeOption,
        shippingAddress: this.getAddress
      })
    }
  },
  mounted () {
    var checkValue
    document.title = PAGE_TITLES.EXPLORE_MORE_LOCATIONS
    this.shippingOptions = returnShippingOptions(this.returnMethods, this.policyReturnMethods, this.fromReturnMethods)
    if (!Object.keys(this.selectedReturnMethodOption).length) {
      checkValue = this.shippingOptions && this.shippingOptions.length ? this.shippingOptions.filter((option) => option.type === this.returnType)[0] || this.shippingOptions[0] : {}
    } else {
      const dropdownDefaultValue = this.shippingOptions.find((option) => option.type === this.selectedCarrier?.type)
      if (dropdownDefaultValue) { // value exists
        checkValue = dropdownDefaultValue
      } else {
        checkValue = this.shippingOptions[0] ?? {}
      }
    }
    this.dropdownDefaultValue = checkValue
    this.onChange(checkValue)
  }
}
</script>

