<template>
  <div
    :class="{
      'mb-11': $vuetify.breakpoint.mdAndUp,
    }"
  >
    <div
      :class="{
        'ml-5 mt-3': $vuetify.breakpoint.smAndDown,
        'ml-8 mt-4': $vuetify.breakpoint.mdAndUp,
      }"
      style="display: flex;"
    >
      <back-button />
    </div>
    <v-row :class="$vuetify.breakpoint.smAndDown ? 'justify-center mt-2' : 'justify-center'">
      <v-col
        xl="7"
        md="9"
        sm="8"
        xs="12"
        style="padding: 0 !important;"
      >
        <header-title
          :class="{
            'ml-9 mb-3': $vuetify.breakpoint.xs,
            'mb-3': !$vuetify.breakpoint.xs,
            'mt-n8': !$vuetify.breakpoint.smAndDown
          }"
        >
          {{ headerTitle }}
        </header-title>
      </v-col>
    </v-row>
    <v-row :class="$vuetify.breakpoint.smAndDown ? 'justify-center mt-2' : 'justify-center'">
      <v-col
        xl="7"
        md="9"
        sm="8"
        xs="12"
        style="padding: 0 !important;"
      >
        <o-tab
          :tabs="tabs"
          class="mt-2"
          :on-click-method="detectTabChange"
        />
      </v-col>
    </v-row>
  </div>
</template>

<script>
import ExpressReturnMethod from '@/components/organism/ExpressReturnMethod.vue'
import ReturnMethods from '@/components/pages/ReturnMethods.vue'
import OTab from '@/components/atoms/OTab.vue'
import BackButton from '@/components/atoms/BackButton.vue'
import HeaderTitle from '@/components/molecules/HeaderTitle.vue'
import { RETURN_METHOD_SCREENS } from 'branding/strings.json'

export default {
  components: { OTab, BackButton, HeaderTitle },
  data () {
    return {
      headerTitle: RETURN_METHOD_SCREENS.RETURN_LOCATION,
      RETURN_METHOD_MAP_TITLE: RETURN_METHOD_SCREENS.RETURN_LOCATION,
      CHOOSE_METHOD: RETURN_METHOD_SCREENS.CHOOSE_METHOD,
      tabs: [
        {
          index: 0,
          component: ExpressReturnMethod,
          class: 'mt-3',
          name:
                "<div class='d-flex flex-column'><o-text style='text-transform: none; font-weight: 900'>Express Return</o-text><o-text style='text-transform: none' class='headingSubTitleSmallPlain mt-1'>No box or label needed</o-text></div>"
        },
        {
          index: 1,
          component: ReturnMethods,
          class: '',
          name:
                "<div class='d-flex flex-column'><o-text style='text-transform: none; font-weight: 900'>Other options</o-text><o-text style='text-transform: none' class='headingSubTitleSmallPlain mt-1'>I'll box & print label</o-text></div>"
        }
      ]
    }
  },
  methods: {
    detectTabChange (index) {
      if (index === 1) {
        this.headerTitle = this.RETURN_METHOD_MAP_TITLE
      } else {
        this.headerTitle = this.CHOOSE_METHOD
      }
    }
  }
}
</script>

