import {
  FetchOrderService,
  CreateRMAService,
  GetShippingLabelService,
  GetQRCodeLabelService,
  GetConfigService,
  IsTrustWorthyUserService,
  DestinationService,
  AuthService
} from '@/common/api.service'
import {
  FETCH_ORDER,
  FETCH_START,
  FETCH_ORDER_END,
  SET_ERROR,
  CREATE_RMA,
  GET_SHIPPING_LABEL,
  GET_QR_CODE_LABEL,
  GET_CONFIG,
  IS_TRUST_WORTHY_USER,
  DESTINATION
} from '@/store/constants'
import { formatReturnMethods } from '@/controllers/ReturnMethodsController.js'
import jwtService from '@/common/jwt.service'
import { getIsGift } from '@/store/sessionStorage.js'

function onFetchOrderSuccess (commit, dispatch, data, state) {
  commit(FETCH_ORDER_END, data)

  const isGiftReturn = getIsGift()
  const giftReturnsAllowed = data.policies?.gift_returns_allowed

  let policies = data.policies

  if (giftReturnsAllowed && isGiftReturn) {
    policies = {
      ...policies,
      return_methods: data.policies.return_methods.filter(
        (method) => method.type !== 'express-returns'
      )
    }
  }
  commit('returns/updatePolicies', policies, { root: true })
  commit('returns/updatePolicyReturnMethods', policies.return_methods, {
    root: true
  })
  commit('returns/setAddress', data.order.shipping_address, {
    root: true
  })
  dispatch(
    'returns/fetchAddressInfo',
    {
      shippingAddress: data.order.shipping_address,
      returnMethods: formatReturnMethods(policies.return_methods)
    },
    { root: true }
  )
}

function onFetchConfigSuccess (commit, data) {
  commit('returns/updateConfig', data, { root: true })
}

export default {
  async [FETCH_ORDER] ({ commit, dispatch, rootState }, params) {
    try {
      commit(FETCH_START)
      const {
        data: { access_token: accessToken }
      } = await AuthService.getAccessToken(params)
      jwtService.saveAccessToken(accessToken)

      const { data } = await FetchOrderService.getOrder()

      onFetchOrderSuccess(commit, dispatch, data, rootState)
      return data
    } catch (error) {
      commit(SET_ERROR)
      throw new Error(error)
    }
  },
  [CREATE_RMA] ({ commit }, params) {
    commit(FETCH_START)
    return CreateRMAService.createRMA(params)
      .then(({ data }) => {
        return data
      })
      .catch((error) => {
        commit(SET_ERROR)
        throw new Error(error)
      })
  },
  [GET_SHIPPING_LABEL] ({ commit }, params) {
    commit(FETCH_START)
    return GetShippingLabelService.getShippingLabel(params)
      .then(({ data }) => {
        return data
      })
      .catch((error) => {
        commit(SET_ERROR)
        throw new Error(error)
      })
  },
  [GET_QR_CODE_LABEL] ({ commit }, params) {
    commit(FETCH_START)
    return GetQRCodeLabelService.getQRCodeLabel(params)
      .then(({ data }) => {
        return data
      })
      .catch((error) => {
        commit(SET_ERROR)
        throw new Error(error)
      })
  },
  [GET_CONFIG] ({ commit }, params) {
    return GetConfigService.getConfig(params)
      .then(({ data }) => {
        onFetchConfigSuccess(commit, data)
        return data
      })
      .catch((error) => {
        commit(SET_ERROR)
        throw new Error(error)
      })
  },
  [IS_TRUST_WORTHY_USER] ({ commit }, params) {
    return IsTrustWorthyUserService.isTrustWorthyUser(params)
      .then(({ data }) => {
        return data
      })
      .catch((error) => {
        commit(SET_ERROR)
        throw new Error(error)
      })
  },
  [DESTINATION] ({ commit }, params) {
    return DestinationService.destination(params)
      .then(({ data }) => {
        return data
      })
      .catch((error) => {
        commit(SET_ERROR)
        throw new Error(error)
      })
  }
}
