<template>
  <div class="bg-well px-4 mb-4">
    <div v-if="this.isShowCase" class="d-flex align-center justify-content-between pt-2">
      <o-text
        text-primary
        bold
        :md="$vuetify.breakpoint.smAndDown"
        :lg="$vuetify.breakpoint.mdAndUp"
        class="pt-2 pb-2"
      >
        {{ this.getSelectedReturnMethodDropOffDescription(carrier) }}
      </o-text>
      <o-text
        text-secondary
        base
        bold
      >
        {{ this.getShippingAmtText() }}
      </o-text>
    </div>
    <div class="mt-2">
      <section class="d-flex" v-if="!isMailBack">
        <o-text
          text-secondary
          class="mb-0"
        >
          Nearest {{ carrier.distance || 'N/A' }}
        </o-text>
        <span class="mr-1 ml-1">&middot;</span>
        <o-text
          text-secondary
          class="mb-0"
        >
          {{ carrier.formattedAddress || 'N/A' }}
        </o-text>
      </section>
      <section v-if="!isMailBack">
        <o-text
          style="cursor:pointer;"
          text-blue
          :class="!isUSPS ? 'mb-2' : 'mb-4'"
          @click="openModal({type: DIALOG_CARRIER_LOCATIONS, selectedCarrier: carrier})"
        >
          See locations
        </o-text>
      </section>
      <section>
        <div v-if="!isMailBack && carrier.openingHours.isOpen !== undefined" class="d-flex align-center">
          <v-icon small class="mr-2">
            schedule
          </v-icon>
          <o-text>
            {{ carrier.openingHours.isOpen ? 'Open' : 'Closed' }}
            <span v-if="carrier.openingHours.todayClosingTime !== ''">
              <span class="mr-.5 ml-.5">&middot;</span>
              {{ carrier.openingHours.isOpen ? `Closes at ${carrier.openingHours.todayClosingTime}`:`Opens at ${carrier.openingHours.todayOpeningTime}` }}
            </span>
          </o-text>
        </div>
        <div class="d-flex align-center mb-1" v-if="isUSPS">
          <v-icon
            small
            class="mr-2 mb-5"
          >
            markunread_mailbox
          </v-icon>
          <o-text>
            {{ USPS_PACKAGE }}
          </o-text>
        </div>
        <div class="d-flex align-center">
          <v-icon
            v-if="carrier.requirePackageAndPrint"
            small
            class="mr-2"
          >
            print
          </v-icon>
          <v-icon
            v-else
            small
            class="mr-2"
          >
            print_disabled
          </v-icon>
          <o-text v-if="!carrier.requirePackageAndPrint">
            {{ carrier.requirePackageAndPrint ? PACKAGE_AND_PRINT : NO_PACKAGE_AND_PRINT }}
          </o-text>
          <o-text v-else-if="isMailBack && carrier.requirePackageAndPrint">
            {{ PRINT }}
          </o-text>
          <o-text v-else>
            {{ PACKAGE_AND_PRINT }}
          </o-text>
        </div>
        <div class="d-flex align-center" v-if="isMailBack && carrier.requirePackageAndPrint">
          <v-icon
            small
            class="mr-2"
          >
            inventory
          </v-icon>
          <o-text>
            {{ PACKAGE }}
          </o-text>
        </div>
        <div v-if="carrier.isSustainable" class="d-flex align-center">
          <v-icon small class="mr-2">
            spa
          </v-icon>
          <o-text>
            {{ SUSTAINABLE }}
          </o-text>
        </div>
      </section>
      <div :class="isMailBack && !isShowCase ? 'd-flex mt-4 pb-3 align-center justify-content-between' : 'd-flex mt-4 pb-6 align-center justify-content-between'">
        <action-button
          @click.stop="openModal({
            type: DIALOG_CARRIER_INFORMATION,
            selectedCarrier: carrier,
            disableModalGoBack: true
          })"
          tertiary-color
          text
          class="pl-0"
        >
          Learn more
        </action-button>
        <action-button
          @click="handleCarrierSelection(carrier)"
          primary-color
          large
        >
          Choose
        </action-button>
      </div>
    </div>
  </div>
</template>

<script>
import ActionButton from '@/components/atoms/ActionButton.vue'
import OText from '@/components/atoms/OText.vue'
import { RETURN_METHOD_SCREENS } from 'branding/strings.json'
import { mapMutations } from 'vuex'
import { DIALOG_CARRIER_INFORMATION, DIALOG_CARRIER_LOCATIONS } from '@/store/constants'
import { isUSPSCarrier, isMailBackReturn, getSelectedReturnMethodDropOffDescription, getShippingAmt } from '@/controllers/ReturnMethodsController.js'

export default {
  name: 'ShippingCarrierCard',
  components: {
    ActionButton,
    OText
  },
  data () {
    return ({
      PACKAGE_AND_PRINT: RETURN_METHOD_SCREENS.PACKAGE_AND_PRINT,
      NO_PACKAGE_AND_PRINT: RETURN_METHOD_SCREENS.NO_PACKAGE_AND_PRINT,
      SUSTAINABLE: RETURN_METHOD_SCREENS.SUSTAINABLE,
      PACKAGE: RETURN_METHOD_SCREENS.PACKAGE,
      USPS_PACKAGE: RETURN_METHOD_SCREENS.USPS_PACKAGE,
      PRINT: RETURN_METHOD_SCREENS.PRINT,
      DIALOG_CARRIER_INFORMATION,
      DIALOG_CARRIER_LOCATIONS
    })
  },
  computed: {
    isMailBack () {
      return isMailBackReturn(this.carrier)
    },
    isUSPS () {
      return isUSPSCarrier(this.carrier)
    }
  },
  methods: {
    ...mapMutations({
      openModal: 'returns/openModal'
    }),
    getSelectedReturnMethodDropOffDescription (returnType) {
      return getSelectedReturnMethodDropOffDescription(returnType)
    },
    getShippingAmtText () {
      return getShippingAmt(this.carrier)
    }
  },
  mounted () {},
  props: {
    isShowCase: {
      type: Boolean,
      default: false
    },
    carrier: {
      type: Object,
      required: true
    },
    handleCarrierSelection: {
      type: Function,
      required: true
    }
  }
}
</script>
