<template>
  <div>
    <div class="mt-8">
      <div style="max-width: 664px" class="mx-auto">
        <v-row
          justify="center"
          class="mb-4 mt-0 mx-auto"
        >
          <div
            class="bg-well text-center justify-center align-center mt-4 py-8"
          >
            <div class="d-flex justify-center">
              <v-img
                :src="require('@/assets/images/confirmed.png')"
                max-width="56"
                max-height="56"
              />
            </div>
            <o-text
              lg
              text-green
              bold
              class="mt-2"
              heading
              :level="Number(1)"
            >
              {{ RETURN_CONFIRMED }}
            </o-text>
            <o-text sm class="mt-1">
              {{ getConfirmationEmail() }}
            </o-text>
            <div class="d-flex mt-4 align-center justify-center">
              <action-button
                @click.stop="openReturnLabelLink"
                secondary-color
                outlined
                :loading="isLoading"
                :disabled="isLoading"
                v-if="this.selectedReturnMethod.type === MAIL_BACK || this.selectedReturnMethod.type === EXPRESS_RETURNS"
              >
                {{
                  this.selectedReturnMethod.type === MAIL_BACK
                    ? PRINT_RETURN_LABEL
                    : VIEW_RETURN_CODES_BUTTON
                }}
              </action-button>
              <o-dialog
                :visible="showReturnCodes"
                is-full-screen
                @close="toggleShowReturnCodes"
              >
                <QRCodeView
                  v-if="this.selectedReturnMethod.type === EXPRESS_RETURNS"
                  :qr-data="this.qrData"
                  from-return-methods
                  :is-exchange-or-refund="this.isExchangeOrRefund"
                />
              </o-dialog>
              <action-button
                tertiary-color
                text
                class="ml-4"
                @click.stop="toggleReturnDetails(true)"
              >
                {{ VIEW_RETURN_DETAILS_BUTTON }}
              </action-button>
              <o-dialog
                :visible="showReturnDetails"
                is-full-screen
                @close="toggleReturnDetails(false)"
              >
                <ReviewView
                  is-modal
                  :show-confirm-action-view="false"
                  :title="this.RETURN_DETAILS_TITLE"
                  :sub-title="getReturnOrderNumber()"
                  show-order-details
                  :return-date="getReturnDate()"
                />
              </o-dialog>
            </div>
          </div>
        </v-row>
      </div>
    </div>
  </div>
</template>

<script>
import OText from '@/components/atoms/OText.vue'
import ActionButton from '@/components/atoms/ActionButton.vue'
import { CONFIRMATION_VIEW, RETURN_REVIEW, PAGE_TITLES } from 'branding/strings.json'
import ODialog from '@/components/atoms/ODialog.vue'
import ReviewView from '@/components/pages/ReviewView.vue'
import QRCodeView from '@/components/pages/QRCodeView.vue'
import { mapGetters } from 'vuex'
import { getFormattedDate, returnFormattedOrderNumber } from '@/utils'
import { GET_SHIPPING_LABEL, GET_QR_CODE_LABEL } from '@/store/constants.js'
import { CONSTANTS } from '@/constants/index.js'
import {
  isExchangeOrRefundOrderItems
} from '@/controllers/OrdersController.js'
import { getIsGift, getGiftEmail } from '@/store/sessionStorage.js'

export default {
  name: 'ConfirmationView',

  components: {
    OText,
    ActionButton,
    ODialog,
    ReviewView,
    QRCodeView
  },

  computed: {
    ...mapGetters({
      order: 'order/order',
      selectedReturnMethod: 'returns/getSelectedReturnMethod',
      rmaCreatedAt: 'ui/rmaCreatedAt',
      refundList: 'order/refundList',
      exchangeList: 'order/exchangeList'
    }),
    isExchangeOrRefund () {
      return isExchangeOrRefundOrderItems()
    }
  },
  data () {
    return {
      RETURN_CONFIRMED: CONFIRMATION_VIEW.RETURN_CONFIRMED,
      CONFIRMATION_EMAIL_MESSAGE: CONFIRMATION_VIEW.CONFIRMATION_EMAIL_MESSAGE,
      VIEW_RETURN_CODES_BUTTON: CONFIRMATION_VIEW.VIEW_RETURN_CODES_BUTTON,
      PRINT_RETURN_LABEL: CONFIRMATION_VIEW.PRINT_RETURN_LABEL,
      VIEW_RETURN_DETAILS_BUTTON: CONFIRMATION_VIEW.VIEW_RETURN_DETAILS_BUTTON,
      MAIL_BACK: CONSTANTS.RETURN_METHODS.MAIL_BACK,
      EXPRESS_RETURNS: CONSTANTS.RETURN_METHODS.EXPRESS_RETURNS,
      IN_STORE: CONSTANTS.RETURN_METHODS.IN_STORE,
      RETURN_DETAILS_TITLE: RETURN_REVIEW.RETURN_DETAILS_TITLE,
      RETURN_DETAILS_DATE_PREFIX: RETURN_REVIEW.RETURN_DETAILS_DATE_PREFIX,
      showReturnDetails: false,
      showReturnCodes: false,
      returnLabelLink: '',
      isGiftReturn: getIsGift(),
      isLoading: false,
      qrData: null,
      state_Email: getGiftEmail()
    }
  },

  props: {
  },

  methods: {
    getReturnDate () {
      return 'Requested on ' + getFormattedDate(this.rmaCreatedAt)
    },
    getReturnOrderNumber () {
      return this.order ? returnFormattedOrderNumber(this.order.order_id) : ''
    },
    getConfirmationEmail () {
      return this.CONFIRMATION_EMAIL_MESSAGE + (this.isGiftReturn && this.state_Email ? this.state_Email : this.order.shopper_email)
    },
    openReturnLabelLink () {
      if (this.selectedReturnMethod.type !== this.MAIL_BACK) {
        document.title = PAGE_TITLES.NEXT_STEPS
      }
      this.selectedReturnMethod.type === this.MAIL_BACK ? window.open(this.returnLabelLink) : this.showReturnCodes = true
    },
    toggleReturnDetails (opening) {
      if (opening) {
        this.showReturnDetails = true
        document.title = PAGE_TITLES.REVIEW
      } else {
        this.showReturnDetails = false
        document.title = PAGE_TITLES.CONFIRMATION
      }
    },
    toggleShowReturnCodes () {
      this.showReturnCodes = false
      document.title = PAGE_TITLES.CONFIRMATION
    }
  },

  mounted () {
    const identifier = this.$route.params.identifier
    const params = {
      identifier
    }
    this.isLoading = true
    if (this.selectedReturnMethod.type === this.MAIL_BACK) {
      this.$store
        .dispatch('order/' + GET_SHIPPING_LABEL, params)
        .then((response) => {
          if (response) {
            this.returnLabelLink = response.label_url
          }
        })
        .catch((err) => {
          console.error({ err })
        })
        .finally(() => {
          this.isLoading = false
        })
    } else if (this.selectedReturnMethod.type === this.EXPRESS_RETURNS || this.selectedReturnMethod.type === this.IN_STORE) {
      this.$store
        .dispatch('order/' + GET_QR_CODE_LABEL, params)
        .then((response) => {
          if (response) {
            this.qrData = response
          }
        })
        .catch((err) => {
          console.error({ err })
        })
        .finally(() => {
          this.isLoading = false
        })
    }
  },
  beforeRouteLeave (to, from, next) {
    if (!to.hash) {
      window.location.replace('/')
      return
    }
    next()
  },
  created () {}
}
</script>

