<template>
  <o-dialog
    :visible="show"
    :is-full-screen="false"
    width="400"
    @close="show=false"
  >
    <v-form>
      <header-title
        class="pb-4"
      >
        {{ TELL_US_MORE }}
      </header-title>
      <o-text-area
        :title="LEAVE_FEEDBACK_ON_YOUR_ORDER"
        id="feedback"
        v-model="feedback"
        placeholder="Tell us about your order, or the delivery"
        @onChange="onChange($event)"
      />
      <div
        class="d-flex justify-start mt-4"
        v-if="$vuetify.breakpoint.mdAndUp"
      >
        <action-button
          @click.stop="onClickMethod"
          primary-color
          x-large
          :disabled="isDisabled"
        >
          {{ SUBMIT_FEEDBACK }}
        </action-button>
      </div>
      <div
        v-if="$vuetify.breakpoint.smAndDown"
        class="d-flex justify-center"
      >
        <action-button
          fab
          fixed
          bottom
          block
          class="mb-n4"
          primary-color
          style="border-radius: 0em; height: 66px"
          :disabled="isDisabled"
          @click.stop="onClickMethod"
        >
          Submit feedback
        </action-button>
      </div>
    </v-form>
  </o-dialog>
</template>

<script>
import ActionButton from '@/components/atoms/ActionButton.vue'
import OTextArea from '@/components/atoms/OTextArea.vue'
import { LEAVE_FEEDBACK } from 'branding/strings.json'
import ODialog from '@/components/atoms/ODialog.vue'
import { mapGetters } from 'vuex'
import HeaderTitle from '@/components/molecules/HeaderTitle.vue'

export default {
  props: {
    visible: Boolean
  },
  components: {
    ODialog,
    ActionButton,
    OTextArea,
    HeaderTitle
  },
  data () {
    return {
      isDisabled: true,
      feedback: '',
      TELL_US_MORE: LEAVE_FEEDBACK.TELL_US_MORE,
      LEAVE_FEEDBACK_ON_YOUR_ORDER: LEAVE_FEEDBACK.LEAVE_FEEDBACK_ON_YOUR_ORDER,
      SUBMIT_FEEDBACK: LEAVE_FEEDBACK.SUBMIT_FEEDBACK
    }
  },
  mounted () {
  },
  computed: {
    ...mapGetters({
      currentItemIndex: 'returns/currentItemIndex',
      selectedItems: 'order/selectedItems'
    }),
    item () {
      return this.selectedItems[this.currentItemIndex]
    },
    show: {
      get () {
        return this.visible
      },
      set (value) {
        if (!value) {
          this.$emit('close')
        }
      }
    }
  },
  methods: {
    onChange (e) {
      this.isDisabled = false
    },
    onClickMethod (event) {
      const sku = this.item.sku
      this.$store.commit('returns/updateReturnItemFeedback', {
        feedback: this.feedback,
        itemId: sku
      })
      this.feedback = ''

      this.$store.commit(
        'returns/updateReason', {
          reason: '',
          itemSku: sku
        })
      this.$emit('click', event)
    }
  }
}
</script>

<style scoped>
  .container {
    padding-top: 60px;
  }
  .v-dialog {
    border-radius: 0px !important;
  }
  .v-card {
    border-radius: 0px !important;
  }
</style>
